/* eslint max-statements:0 */
var contexts = {},
  validateOptions,
  _ = require("lodash"),
  traverser = require("./graph/traverser"),
  expander = require("./graph/expander"),
  createRecipeModel = require("./models/recipe");

/**
 * Contexts handle the connections between the current configuration engine
 * and the multiple modules, taking care of memoizing vertex instances
 * and expensive computations that can be cached.
 *
 * @module context
 */

/**
 * Gets a context instance.
 *
 * @param  {Object} options - Options object
 * @param  {Object} options.graph - Product configuration {@link Graph}.
 * @param  {String} options.engineId - Unique id for the context.
 * @return {Context} Context instance
 * @namespace context
 * @example
  context({
    graph: { vertices: [id: 1, ns: "a"], edgeGroups: { likes: [0, 0]} },
    engineId: "my-engine-id"
  });
 */
function getContext(options) {

  var
    // Reference to models contextualized to this context
    modelMap,
    Recipe,

    // Keeps singletons of the models
    vertexInstances = {},

    /**
     * Context handler
     */
    context = {},

    contextProduct,
    contextRecipe,
    graph,
    contextTraverser,

    engineId = options && options.engineId,

    cache = {};

  validateOptions(options);

  graph = expander(options.graph);

  // If the context was already define for this engine instance
  // we will reuse it.
  if (contexts[engineId]) {
    return contexts[engineId];
  }

  function failDestroyed() {
    throw new Error("The context " + engineId +
        " has been destroyed and can no longer be used.");
  }

  function graphThunk() {
    if (!options) {
      failDestroyed();
    }
    return graph;
  }

  function modelForNs(ns) {
    // Models should either be defined explicitly or generated
    // dynamically from the graph ns
    if (!modelMap[ns]) {
      throw new Error("Unknown graph namespace \"" + ns + "\"");
    }
    return modelMap[ns];
  }

  /**
   * Contains a collection of {ca: ca, av: av} with the recipe selection.
   * It can contain attributes that given their visibility rules might not
   * be exposed via recipe.getData()
   *
   * The collection uses the ca.id as the hash key for fast access to recipe
   * ca's.
   *
   * @name recipeData
   * @type {Object}
   * @private
   */
  context.recipeData = {};


  /**
   * Contains {ConfigurableAttribute} states. This includes:
   *
   *   - selectedFacetFiltes
   *   - sympatheticFilters
   *
   * The collection uses the ca.id as the hash key for fast access to recipe
   * ca's.
   *
   * @name recipeFilters
   * @type {Object}
   * @private
   */
  context.recipeFilters = {};


  /**
   * Gets a vertex or an instance provided a vertex key and a ns.
   *
   * @name vertexInstance
   * @function
   * @param  {Object} vertex - Vertex options
   * @param  {String} vertex.vertexKey - Vertex key. For example ns:123
   * @param  {String} [vertex.ns] - Vertex namespace. Required if you need an
   * instance.
   * @return {(object|vertexInstance)}        Vertex object or instance
   */
  context.vertexInstance = function (vertex) {
    var ModelClass;

    if (!modelMap) {
      failDestroyed();
    }

    if (vertex.getAndCache) {
      // already an instance
      return vertex;
    }

    if (!vertex || !vertex.vertexKey) {
      throw new Error("Missing vertexKey for instantiating " +
        JSON.stringify(vertex));
    }

    var key = vertex.vertexKey;
    var instance = vertexInstances[key];

    if (!instance) {

      vertex = expandVertexKey(vertex);

      ModelClass = modelForNs(vertex.ns);

      instance = vertexInstances[key] = new ModelClass({id: vertex.id});
    }
    return instance;
  };


  /**
   * Allows us to update the graph used on a context without
   * having to create a new context. After setting a new graph
   * you might have to explicitly clear the caches for the context
   *
   * @name setGraph
   * @function
   * @param {Object} graphJson - Graph JSON
   */
  context.setGraph = function (graphJson) {
    graph = expander(graphJson);
  };

  /**
   * Creates a new traverser or uses an existing one if it was already
   * created
   *
   * @name createTraverser
   * @function
   * @returns {Traverser} Graph {@link Traverser}
   */
  context.createTraverser = function () {
    var TraverserClass;
    if (!contextTraverser) {
      TraverserClass = traverser({
        graph: graphThunk,
        context: context
      });
      contextTraverser = new TraverserClass();
    }
    return contextTraverser;
  };


  /**
   * Sets the product instance that is used within the context.
   *
   * @name setProduct
   * @function
   * @param {ConfigurableProduct} product - Product instance
   */
  context.setProduct = function (product) {
    var vertexKey = product.getVertexKey();
    vertexInstances[vertexKey] = product;
    contextProduct = product;
  };

  /**
   * Gets the product for this context.
   *
   * @name product
   * @function
   * @return {ConfigurableProduct} Product Instance used on the context.
   */
  context.product = function () {
    var productVertex;
    if (!contextProduct) {
      productVertex = {
        id: (options.id || graph.vertices[0].id),
        ns: "p"
      };
      productVertex.vertexKey = "p:" + productVertex.id;
      context.setProduct(context.vertexInstance(productVertex));
    }
    return contextProduct;
  };

  /**
   * Gets the recipe used on this context. If no recipe existed a new one will
   * be created using the default recipe.
   *
   * @name recipe
   * @function
   * @param {Boolean} [setDefault=true] Set the default values for a recipe
   * @return {Recipe} Recipe Instance used on the context.
   */
  context.recipe = function (setDefault) {
    if (!contextRecipe) {
      return context.newRecipe(setDefault);
    }
    return contextRecipe;
  };

  /**
   * Creates the recipe to be used on this context.
   * Always creates new recipe. Is useful for situations when we always need a new instance.
   *
   * @name newRecipe
   * @function
   * @param {Boolean} [setDefault=true] Set the default values for a recipe
   * @return {Recipe} Recipe Instance used on the context.
   */
  context.newRecipe = function (setDefault) {
    context.createRecipeModel();
    contextRecipe = new Recipe(context, engineId);
    if (setDefault !== false) {
      contextRecipe.setDefault();
    }
    return contextRecipe;
  };

  /**
   * Creates an instantiable recipe model scoped to the current context.
   *
   * @name createRecipeModel
   * @function
   * @return {Recipe} Recipe model
   */
  context.createRecipeModel = function () {
    if (!Recipe) {
      Recipe = createRecipeModel(options);
    }
    return Recipe;
  };

  /**
   * We keep caches on the context in order to share them among models and
   * to simplify freeing up memory when the context is destroyed.
   *
   * @name getAndCache
   * @function
   * @param  {ModelInstance}   instance - Model instance that owns the cache
   * entry. This can be Product, ConfigurableAttribute ...
   * @param  {number|string}   id  Id for the instance that owns the cache
   * @param  {Function} fn Method that retrieves the un-cached value.
   * @return {mixed}            Whatever fn returns.
   */
  context.getAndCache = function (instance, id, fn) {
    var cacheId = instance.getVertexKey() + "|" + id;
    var result = cache[cacheId];
    if (result === undefined) {
      cache[cacheId] = result = fn.call(instance);
    }
    return result;
  };

  /**
   * Removes all references and caches for a context.
   * The destroy method should be called for disposing
   * a context. It will remove all references to the
   * graph and will remove event listeners from recipes.
   *
   * @name destroy
   * @function
   */
  context.destroy = function () {
    try {
      // Removes all event listeners from the recipe
      var recipe = context.recipe(false);
      recipe.off();
      recipe.destroy();
    } catch (e) {
      /* continue regardless of error */
    }

    context.recipeData = {};
    context.recipeFilters = {};
    context.clearCache();
    contexts[engineId] = null;
    graph = null;
    contextTraverser = null;
    contextProduct = null;
    contextRecipe = null;
    modelMap = null;
    Recipe = null;
    options = null;
  };

  /**
   * Clears the caches for the current context.
   *
   * @name clearCache
   * @function
   */
  context.clearCache = function () {
    cache = {};
    vertexInstances = {};
  };

  /**
   * Gets an array of instantiated contexts
   *
   * @name contexts
   * @function
   * @return {Array} - Instantiated contexts
   */
  context.contexts = function () {
    return contexts;
  };

  /**
   * Gets the id of the current context.
   *
   * @name engineId
   * @function
   * @return {String} - Context identifier, defined when creating the context
   * via options.engineId
   */
  context.engineId = function () {
    return (options || {}).engineId;
  };

  /**
   * Gets a list of currently instantiated engine ids
   *
   * @name engineIds
   * @function
   * @return {Array} Context identifiers, defined when creating the contexts
   * via options.engineId
   */
  context.engineIds = function () {
    return Object.keys(contexts);
  };

  /**
   * Sets new options for an environment that has already been
   * destroyed.
   *
   * @name setOptions
   * @function
   * @param {Object} newOptions - If you want to recover a destroyed context
   * you need to provide re-initialization options.
   * @param  {Object} newOptions.graph - Product configuration {@link Graph}.
   * @param  {String} newOptions.engineId - Unique id for the context.
   * @return {Context} Context instance
   */
  context.setOptions = function (newOptions) {
    if (options) {
      throw new Error("You first need to destroy a context before " +
        "setting new options.");
    }
    options = newOptions;
    validateOptions(options);
    graph = expander(options.graph);
    createModelClasses();
  };

  /**
   * Simple method for explaining the relationships of a given vertexKey
   *
   * @name explain
   * @function
   * @param  {String} vertexKey Vertex key in ns:123 format.
   * @return {Object}           Same as {@link Traverser#explain}
   */
  context.explain = function (vertexKey) {
    var vertex = expandVertexKey({vertexKey: vertexKey});
    return context.vertexInstance(vertex).explain();
  };

  // Keeping a reference to the context
  if (options.engineId) {
    contexts[options.engineId] = context;
  }

  function createModelClasses() {
    modelMap = require("./models/map")({
      graph: graphThunk,
      engineId: engineId,
      context: context
    });
  }

  createModelClasses();

  options.context = context;

  return context;
}


validateOptions = function (options) {
  if (!options) {
    throw new Error("No options supplied for the configuration-engine context");
  }
  if (!options.graph) {
    throw new Error("Missing graph on configuration-engine context options");
  }
  // By using engineId's we can latter get a list of all the contexts
  // that have been created.
  if (!options.engineId) {
    throw new Error("Missing engineId on configuration-engine context options");
  }
};


module.exports = getContext;


/** Flushes all the contexts */
module.exports.flushAll = function () {
  _.each(contexts, function (context, key) {
    try {
      context.destroy();
    } catch (e) {
      /* continue regardless of error */
    }
    delete contexts[key];
  });
  contexts = {};
};


/** Gets all engine ids */
module.exports.engineIds = function () {
  return Object.keys(contexts);
};

/** Gets all the contexts */
module.exports.contexts = function () {
  return contexts;
};


/**
 * Given a vertexKey, extract the id and the ns if they are not explicitly set
 * @param  {Object} vertex Vertex object
 * @return {Object}        Vertex with extra ns and id if a vertexKey is passed
 */
function expandVertexKey(vertex) {
  var parts = vertex.vertexKey && vertex.vertexKey.split(":");
  vertex.ns = vertex.ns || parts[0];
  vertex.id = vertex.id || +parts[1];
  return vertex;
}
