/**
 * @publicDocs methods/getWebGlExtraSettings
 *   ../../docs/get_webgl_extra_settings.md
 */


module.exports = function (engine) {
  var productEngine  = engine.product();

  /**
   * Gets a WebGl extra settings for the current Product, if present.
   *
   * @returns {Object} Custom WebGl settings for product or null
   */
  return function getWebGlExtraSettings() {
    if (productEngine.hasWebGlExtraSettings()) {
      return productEngine.getWebGlExtraSettings();
    }
    return null;
  };
}
