/**
 * @publicDocs methods/indexConfigurableAttributeMetadata
 *   ../../docs/index_configurable_attribute_metadata.md
 */

var
  _ = require("lodash"),
  getAttribute = require("./get_attribute"),
  GetFullTextSearch = require("./get_full_text_search");

module.exports = function (engine, getProduct) {

  /**
   * This method will index all the metadata for the given attribute
   *
   * @param  {options} options the fields and attribute you want to index
   * @param  {Function} callback the callback will be called when the index
   * is done, if error it will be passed into the callback
   * @return {Function}
   */
  return function indexConfigurableAttributeMetadata (options, callback) {
    options = options || {};

    var
      attribute = null;

    if (!options.attribute) {
      callback(new Error("You must provide an options.attribute param"));
      return;
    }

    attribute = getAttribute(engine, getProduct)(options.attribute);

    if (!attribute) {
      callback(new Error("You must provide a valid options.attribute param"));
      return;
    }

    GetFullTextSearch.createSearchIndex(options);

    _.each(attribute.allValues, function (attributeValue) {
      _.each(attributeValue.metadata, function (metadata) {
        GetFullTextSearch.addDocument(engine, metadata);
      });
    });

    _.each(attribute.valueUsages, function (valueUsage) {
      _.each(valueUsage.metadata, function (metadata) {
        GetFullTextSearch.addDocument(engine, metadata);
      });
    });

    _.each(attribute.metadata, function (metadata) {
      GetFullTextSearch.addDocument(engine, metadata);
    });

    callback();
  };
};
