var _ = require("lodash");

module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
    // eagerly evaluate so FVs will be pre-cached
    this.facetValues();
  }

  /**
   * Facet.
   *
   * On the graph their vertices contain the namespace (ns) "f"
   *
   * @param {Object} options Options
   * @param {number} options.id Facet numeric id.
   * @class
   * @name Facet
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends Facet */{

    ns: "f",

    /**
     * Gets the {@link FacetValue}s for this Facet
     * @instance
     * @return {Array<FacetValue>}
     */
    facetValues: function () {
      return this.getAndCache("facetValues", function () {
        var self = this;
        var instances = this.traverser()
          .outgoing("hasFacetValue")
          .expand()
          .sortBy("order")
          .instances();

        _.each(instances, function (fv) {
          fv._facet = self;
        });
        return instances;
      });
    },


    /**
     * Gets the {@link FacetValue}s for this Facet that have selectable
     * {@AttributeValues} for a given {@link ConfigurableAttribute}
     * @instance
     * @param {ConfigurableAttribute} ca - A configurable attribute instance
     * @return {Array<FacetValue>}
     */
    selectableFacetValues: function (ca) {
      var self = this;
      return this.getAndCache("selectableFVs" + ca.id, function () {
        var set = _.reduce(ca.unsortedValues(), function (set, av) {
          var fvs = av.facetValuesForFacet(self), fv;

          // really hot code path, hence the for loop :(
          for (var i = 0, len = fvs.length; i < len; i += 1) {
            fv = fvs[i];
            set[fv.id] = fv;
          }

          return set;
        }, {});

        return _.sortBy(_.values(set), function (fv) {
          return fv.getData().order;
        });
      });
    },


    /**
     * Finds a single {@link FacetValue} using the search criteria.
     * @param {Object} query - The object of property values to filter by
     * @instance
     * @return {FacetValue}
     */
    findFacetValue: function (query) {
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        query = {id: +query};
      }

      return this.traverser(this.facetValues())
        .expand()
        .where(query)
        .instance();
    },


    /**
     * Is this facet optional?
     * @return {Boolean}
     */
    isOptional: function () {
      return this.getAndCache("isOptional", function () {
        return this.traverser()
          .incomingSize("hasOptionalFacet") > 0;
      });
    },

    /**
     * Is this facet hidden?
     * @return {Boolean}
     */
    isHidden: function () {
      var self = this;
      return this.getAndCache("isHidden", function () {
        var hasHidden = self.traverser()
          .incomingSize("hasHiddenFacet") > 0;
        var hasInvertedHidden = self.traverser()
          .incomingSize("hasInvertedHiddenFacet") > 0;
        return hasHidden || hasInvertedHidden;
      });
    },

    /**
     * Is this facet inverted?
     * @return {Boolean}
     */
    isInverted: function (ca) {
      return this.getAndCache("isInverted-" + ca.id, function () {
        return this.traverser()
          .incomingSize("hasInvertedHiddenFacet") > 0;
      });
    },


    /**
     * Gets a copy of this facet with including the
     * facet value ids
     *
     * @returns {Object} Facet data including its facet ids. Example output:
     *
     *     {
     *       id: 20804,
     *       ns: "f",
     *       name: "Individual Webbing Colors",
     *       description: "",
     *       label: "",
     *       selectorType: "dropdown",
     *       vertexKey: "f:20804",
     *       values: [
     *         { id: 24497 },
     *         { id: 24498 }
     *       ]
     *     }
     *
     * @instance
     */
    toJSON: function () {
      var facet = this.clone();
      if (facet) {
        facet.values = this.facetValues().map(function (fv) {
          return {id: fv.id};
        });
        return facet;
      }
    }
  }, constructor);

};
