var _ = require("lodash"),
  RE_LAST_NUMBERS = require("./common").RE_LAST_NUMBERS,
  getSortedVerboseAttributes = require("./get_sorted_verbose_attributes.js");

/**
 * Gets a recipe in it's FC2 concise format
 */
module.exports = function getConciseRecipe(recipeInstance, productInstance) {
  var orderedAttributes = getSortedVerboseAttributes(productInstance),
    verboseRecipe = recipeInstance.legacy("verbose"),
    recipeIndexes;

  recipeIndexes = _.reduce(orderedAttributes, function (indices, legacyCaId) {

    var caId = legacyCaId.match(RE_LAST_NUMBERS)[1],
      ca = productInstance.attributeById(+caId),
      avId, data;

    var caData = (ca && ca.getData()) || {};

    // Shadow attributes are a legacy from FC2.
    // They are represented on concise recipes and taken into
    // account by imagecomposer CD-1728
    if (caData.isShadow) {
      indices.push(0);

    // The CA is not included on the verboseRecipe, it might be not
    // visible
    } else if (!verboseRecipe[legacyCaId]) {
      indices.push(-1);
    } else if (ca.hasDynamicValue()) {
      data = verboseRecipe[legacyCaId].data || verboseRecipe[legacyCaId];
      indices.push(JSON.stringify(data));
    } else {
      avId = verboseRecipe[legacyCaId].match(RE_LAST_NUMBERS)[1];

      indices.push(_.indexOf(
        _.pluck(ca.legacyUnsortedValues(), "id"),
        +avId
      ));
    }


    return indices;
  }, []);

  return recipeIndexes.join(",");
};
