/* eslint complexity:0*/

var _ = require("lodash");
var async = require("async");
var getNormalizedError = require("../../get_error")("saveRecipe");
var recipeUtils = require("./utils");
var recipeV3Steps = require("./v3_steps");
var recipeV1Steps = require("./v1_steps");

module.exports = function (engine, fcParams, api) {

  var  getRecipe = require("../get_recipe")(engine);
  var  getItemNumber = require("../get_item_number")(engine);

  /**
   * This is the main public method for saving recipes at their current state
   *
   * @memberOf Recipe
   * @fires recipe#saved
   * @param {Object} customerData - A Javasript plain object with custom
   *    information that needs to be saved alongside the recipe
   * @callback {Error} err - A Javascript error with the message on err.message.
   * @callback {Object} info - The recipe id and the resource URL. This info is
   *   the same that is emitted by the {@link recipe#saved} event
   *   i.e.
   *   {id: 123, resource: "https://prod.fluidconfigure.com/api/recipe/123"}
   */
  function saveRecipe(customerData, callback) {
    if (!callback) {
      callback = customerData;
      customerData = {};
    }

    if (!callback || (typeof callback !== "function")) {
      throw new Error("saveRecipe expects a callback");
    }

    // The following sequence of sync and async actions are required for posting
    // a recipe. Using async.auto we can hook-in, skip and alter steps
    // dynamically by redefining the saveRecipeSteps list.
    var saveRecipeSteps = {
      // Callback method for interrupting the auto sequence, for example when
      // timing out
      done: function (cb) {
        cb(null, callback);
      },

      // Gets the customer data right from saveRecipe(customerData)
      customerData: async.constant(customerData),
      fcParams: async.constant(fcParams),

      saveEndpoint: getSaveEndpoint,

      // Handles timeouts
      hasTimedOut: ["customerData", "done", recipeUtils.getHasTimedOutFunction],

      // Loads the preferences, they might already be cached by the
      // configuration-loader. We need this to validate the locale and
      // in the future to get the API key or other settings
      preferences: getPreferences,

      // Validates or sets the locale
      locale: ["preferences", getLocale],
      // Picks from fcParams the context data like workflow, customerId...
      data: ["preferences", "fcParams", getData],

      apiVersion: ["preferences", getApiVersion],

      // We want to give the implementors a chance to update the recipe
      // before saving it. They might want to include a user id, apply
      // a discount, remove personal data from the recipe...
      // If they implement this method it will need to provide a callback
      // to let us know when they're done
      beforeSaveHook: ["hasTimedOut", "customerData",
      recipeUtils.beforeSaveHook],

      conciseRecipe: ["customerData", conciseRecipe],

      selectionRecipe: ["customerData", getSelectionRecipe],

      configVersion: getConfigVersion,

      // Gets the time and context values like the conciseRecipe, quantities,
      // price...
      computedDefaults: ["hasTimedOut", "conciseRecipe",
        "preferences", "locale", "data", "configVersion",
        "beforeSaveHook", "customerData", computeDefaults
      ],

      // Combines data, computedDefaults, customerData and removes invalid
      // values
      recipeV1Data: ["computedDefaults", "apiVersion",
        "data", "customerData", "preferences",
        recipeV1Steps.recipeData],

      // recipeV3Data: ["computedDefaults", "apiVersion", "data",
      // "recipeV1Data",
      // "selectionRecipe", getRecipeV3Data],

      // Resolves how is the recipe going to be posted. Users can opt to use
      // their servers for generating the recipes, we allow them to override
      // the default method
      performV1PostFunction: ["hasTimedOut", "customerData", "preferences",
        getPerformPostRecipeV1Function
      ],

      // performV3PostFunction: ["hasTimedOut", "customerData",
      //   getPerformPostRecipeV3Function
      // ],

      // Performs the actual post and gets back the recipe id that was created
      recipeV1SaveResponse: ["hasTimedOut", "computedDefaults", "recipeV1Data",
        "saveEndpoint", "performV1PostFunction", /* "recipeV3Data", */
        "selectionRecipe",
        postRecipeV1Data
      ],

      // recipeV3SaveResponse:["hasTimedOut", "recipeV1SaveResponse",
      // "recipeV3Data", "performV3PostFunction", postRecipeV3Data],
      // Before we broadcast the result with the save result we want to give
      // implementors a chance to know about it before our side effects kick
      // in. This can be used for instance, to restore a version of the recipe
      // that was modified with some temporary data right before being saved.
      afterSaveHook: [
        "hasTimedOut", "customerData", "recipeV1SaveResponse",
        /* "recipeV3SaveResponse" ,*/ recipeUtils.afterSaveHook
      ],

      // Emits the recipe:saved event with the same response we got back from
      // the post function. This is the last step and the output of this method
      // defines what is passed to the saveRecipe callback
      broadcastSuccess: [
        "hasTimedOut", "recipeV1SaveResponse", /* "recipeV3SaveResponse", */
        "preferences", "afterSaveHook",
        broadcastNewRecipe
      ],

    };

    async.auto(saveRecipeSteps, function (err, steps) {
      // We will perform the final callback on the next tick after the events
      // have been propagated
      _.defer(function () {
        var result = steps && steps.broadcastSuccess;

        if (steps.hasTimedOut()) {
          return;
        }

        err = getNormalizedError(err);
        callback(err, result);
      });
    });
  }
  /**
   * Common functions that are used by recipe v1 and v3
   * They are:
   *  - computeDefaults
   *  - getPreferences
   *  - getData
   *  - getConfigVersion
   *  - getLocale
   *  - getApiVersion
   */
  function computeDefaults(cb, steps) {
    recipeV1Steps.computeDefaults(cb, steps, fcParams, api);
  }

  function getPreferences(cb) {
    recipeUtils.getPreferences(cb, fcParams);
  }

  function getData(cb, steps) {
    recipeUtils.getData(cb, steps, getItemNumber);
  }

  function getConfigVersion(cb) {
    recipeUtils.getConfigVersion(cb, api);
  }

  function getLocale(cb, steps) {
    recipeUtils.getLocale(cb, steps, fcParams);
  }

  function getApiVersion(cb, steps) {
    cb(null, fcParams.apiVersion || steps.preferences.apiVersion || "2012");
  }

  function getSaveEndpoint(cb) {
    cb(null, "https://" + fcParams.configureEndpoint + "/v1/api/recipe/");
  }

  /**
   * Functions to call the old recipe methods
   * They are:
   * - conciseRecipe
   * - getPerformPostRecipeV1Function
   * - postRecipeV1Data
   */

  function conciseRecipe(cb, steps) {
    recipeV1Steps.conciseRecipe(steps, engine, getRecipe, cb);
  }

  function getPerformPostRecipeV1Function(cb, steps) {
    recipeV1Steps.getPerformPostRecipeFunction(cb, steps, fcParams);
  }

  function postRecipeV1Data(cb, steps) {
    recipeV1Steps.postRecipeData(cb, steps);
  }

  /**
   * Functions that will call the new recipe service to save the recipe.
   * They are:
   * - getRecipeV3Data
   * - getSelectionRecipe
   * - getPerformPostRecipeV3Function
   * - postRecipeV3Data
   */
  // function getRecipeV3Data(cb, steps) {
  //   recipeV3Steps.recipeData(cb, steps, api);
  // }

  function getSelectionRecipe (cb, steps) {
    recipeV3Steps.selectionRecipe(steps, engine, getRecipe, cb);
  }

  // function getPerformPostRecipeV3Function(cb, steps) {
  //   recipeV3Steps.getPerformPostRecipeFunction(cb, steps, fcParams);
  // }

  // function postRecipeV3Data(cb, steps) {
  //   recipeV3Steps.postRecipeData(cb, steps);
  // }

  /**
   * Once a recipe is saved we broadcast it's information to the
   * external world
   */
  function broadcastNewRecipe(cb, steps) {
    if (steps.hasTimedOut()) {
      return;
    }

    var err;
    var id;
    var resource;
    // var v3Resource;
    var apiVersion = steps.apiVersion;

    try {

    // For now all we want to support are v1Recipes
    // var isV1Recipe = true;

    // if (!isV1Recipe) {
    //   id = steps.recipeV3SaveResponse.recipeId;
    //   v3Resource = resource = recipeV3Steps.
    //   getRecipeResourceUrl(fcParams, id, steps);
    // } else {
      id = steps.recipeV1SaveResponse.id;
      resource = recipeV1Steps.getRecipeResourceUrl(fcParams, id, steps);
    // }

      var eventData = {
        id: id,
        resource: resource,
        apiVersion: apiVersion
      };

      // if (isV1Recipe) {
      //   eventData.deprecationWarning = {
      //     "IMPORTANT":
      //     "Contact Fluid to enable the new Recipe " + v3Resource +
      //     " format on your account."
      //   };
      // }

       /**
       * Recipe saved event
       *
       * @event recipe#saved
       * @type {object}
       * @property {object} savedInfo - Object containing the
       *                              id and the resource
       * @example
       core.on("recipe:save", function (info) {
          console.log(info);
          // {id: 123,
          //   resource: "https://prod.fluidconfigure.com/api/recipe/123"}
       });
       */
      api.trigger("recipe:saved", eventData);
    } catch (e) {
      if (process.env.NODE_ENV !== "production") {
        console.warn("recipe:saved callback failed with exception", e);
      }
      err = new Error(
        "recipe:saved callback thrown the following exception: " + e.message);
    }
    cb(err, eventData);
  }

  return saveRecipe;
};
