/**
 * @publicDocs methods/setFallbackRecipe ../../docs/set_fallback_recipe.md
 */

var
  _ = require("lodash"),
  selectValue = require("./select_value");

module.exports = function (engine, api) {
  var setFallbackRecipe;
  var setFallbackValue = selectValue(engine, null, api, true);

  /**
   * @param {array} - A required array of changes. Every item is an object with properties ca and av. For example:
   *       var changes = [{ca: {alias: "bodymaterial"}, av: {name: "Alder"}},
   *                      {ca: {alias: "bodycolor"}, av: {name: "2-Color Sunburst"}}];
   * @param {function} - A required callback function receiving an error and an object with the changed data as parameters.
   * @return {function} - returns the result of the callback function in case where an error is present 
   */
  setFallbackRecipe = function(changes, callback) {
    var recipe; 
    if (!changes || !callback || (callback && !_.isFunction(callback))) {
      throw new Error("Invalid arguments for setFallbackRecipe(changes, callback)");
    }

    recipe = engine.recipe();

    recipe.once("change", function (data) {
      callback(null, JSON.parse(JSON.stringify(data)));
    });

    try {
      // Only apply changes on a single transaction
      // @todo: POTENTIAL SOURCE OF ISSUES
      // The order in which you define changes, via an array of objects, CAN
      // AND WILL influence how changes are applied (eg. sympathetic
      // relationships and filters can change selectable values for subsequent
      // attributes).
      recipe.start();
      _.each(changes, function (change) {
        setFallbackValue.call(null, change);
      });
      recipe.commit();
    } catch (e) {
      try {
        recipe.rollback();
      } catch (e) {
        // continue regardless of error
      }
      return callback(e);
    }
  };
  return setFallbackRecipe;
};
