
var
  _ = require("lodash"),
  fullTextSearch = require("@cfg.plat/fluid-full-text-search"),
  defaultFieldsToIndex = {
    fields: [
      {name: "engine", boost: 1},
      {name: "customNs", boost: 1},
      {name: "ns", boost: 1},
      {name: "vendorId", boost: 1},
      {name: "name", boost: 1},
      {name: "description", boost: 1},
      {name: "tooltip", boost: 1},
      {name: "alias", boost: 1},
      {name: "key", boost: 1},
      {name: "value", boost: 1}
    ]
  },
  searchIndex = null;

module.exports = {

  getSearchIndex: function () {
    return searchIndex;
  },

  createSearchIndex: function (options) {
    if (!searchIndex) {

      options = options || {};
      options = _.defaults({}, options, defaultFieldsToIndex);

      fullTextSearch(options, function (err, index) {
        if (!err) {
          searchIndex = index;
        }
      });
    }
  },

  addDocument: function (engine, document, attributesToExclude) {
    if (!searchIndex) {
      throw new Error("You have to create the index before " +
        "starting adding documents");
    }

    document = _.omit(document, attributesToExclude || []);
    document.customNs = "NS" + document.ns.toUpperCase() + "NS";
    document.engine = "CE" + engine.engineId().toUpperCase() + "CE";

    searchIndex.addDocument(document);
  }
};
