/**
 * @publicDocs methods/getWebGlModels ../../docs/get_webgl_models.md
 */

var _ = require("lodash"),
   validateFilter = require("../util").validateFilter;

module.exports = function (getProductJson) {
  /**
   * Gets a collection of WebGl model for the current Product.
   *
   * @param  {Object} options - Options object
   * @param  {Object} options.filter - Filters the attributes using
   *                                 [_.filter](https://lodash.com/docs#filter)
   * @returns {Array<Object>} An array of WebGl Model objects
   */
  return function getWebGlModels(options) {
    var webGlModels;

    options = options || {};

    validateOptions(options);

    webGlModels = Object.values(getProductJson().webGlModels);

    return (options.filter && _.filter(webGlModels, options.filter)) || webGlModels;
  };

  function validateOptions(options) {
    if (!_.isPlainObject(options)) {
      throw new Error("getWebGlModels options should be an Object");
    }

    validateFilter(options.filter);
  }
}
