module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * `View`s define what image should be shown for when one of the following is
   * selected:
   *
   * - {@link ConfigurableProduct}
   * - {@link ConfigurableAttribute}
   * - {@link AttributeValue}
   * - {@link ValueUsage}
   *
   * If a view for one of previous entities is not available the previous one
   * will be used instead.
   *
   * On the graph their vertices contain the namespace (ns) "v"
   *
   * Contrary to other Graph entities, view use their name as the primary key.
   *
   * @param {Object} options Options
   * @param {String} options.id View name, used as id/primary key.
   * @class
   * @name View
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends View */{
    ns: "view"
  });
};
