/* eslint-disable max-params */
var _ = require("lodash");
var eventTypes = require("./event_types"),
  evenTypesMap = require("./event_types_map");

var util = require("../../util");
var isEmptyObject = util.isEmptyObject;

var executionEnv = process.env.NODE_ENV;

function getEvents(
  fcParams,
  sessionId,
  eventName,
  eventData,
  isWebGl
) {
  var eventType = null;
  _.reduce(Object.keys(evenTypesMap),
    function(result, key) {
      if (eventType !== null) { return eventType };
      eventType = ((eventName === key) || (eventName === evenTypesMap[key]))
        ? evenTypesMap[key] : null;
      return eventName;
    },
    eventType
  );

  if (eventType === eventTypes.RECIPE_CHANGED) {
    return mapRecipeChangedEvent(
      fcParams,
      sessionId,
      eventType,
      eventData,
      isWebGl
    );
  }
  var event = getEventByType(
    fcParams,
    sessionId,
    eventType,
    eventData,
    isWebGl
  );

  return (event !== null) ? [event]: [];
}

module.exports = getEvents;

function mapRecipeChangedEvent(
  fcParams,
  sessionId,
  eventType,
  eventData,
  isWebGl
) {
  return _.map(eventData, function(data) {
    return validateRecipeChange(data) ? getEventByType(
      fcParams,
      sessionId,
      eventType,
      data,
      isWebGl
    ) : undefined;
  });
}

function validateRecipeChange(eventData) {
  var av = eventData.av;
  var ca = eventData.ca;

  var isValid = true;

  if (ca && isEmptyObject(av)) {
    isValid = false;
  } else if (ca && !isEmptyObject(av))  {
    if (!av.color && !av.url) {
      isValid = false;
    }
    if (av.text) {
      isValid = true;
    }
  }
  return isValid;
}


// eslint-disable-next-line complexity
function getEventByType(fcParams, sessionId, eventType, eventData, isWebGl) {
  var baseEvent = new BaseEvent(fcParams, sessionId, eventType, isWebGl);
  switch (eventType) {
    case eventTypes.ADD_TO_CART:
      return new AddToCart(baseEvent, eventData, fcParams);
    case eventTypes.ADD_TO_WISH_LIST:
      return new AddToWishList(baseEvent, eventData);
    case eventTypes.MODEL_LOADED:
      return new ModelLoaded(baseEvent, eventData);
    case eventTypes.ATTRIBUTE_VIEW_CLICKED:
      return new AttributeViewClicked(baseEvent, eventData);
    case eventTypes.VIEW_CHANGED:
      return new ViewChanged(baseEvent, eventData);
    case eventTypes.ROTATION:
      return new Rotation(baseEvent, eventData);
    case eventTypes.TEXTURE_LOADED:
      return new TextureLoaded(baseEvent, eventData);
    case eventTypes.ZOOM:
      return new Zoom(baseEvent, eventData);
    case eventTypes.UGC_IMAGE_SAVED:
      return new UgcImageSaved(baseEvent);
    case eventTypes.RECIPE_CHANGED:
      return new RecipeChanged(baseEvent, eventData, fcParams);
    case eventTypes.SHARING_EMAIL:
      return new Shared(baseEvent, "email", eventData);
    case eventTypes.SHARING_FACEBOOK:
      return new Shared(baseEvent, "facebook", eventData);
    case eventTypes.SHARING_TWITTER:
      return new Shared(baseEvent, "twitter", eventData);
    case eventTypes.SHARING_PINTEREST:
      return new Shared(baseEvent, "pinterest", eventData);
    default:
      showEventsTrackedMessage(eventType, evenTypesMap);
      return null;
  }
}

function showEventsTrackedMessage(eventType, eventsTracked) {
  if (executionEnv === "development") {
    var message = "analytics: Event type: "
      + eventType + " is not handled yet! \nEvents tracked are: ";
    console.log(
      message + Object.keys(eventsTracked).join(", ")
    );
  }
}

function getDisplayStrategy(isWebGl) {
  return (isWebGl) ? "webGL" : "imageComposer";
}

var BaseEvent = function (fcParams, sessionId, eventType, isWebGl) {
  return {
    type: eventType,
    customerId: fcParams.customer,
    productId: fcParams.productId,
    sessionId: sessionId,
    imagingStrategy: getDisplayStrategy(isWebGl)
  };
}

function getRecipeIdfromUrl (resourceUrl) {
  var recipeId = "";
  try {
    var regexRecipeId = /recipe\/(\d+)\?apiKey/;
    recipeId = regexRecipeId.exec(resourceUrl)[1];
  } catch (ex) {
    console.log(ex.messege);
  }
  return recipeId;
}

var AddToCart = function(baseEvent, eventData, fcParams) {
  var recipeId = getRecipeIdfromUrl(eventData.resource);
  baseEvent.quantity = eventData.quantity;
  baseEvent.price =
    truncateFloat(eventData.price, 3);
  baseEvent.currency = eventData.currency || fcParams.curency || "USD";
  baseEvent.recipeId = recipeId || "";
  return baseEvent;
}

var AddToWishList = function(baseEvent, eventData) {
  var recipeId = getRecipeIdfromUrl(eventData.resource);
  baseEvent.recipeId = recipeId || "";
  return baseEvent;
}

var ModelLoaded = function(baseEvent, eventData) {
  baseEvent.duration = (eventData && eventData.duration)
    ? Math.round(eventData.duration): 100;
  return baseEvent;
}

var AttributeViewClicked = function (baseEvent, eventData) {
  baseEvent.attributeName =
    eventData.attributeName || "";
  baseEvent.attributeValueName =
    eventData.attributeValueName || "";
  return baseEvent;
}

var ViewChanged = function (baseEvent, eventData) {
  baseEvent.viewName = eventData;
  return baseEvent;
}

var Rotation = function (baseEvent, eventData) {
  addCoordinates(baseEvent, "startPosition", eventData.startPosition);
  addCoordinates(baseEvent, "endPosition", eventData.endPosition);
  baseEvent.source = (eventData && eventData.source)
    ? eventData.source : "auto";
  return baseEvent;
}

function truncateFloat(value, length) {
  return Number((value).toFixed(length));
}

var addCoordinates = function (baseEvent, keyName, coordinates) {
  if (keyName === "startPosition") {
    baseEvent["startPositionX"] =
      truncateFloat(coordinates.x, 3);
    baseEvent["startPositionY"] =
      truncateFloat(coordinates.y, 3);
    baseEvent["startPositionZ"] =
      truncateFloat(coordinates.z, 3);

  } else {
    baseEvent["endPositionX"] =
      truncateFloat(coordinates.x, 3);
    baseEvent["endPositionY"] =
      truncateFloat(coordinates.y, 3);
    baseEvent["endPositionZ"] =
      truncateFloat(coordinates.z, 3);
  }
}

var Zoom = function (baseEvent, eventData) {
  baseEvent.distance =
    truncateFloat(eventData.zoomDistance, 3);
  baseEvent.action = (eventData && eventData.action)
    ? eventData.action : "zoomIn";
  baseEvent.source = eventData.source || "auto";
  return baseEvent;
}

var TextureLoaded = function (baseEvent, eventData) {
  baseEvent.duration = (eventData && eventData.duration)
    ? Math.round(eventData.duration) : 100;
  baseEvent.textureUrl = (eventData && eventData.textureUrl)
    ? eventData.textureUrl : "";
  return baseEvent;
}

function isDefaultLocale(fcParams) {
  var isDefaultLocale = false;
  var defaultLocale = fcParams.locales.default;

  if (
    !fcParams.locale ||
    fcParams.locale.toLowerCase() === defaultLocale.toLowerCase()
  ) {
    isDefaultLocale = true;
  }

  return isDefaultLocale;
}

var RecipeChanged = function (baseEvent, eventData, fcParams) {
  var ca = eventData.ca;
  var av = eventData.av;
  baseEvent.attributeName = ca.name;
  baseEvent.locale = fcParams.locale|| fcParams.locales.default;
  baseEvent.locale = baseEvent.locale.toLowerCase();
  baseEvent.isDefaultLocale = isDefaultLocale(fcParams);
  baseEvent.attributeId = ca.id;
  if (av.text) {
    baseEvent.type = eventTypes.RECIPE_TEXT_CHANGED;
    baseEvent.value = av.text;
  } else {
    baseEvent.value = av.color || av.url;
    baseEvent.attributeValueName = av.name;
    baseEvent.attributeValueId = av.id;
  }
  return baseEvent;
};

var UgcImageSaved = function (baseEvent) {
  baseEvent.type = "imageUploaded";

  return baseEvent;
};

var Shared = function (baseEvent, platform, eventData) {
  /**
   * metrics service saves share event in a single table,
   * so for this reazon we change type to shared.
   */
  baseEvent.type = "sharing";
  baseEvent.platform = platform;
  baseEvent.recipeId = getRecipeId(eventData);

  return baseEvent;
}

function getRecipeId (eventData) {
  if (eventData.recipeId) {
    return eventData.recipeId;
  }

  var recipeId = "";
  try {
    var regexRecipeId = /recipeId\=(\d+)/;
    recipeId = regexRecipeId.exec(eventData.recipeUrl)[1];
  } catch (ex) {
    console.log(ex.messege);
  }
  return recipeId;
}
