/**
 * @publicDocs methods/getUpcharges ../../docs/get_upcharges.md
 */

var
  _ = require("lodash");

module.exports = function (engine, api) {

  function getUpcharges(options) {
    options = options || {};

    var
      caKey = options.caKey || "alias",
      recipe = engine.recipe(),
      currency = options.currency || api.getCurrency();

    if (!currency) {
      throw new Error("Missing options.currency for getUpcharges(options)");
    }


    if (options.format === "aggregated") {
      return getAggregatedUpcharges(_.omit(options, "format"));
    }

    return _.reduce(recipe.getData(), function (result, entry) {
      if (entry.av && entry.ca && !entry.ca.hasDynamicValue()) {
        if (options.excludeInvisible && !entry.ca.isVisible()) {
          return result;
        }
        var upcharge = entry.av.getUpchargeFor(entry.ca, currency);
        var key = entry.ca.getData()[caKey];
        result[key] = upcharge;
      }
      return result;
    }, {});
  }



  var i = require("icepick");

  function getAggregatedUpcharges(options) {
    options = options || {};
    var caKey = options.caKey || "alias";
    var upcharges = getUpcharges(_.defaults(
      {
        caKey: "id",
      }, _.omit(options, ["format", "caKey"])
    ));

    var productJson = api.getProduct();
    var allAttributes = productJson.allAttributes;

    function sumUpResult(result, caId, upcharge, children) {
      var attribute = i.getIn(productJson, allAttributes[caId]);
      var key = attribute[caKey];
      children = children || [key];
      if (result[key]) {
        result[key].upcharge = upcharge;
        if (upcharge) {
          result[key].sum += upcharge;
          result[key].summed = result[key].summed.concat(children);
        }
      } else {
        result[key] = {
          upcharge: upcharge,
          sum: upcharge,
          summed: (upcharge && children) || []
        };
      }
      if (attribute.parentId) {
        sumUpResult(result, attribute.parentId, upcharge, children);
      }
    }

    var result = _.reduce(upcharges, function (result, upcharge, caId) {

      sumUpResult(result, caId, upcharge);

      return result;
    }, {});

    return result;
  }

  return getUpcharges;
};
