var
  _ = require("lodash");


/**
 * Updates the list of ca.sympatheticFilters() that are made available for
 * a configurable attribute given a sympathetic filter slave
 * relationship
 */
module.exports = function applySympatheticFilters(ca) {

  // We will use a Vans shoe to describe this behavior:
  //
  // http://tinyurl.com/nqfdlx8
  //
  // The shoe makes use of the following graph:
  //
  // http://tinyurl.com/p97ku2d
  //
  // Vans uses Sympathetic filters to restrict what sizes to
  // suggest when configuring their shoes.
  //
  // That way when the product has the "Boys" audience, only smaller
  // sizes can be selected.
  //
  // Lets see walk along with the graph to describe how the relationships
  // are figured out.

  // We start by selecting the Size (in red) ca (configurable attribute)


  // Another example here:
  // Lets say we are using this product configuration http://tinyurl.com/lottdzw
  // As you can see, the attribute ca:32273 has two sympathetic facet filters
  // and both of them are pointing or have ca:32188 as slave
  //
  /* {
      "ca:32273": {
        "sympatheticFacets": [
          {
            "mastersRelation": "or",
            "masters": [
              {
                "facet": "f:21877",
                "facetValue": "fv:31007"
              }
            ],
            "slaves": [
              {
                "productAttribute": "ca:32188",
                "facet": "f:21874",
                "facetValue": "fv:30966"
              }
            ]
          },
          {
            "mastersRelation": "or",
            "masters": [
              {
                "facet": "f:21877",
                "facetValue": "fv:31006"
              }
            ],
            "slaves": [
              {
                "productAttribute": "ca:32188",
                "facet": "f:21876",
                "facetValue": "fv:31000"
              }
            ]
          }
        ]
      }
    }
  */
  // At the very beginning, when the recipe is built, one of the filters
  // becomes active due to the selection of the value on the master attribute.
  // But when another value is selected on the master attribute, due to
  // user interaction, the filters will probably change. So to ensure that only
  // the proper/active filters will be taken into account, we have to clean
  // all the previous filters doing something like
  // ca.recipeFilters().sympatheticFilters = {}
  // or just assigning new ones and replacing old ones.

  var newSympatheticFilters = {};
  var currentSympatheticFilters = ca.sympatheticFilters();

  // as we can see there are more than 30 av (attribute values) in this case
  // sizes painted as orange circles to choose from.

  // To restrict the number of sizes we need to get a list
  // of all configurable attributes that drive the
  // filtering
  var sympatheticSlaves = ca.sympatheticSlaves();
  var pendingFacetChanges = {};

  _.each(sympatheticSlaves, function (sfs) {
    var
      previousFv,
      potentialFv,
      potentialFacet,
      sf,
      masterCa,
      masterAv,
      masterAvFvs,
      masters,
      masterRelation,
      anyOrAll,
      filterIsActive;

    // potential facet value to enforce on this CA
    potentialFv = sfs.potentialFacetValue();
    potentialFacet = sfs.facet();
    // the filter that mediates
    sf = sfs.rule();
    // master CA we are checking
    masterCa = sf.masterAttribute();

    // selected av and facet values for that CA
    masterAv = masterCa.getSelectedOrDefaultValue();
    // facet values the selected AV has
    masterAvFvs = (masterAv && masterAv.facetValues()) || [];

    // all the master rules
    masters = sf.masters();
    masterRelation = sf.relationshipType();
    anyOrAll = {"or": _.any, "and": _.all}[masterRelation];

    filterIsActive = anyOrAll(masters, function (sfm) {
      var masterFv = sfm.facetValue();

      // if the selected av on the master CA has a facet value that matches
      // the value in the SFM, this rule is enabled
      return masterFv && !!_.find(masterAvFvs, {id: masterFv.id});
    });

    previousFv = currentSympatheticFilters[potentialFacet.id];

    if (filterIsActive && potentialFv) {
      newSympatheticFilters[potentialFacet.id] = potentialFv;

      if (!previousFv || previousFv.id !== potentialFv.id) {
        addFacetChange(potentialFacet, potentialFv);
      }
    } else {
      if (previousFv != null) {
        addFacetChange(potentialFacet, null);
      }
    }
  });

  function addFacetChange(f, fv) {
    /*var existingChange = pendingFacetChanges[f.id];
    if (existingChange && existingChange.fv && existingChange.fv !== fv) {
      console.warn("Conflicting sympathetic filter for " + ca);
    }*/
    pendingFacetChanges[f.id] = {
      f: f,
      fv: fv
    };
  }

  _.each(pendingFacetChanges, function (obj) {
    ca.recipe().notifyFacet([{
      ca: ca,
      f: obj.f,
      fv: obj.fv
    }]);
  });

  ca.recipeFilters().sympatheticFilters = newSympatheticFilters;

  return ca;
};
