var _ = require("lodash");

module.exports = function (options) {
  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * Facet.
   *
   * On the graph their vertices contain the namespace (ns) "pf"
   *
   * @param {Object} options Options
   * @param {number} options.id Facet numeric id.
   * @class
   * @name ProductFacet
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(
    /** @lends ProductFacet */ {
      ns: "pf",

      /**
       * Gets the {@link FacetValue}s for this ProductFacet
       * @instance
       * @return {Array<FacetValue>}
       */
      facetValues: function () {
        return this.getAndCache("facetValues", function () {
          var instances = this.traverser()
            .outgoing("hasFacetValue")
            .expand()
            .sortBy("order")
            .instances();

          _.each(instances, function (fv) {
            fv._facet = fv.facet();
          });
          return instances;
        });
      },

      /**
       * Gets the {@link Facet} for this ProductFacet
       * @instance
       * @return {Facet}
       */
      facet: function () {
        return this.getAndCache("facet", function () {
          return this.traverser().outgoing("hasFacet").instance();
        });
      },
    },
    constructor
  );
};
