/**
 * @publicDocs methods/getAttributes ../../docs/get_attributes.md
 */
var _ = require("lodash"),
  clone = _.compose(JSON.parse, JSON.stringify),
  searchTypeMap = {
    // Gets a all the attributes available for a product including
    // subattributes
    all: "allAttributes",
    // Attributes that do not have a UI selector.
    nonSelectable: "nonSelectableAttributes",
    // Gets a all the attributes available for a product except the ones that
    // can be used as sympathetic filter slaves
    nonSympatheticSlave: "nonSympatheticSlaveAttributes"
  },
  MAX_EXPENSIVE_CALLS_FACTOR = process.env.MAX_EXPENSIVE_CALLS_FACTOR || 4;

module.exports = function (engine) {
  var product = engine.product(),
    maxCalls,
    totalCalls = 0;


  /**
   * Gets a collection of attributes for the current Product.
   *
   * This method is expensive and should not be used lightly. Specially on large
   * products. By default a throttling mechanism will prevent this method from
   * being used passed n (total attributes * 4) calls. A console.warn message
   * will be logged and no results will be returned.
   *
   * @param  {Object} options - Options object
   * @param  {Object} options.filter - Filters the attributes using
   *                                 [_.filter](https://lodash.com/docs#filter)
   * @param  {Object} options.throttle - Shall this method have throttling
   *                                     protection (default: true)
   * @param  {Object} options.searchType - The following search types are
   *  allowed:
   *    - all: (default) Gets a all the attributes available for a product
   *           including subattributes
   *    - nonSelectable: Attributes that do not have a UI selector.
   *    - nonSympatheticSlave: Gets a all the attributes available for a product
   *                           except the ones that can be used as sympathetic
   *                           filter slaves.
   * @returns {Array<Object>} An array with Configurable Attribute POJO's
   */
  return function getAttributes(options) {
    var attributes;

    options = options || {};

    validateOptions(options);

    if (!throttle(options)) { return []; }

    attributes = getAttributesFromEngine(product, options);

    maxCalls = maxCalls || (attributes.length * MAX_EXPENSIVE_CALLS_FACTOR);

    return filterAttributes(attributes, options);
  };

  function throttle(options) {
    if (options.throttle !== false) {
      totalCalls += 1;
      if (maxCalls && (totalCalls > maxCalls)) {

        console.warn("Maximum of " + maxCalls +
          " getAttributes calls reached. Given how expensive this method is, " +
          "you should only use it for bootstrapping your application.");
        return false;
      }
    }
    return true;
  }

};


function getAttributesFromEngine(product, options) {
  // Cloning will expand the attributes
  return clone(product[options.searchType]());
}


function filterAttributes(attributes, options) {
  if (options.filter) {
    attributes = _.filter(attributes, options.filter);
  }
  return attributes;
}


function getSearchType(searchType) {
  if (searchType) {
    searchType = searchTypeMap[searchType];
  } else {
    searchType = searchTypeMap.all;
  }
  return searchType;
}

function validateOptions(options) {
  if (!_.isPlainObject(options)) {
    throw new Error("getAttributes options should be an Object");
  }
  if (options.filter) {
    validateFilter(options.filter);
  }
  options.searchType = getSearchType(options.searchType);
  if (!options.searchType) {
    throw new Error("getAttributes unknown options.searchType. Valid search" +
      " types are " + _.keys(searchTypeMap).join(", "));
  }
}

function validateFilter(filter) {
  if (process.env.NODE_ENV !== "production") {
    var isValid = _.isFunction(filter) || _.isPlainObject(filter);

    if (!isValid) {
      throw new Error("getAttributes filter requires an object or " +
        "a function. Please check https://lodash.com/docs#filter for details");
    }
  }
}
