module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGlEnvironmentCamera.
   *
   * On the graph their vertices contain the namespace (ns) "wcam"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGlEnvironmentCamera numeric id.
   * @class
   * @name WebGlEnvironmentCamera
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlEnvironmentCamera */{

    ns: "wcam"

  }, constructor);

};
