/**
 * @publicDocs methods/getWebGlLoadingStrategies
 *   ../../docs/get_webgl_loading_strategies.md
 */
var _ = require("lodash");
var validateFilter = require("../util").validateFilter;


module.exports = function (getProduct) {
  /**
   * Gets a collection of WebGl Loading Strategy for the current Product.
   *
   * @param  {Object} options - Options object
   * @param  {Object} options.filter - The object of property webgl strategy to
   *   filter by
   * @returns {Array<Object>} An array of WebGl Loading Strategy entities
   */
  return function getWebGlLoadingStrategies(options) {
    if (options && options.filter) {
      validateFilter(options.filter);
    }
    return _.filter(
      getProduct().webGlLoadingStrategies, options && options.filter
    );
  };
}
