/**
 * @publicDocs methods/getWebGlMaterial ../../docs/get_webgl_material.md
 */

module.exports = function (engine, getProductJson) {
  var product = engine.product();

  return function getWebGlMaterial(query) {
    var modelNode = product.findWebGlMaterial(query);

    if (!modelNode) {
      throw new Error(
        "getWebGlMaterial could not find a WebGl material matching " +
          JSON.stringify(query) +
          ". Available WebGl material are: " +
          product.webGlMaterials().join(", ")
      );
    }

    return getProductJson().webGlMaterials[modelNode.id];
  };
};
