var
  _ = require("lodash"),
  graphVersions = 1,
  FROM = 0,
  TO = 1;

// Expands the configuration graph in order to make
// edges easier to find and iterate
module.exports = function (graph) {

  if (graph.version) {
    return graph;
  }

  // Prevents re-expanding the graph and keep track
  // of the version so we can use the graph as a memoization
  // hash. This is useful when generating model classes
  graph.version = graphVersions;
  graphVersions += 1;

  // map from position to vertex key
  var posKeyMap = new Array(graph.vertices.length);

  // Generates an index hashmap where the key is the ns:id and the
  // value is the position of the vertex on the vertices array
  // Given we want to have the ability to search
  // for vertices by their ns, adding the full ns:id
  // on the JSON would create too much overhead for something that
  // can be added cheaply on runtime ad .vertexKey
  graph.vertexIndex = {};
  graph.vertexNsIndex = {};


  var len = graph.vertices.length,
    vertex, pos, vertexKey;
  for (pos = 0; len > pos; pos = 1 + pos) {
    vertex = graph.vertices[pos];
    vertexKey = vertex.ns + ":" + vertex.id;
    vertex.vertexKey = vertexKey;

    graph.vertexIndex[vertexKey] = vertex;
    graph.vertexNsIndex[vertex.ns] = true;

    posKeyMap[pos] = vertexKey;
  }

  // Generates an edges list and a vertex hash map
  graph.edges = [];
  graph.outgoingVertices = {};
  graph.incomingVertices = {};

  // create the incoming/outgoing maps first
  _.each(graph.edgeGroups, function (edges, type) {
    graph.outgoingVertices[type] = {};
    graph.incomingVertices[type] = {};
  });


  _.each(graph.edgeGroups, function indexEdges(edges, type) {
    var i = 0, len = edges.length, edge, fromId, toId;
    var outMap = graph.outgoingVertices[type];
    var inMap = graph.incomingVertices[type];


    for (; len > i; i += 1) {
      edge = edges[i];
      fromId = posKeyMap[edge[FROM]];
      toId = posKeyMap[edge[TO]];


      if (outMap[fromId] === undefined) {
        outMap[fromId] = [];
      }
      outMap[fromId].push(edge[TO]);

      if (inMap[toId] === undefined) {
        inMap[toId] = [];
      }
      inMap[toId].push(edge[FROM]);


      graph.edges.push([fromId, toId, type]);
    }
  });


  // Only freeze the graph when running on the server/node
  // Deep freezing is very expensive but we want to make sure
  // that node.js tests prevent us from changing the configuration
  // graph structure which should remain immutable.
  if (process.env.FREEZE_GRAPH) {
    require("deep-freeze")(graph);
  }


  return graph;
};
