/* eslint complexity:0, max-len: 0 */

/**
 * @publicDocs methods/selectValue ../../docs/select_value.md
 */

var _ = require("lodash");

// eslint-disable-next-line max-params
module.exports = function (engine, userEdited, api, isFallbackRecipe, alwaysNotify) {
  userEdited = userEdited || {};

  function handleError(err, callback) {
    if (callback) {
      return callback(err);
    } else {
      throw err;
    }
  }

  return function selectValue(/* [ca, av] or {ca: *, av: *}, *callback */) {
    if (arguments.length === 0) {
      throw new Error("Missing arguments for selectValue(options, callback)");
    }

    var recipe = engine.recipe();
    var options = parseOptions.call(null, arguments);
    var ca = options.ca;
    var av = options.av;
    var callback = options.callback;

    if (callback && !_.isFunction(callback)) {
      throw new Error("Invalid arguments for selectValue(options, callback)");
    }
    // Cant wait for changes when using the method within a transaction
    if (callback) {
      recipe.once("change", onChange);
    }

    try {
      if (ca && ca.id && userEdited.cas) {
        userEdited.cas[ca.id] = true;
      }
      recipe.setOne(ca, av, isFallbackRecipe, alwaysNotify);
    } catch (e) {
      recipe.off("change", onChange);
      /**
       * @publicDocs events/recipe:invalid-selection
       * ../../docs/recipe_invalid_selection_event.md
       */
      api.trigger("recipe:invalid-selection", {error: e, ca: ca, av: av});
      return handleError(e, callback);
    }

    function onChange(data) {
      callback(null, JSON.parse(JSON.stringify(data)));
    }
  };
};

function getNormalizedCa (ca) {
  // Supporting "alias" setting
  if (_.isString(ca)) {
    ca = { alias: ca };
  }

  if (_.isNumber(ca)) {
    ca = { id: ca };
  }

  return ca;
}

function normalizeAvFormat (av) {
  // Supporting , "name" value setting
  if (_.isString(av)) {
    av = { name: av };
  }

  if (_.isNumber(av)) {
    av = { id: av };
  }

  return av;
}

function parseCaAndAv(params, callback) {
  var ca;
  var av;
  var args = params[0];
  // check if we have a callback on the params
  if (params[1] && _.isFunction(params[1])) {
    callback = params[1];
  }
  //check if this is an array to get ca and av
  if (_.isArray(args)) {
    ca = args[0];
    av = args[1];
  }
  // check if this is an object to get ca and av
  if (_.isObject(args) && !_.isArray(args)) {
    ca = args.ca || args.caId;
    av = args.av || args.avId;
  }
  // format the ca and the av
  ca = getNormalizedCa(ca);
  av = normalizeAvFormat(av);

  return {ca: ca, av: av, callback: callback};
}

function parseOptions(params, callback) {
  var result = {};

  if (params.length === 1 || params.length === 2) {
    result = parseCaAndAv(params, callback);
  } else {
    result = {
      ca: getNormalizedCa(params[0]),
      av: normalizeAvFormat(params[1]),
      callback: params[2]
    };
  }

  return result;
}

module.exports.parseOptions = parseOptions;