var _ = require("lodash");

module.exports = function addBaseUrls(fcParams, productGraph) {
  var baseUrl;
  _.each(_.where(productGraph.vertices, {ns: "av"}), function (av) {
    baseUrl = fcParams.productBaseUrl;

    // clipArts values are placed inside customer's folder instead of product's
    if (av.clipArtImageUrl) {
      baseUrl = fcParams.customerBaseUrl;

      av.clipArtImageUrl = baseUrl + av.clipArtImageUrl;
    }

    if (av.tooltipImage) {
      av.tooltipImage = baseUrl + av.tooltipImage;
    }
    if (av.url) {
      av.url = baseUrl + av.url;
    }
  });
};