module.exports = applyParameterizedFacetFilters;

var _ = require("lodash");

// Parameterized facets contain facets that are pre-selected
// based on the contents of fcParams.facetFilter
// and a URL parameter.
function applyParameterizedFacetFilters(fcParams, engine) {
  var facetFilters = fcParams.facetFilters;
  if (!_.size(facetFilters)) {
    return;
  }
  var wasModified = false;
  // FC2 does not include information about the ca's that
  // are used for parameterized facets.
  // While this might seem inefficient at first time there's
  // some reasoning for them not to explicitly specify the ca's:
  // some facets could be applied to many ca's and creating
  // the map could be tedious, error prone and require code changes
  var cas = engine.product().allAttributes();
  _.each(cas, function (ca) {
    var facets = ca.parameterizedFacets();
    _.each(facets, function (facet) {
      var facetName = facet.getData().name;
      var filters = _.where(facetFilters, {facet: facetName});
      _.each(filters, function (filter) {
        ca.selectFacetValue(
          facet,
          {name: filter.facetValue}
        );
        wasModified = true;
      });
    });
  });
  if (wasModified) {
    engine.recipe().saveAsDefault();
  }
}
