var urlSettings = require("./configure.json"),
  getUrlComposer = require("./url_composer.js"),
  urlTemplates = urlSettings.templates,
  method,
  methods = {};

for (method in urlTemplates) {
  if (urlTemplates.hasOwnProperty(method)) {
    methods[method] = getUrlComposer(urlTemplates[method], urlSettings);
    methods[method].name = method;
  }
}

module.exports = methods;
