/**
 * @publicDocs methods/validateText ../../docs/validate_text.md
 */

var _ = require("lodash");
var async = require("async");
var fetchPreferences = require("../preferences");
var request = require("superagent");
var getMigration = require("../migrations/2018-07-24-validateText")

var memory = {};

module.exports = function (fcParams) {
  var validateTextEndpoint = fcParams.validateTextEndpoint || (
    "https://" + fcParams.configureEndpoint + "/wordfilter/check"
  );

  return function validateText(text, language, callback) {
    var memoKey = text + ":" + language;

    // Memoize validations
    if (memory[memoKey]) {
      return callback(null, memory[memoKey]);
    }

    if (_.isUndefined(text) || _.isUndefined(language) ||
      _.isUndefined(callback)) {

      throw new Error("Missing arguments for " +
        "validateText(text, language, callback)");
    }

    if (!_.isFunction(callback)) {
      throw new Error("Invalid arguments for " +
        "validateText(text, language, callback). callback parameter " +
        "must be a function");
    }

    var options = {
      text: text,
      language: language,
      callback: callback
    };

    async.auto({
      validateTextEndpoint: async.constant(validateTextEndpoint),
      fcParams: async.constant(fcParams),
      options: async.constant(options),
      preferences: ["fcParams", "options", autoPreferences],
      parameters: ["preferences", autoParameters],
      validateText: [
        "parameters", "validateTextEndpoint",
        autoValidateText
      ]
    }, function (err, results) {
      var callback = results.options.callback;
      if (err) {
        if (process.env.NODE_ENV !== "production") {
          console.warn("validateText failed with error:" +
            (err && err.message));
        }
        return callback(err);
      }
      var formatResponse = getMigration(results.fcParams.apiVersion);
      memory[memoKey] = formatResponse(results.validateText);

      callback(null, memory[memoKey]);
    });
  };
};

function autoPreferences(cb, results) {
  fetchPreferences(results.fcParams, cb);
}

function autoParameters(cb, results) {
  var fcParams = results.fcParams;
  var parameters = {
    text: results.options.text,
    lang: results.options.language,
    apiKey: (fcParams.preferences || results.preferences || {}).apiKey,
    workflow: fcParams.workflow,
    // The value for configVersion needs to be set to for the cache busting
    // to work on the word filter, but in case it's not set we'll always cache
    // using the same "irrelevant" key
    configVersion: fcParams.version || "irrelevant"
  };
  if (fcParams.environment) {
    parameters.environment = fcParams.environment;
  }

  cb(null, parameters);
}

function autoValidateText(cb, results) {
  request
    .get(results.validateTextEndpoint)
    // Wait 8 seconds for the server to start sending,
    // We need a high number here in order to be able to
    // reach prod.fluidconfigure.cn which might be slow from
    // outside of China
    .timeout(8000)
    .query(results.parameters)
    .set({
      Accept: "application/json",
      "X-Api-Key": results.parameters.apiKey
    })
    .end(function (err, res) {
      if (err) { return cb(err); }
      if (res.status >= 400) {
        return cb(new Error(res.text));
      }
      cb(null, res.body);
    });
}
