/* eslint complexity:0 */
var _ = require("lodash");
// Some properties that are passed as customer data to the
// saveRecipe(customerData, cd) method should not be part of the
// recipe.customerData section
var ignoredProperties = [
  "recipePoster", "recipeV3Poster","stripPersonalization", "images", "purpose",
  "eventBus", "uiSettings", "beforeSave", "afterSave", "timeout", "onTimeout",
  "catalogs", "catalog"
];

var recipeDefaults = {
  productQuantities:{
    // PD-1554 we have to keep product quantity as a string
    // to keep backwards compatibility with customers that are
    // type checking it as a string on configure-ui@3
    product: "1"
  } ,
  customerData: {
    isOnCatalog: false,
    recipeName: ""
  }
};

var publishedDataEnv = process.env.FLUID_PUBLISHED_DATA_ENV || "prod";

var LAST_DIGITS_RE = /(\d+)$/;

var request = require("superagent");

// This base URL is used during the save process by Fluid servers, so
// there's no need to use the CDN http://configure-published.fluidretail.net
var baseUrlBase = "http://fluid-configure-published.s3.amazonaws.com/";

var getProductQuantity = require("./utils").getProductQuantity;

module.exports = {
  recipeData: recipeData,
  getPerformPostRecipeFunction: getPerformPostRecipeFunction,
  postRecipeData: postRecipeData,
  getRecipeResourceUrl: getRecipeResourceUrl,
  computeDefaults: computeDefaults,
  conciseRecipe: conciseRecipe
}

function conciseRecipe(steps, engine, getRecipe, cb) {
  var result;
  if (!steps.customerData.stripPersonalization) {
    result = getRecipe("legacyConcise");
  } else {
    var recipe = engine.recipe();
    recipe.start();
    recipe.removePersonalValues();
    result = getRecipe("legacyConcise");
    recipe.rollback();
  }
  cb(null, result);
}


/** Uses either a custom method passed via fcParams.recipePoster,
  options.recipePoster or a JSONp call to save the recipe on the server
  and get back an object with the {id: 123}
*/
function getPerformPostRecipeFunction(cb, steps, fcParams) {
  var apiKey = steps.preferences.apiKey;

  /** Posts the recipe to the recipe-v1-api */
  function xhrPost(url, data, callback) {
    request
      .post(url)
      .send(data)
      .type("json")
      .set("X-Api-Key", apiKey)
      .set("Accept", "application/json")
      .end(function (err, res) {
        if (err) {
          var message = _.get(err, "response.body.message", err.message);
          return callback(new Error("Couldn't save recipe: " + message));
        }
        callback(null, JSON.parse(JSON.stringify(res.body)));
      });
  }

  var performPost = steps.customerData.recipePoster || fcParams.recipePoster ||
    xhrPost;
  cb(null, performPost);
}

/** Merges all the info required to post the recipe into a single POJO */
function recipeData(cb, steps) {

  var customerData = steps.customerData;
  var data = _.merge(
    steps.data,
    recipeDefaults,
    steps.computedDefaults, {
      customerData: _.omit(customerData, ignoredProperties)
    }
  );

  try {
    data.customerData = JSON.stringify(data.customerData);
  } catch (e) {
    return cb(new Error("Failed serializing saveRecipe POST Data. " +
      "Got error: " + e.message));
  }

  // We only need images persisted on the DB for legacy compatibility.
  // After FC3 the server should be able to build the URL's for any given
  // purpose on demand
  if (customerData.images) {
    data.imagesUrl = customerData.images;
  }
  // Legacy purpose, the comment on data.imagesUrl applies here as well
  // TODO: Remove this as soon as FC2 is phased out
  if (customerData.purpose) {
    data.purpose = customerData.purpose;
  }

  data.apiVersion = steps.apiVersion;

  cb(null, data);
}

/** Computed default data expected by the PHP endpoint, including creation
  date, quantity, locale, productId... */
function computeDefaults(cb, steps, fcParams, api) {
  var data = steps.data;
  var customerData = steps.customerData || {};
  if (steps.hasTimedOut()) {
    return;
  }
  var productId = LAST_DIGITS_RE.exec(_.get(fcParams, "product",
    fcParams.productId))[1];
  var defaults = {
    locale: steps.locale,
    apiKey: steps.preferences.apiKey,
    licenseeKey: steps.preferences.licenseeKey,
    // TODO: Remove prefix as soon as the PHP endpoint is replaced with the
    // Node.js code
    productId: "p_" + productId,
    // TODO: Use a transaction to remove personalizations if the
    // stripPersonalization is set to true
    conciseRecipe: steps.conciseRecipe,
    // configVersion is used to help invalidating CDN caches after
    // a customer has published in order to keep consistency on recipes
    // that rely on multiple config files spread across the S3
    configVersion: steps.configVersion,
    // productQuantities should be sent like an object
    // since it is the current format on recipe-v1-api.
    productQuantities: {
      // PD-1554 product quantity was a string on configure-ui@3 and
      // we have to keep it that was as customers are type checking it
      product: "" + getProductQuantity(customerData, api)
    },
    // TODO: Remove as soon as the price can be computed by the
    // backend servers using the configuration-engine directly
    price: customerData.price || api.getPrice(),
    baseUrl: buildBaseUrl(data),
    customerData: _.defaults({
      FC_LOCALE: steps.locale, // Legacy location for locales
      // The catalog does not expect milliseconds on the ISO Stringified
      // date and changing this will result on breaking implementations
      // relaying on this format. DEV-1457. Once the recipes are persisted
      // they will contain milliseconds, but the API on
      // retrieveCatalogRecipeJsonp.php does not reflect them.
      FC_CREATION_DATE: (new Date()).toISOString().replace(/\.[0-9]{3}/, ""),
      recipeName: ""
    }, steps.beforeSaveHook || {})
  };

  addCatalogDefaults(defaults, customerData);

  cb(null, defaults);
}



function addCatalogDefaults(data, customerData) {
  if (customerData.catalogs) {
    data.catalogs = customerData.catalogs;
  }

  if (data.catalogs || customerData.catalog) {
    data.customerData.isOnCatalog = true;
  }
}

/**
 * We include on the final response the API URL where the recipe resource
 * can be accessed. If the customer provided an API key on fcParams we will
 * include it on the URL.
 */
function getRecipeResourceUrl(fcParams, id, steps) {
  var apiKey = fcParams.apiKey || steps.preferences.apiKey;

  return "https://cdn-" + fcParams.configureEndpoint + "/api/recipe/" + id +
    (apiKey ? ("?apiKey=" + apiKey) : "");
}

/** Performs the actual POST of the recipe to a server. Given the actual
  "postRecipe" function can be implemented externally, we need to wrap it on
  a try/catch block to bubble up any error through the async interface */
function postRecipeData(cb, steps) {
  var recipeData = steps.recipeV1Data;
  recipeData.recipeSelection = steps.selectionRecipe;
  if (steps.hasTimedOut()) {
    return;
  }

  if (!recipeData) {
    return cb();
  }

  var errCopy = "Failed posting recipe, got error: ";

  // this method might trigger sync callbacks or throw exceptions
  // from superagent
  try {
    var postRecipe = steps.performV1PostFunction;
    var endpoint = steps.saveEndpoint;
    postRecipe(endpoint,
    recipeData,
      function (err, response) {
        if (!err && !response) {
          err = new Error("Empty response body");
        } else if (!err && !response.id) {
          err = new Error("Server response does not include recipe id");
        }
        if (err) {
          err = new Error(errCopy + err.message);
        }
        cb(err, response);
      });
  } catch (e) {
    cb(new Error(errCopy + e.message));
  }
}



/** Given an environment and a workflow gets the base URL */
function buildBaseUrl(data) {
  return baseUrlBase +
    encodeURIComponent(data.environment || publishedDataEnv) + "/" +
    encodeURIComponent(data.workflow || "prod");
}
