var applySympatheticFilters = require("./rules/apply_sympathetic_filters"),
  _ = require("lodash"),
  RE_LAST_NUMBERS = require("../legacy/common").RE_LAST_NUMBERS,
  // Dynamic attributes are not represented on graph edges, so we have
  // to create them explicitly
  DynamicAttributeValue = require("./dynamic_attribute_value");

module.exports = function (contextOptions) {
  var BaseModel = require("./base")(contextOptions),
    constructor;

  /**
   * Configurable Attribute.
   *
   * Configurable attributes belong to a {@link ConfigurableProduct}
   * or to other Configurable Attributes (named as sub attributes).
   * They contain {@link AttributeValue}s and implement the logic for
   * toggling selectable {@link AttributeValue}s by using {@link Filter}s
   * and other strategies.
   *
   * On the graph their vertices contain the namespace (ns) "ca"
   *
   * @param {Object} options Options
   * @param {number} options.id Configurable Attribute numeric id.
   * @class
   * @name ConfigurableAttribute
   * @constructor
   * @augments BaseModel
   */
  constructor = function (options) {
    BaseModel.call(this, options);

    // eagerly evaluate facets so they will be pre-cached
    this.allFacets();

    // Initialize recipeFilters
    this.recipe().context().recipeFilters[this.id] = {
      // it is updated when the value gets set for CA or when facet is set directly
      selectedFacetMatchFilters: {},
      // it is updated when the value gets set for CA or when facet is set directly
      // {[f.id]: { [caId]: fv }
      selectedFacetExcludeFilters: {},
      sympatheticFilters: {},
    };
  };

  /** @memberOf ConfigurableAttribute */
  return BaseModel.extend(
    /** @lends ConfigurableAttribute */ {
      ns: "ca",

      /**
       * Gets a reference to the data for this instance.
       * Overwriting default method to ensure to provide model components in a way suitable for configure core.
       * @returns {Object}
       */
      getData: function () {
        var data = BaseModel.prototype.getData.call(this);
        if (!data.modelComponents && this.modelComponents().length) {
          data.modelComponents = this.modelComponents().map(function (
            component
          ) {
            return component.getData();
          });
        }
        if (data.modelComponents && data.modelComponents.length) {
          data.avModelComponents = _.clone(this.avModelComponents());
          Object.keys(data.avModelComponents).forEach(function (avId) {
            data.avModelComponents[avId] = data.avModelComponents[avId].map(
              function (component) {
                return component.getData();
              }
            );
          });
        }

        return data;
      },

      /**
       * Gets a collection of Attribute Value instances that can be selected for
       * this configurable attribute.
       *
       * These values can be presented on a UI as selectable.
       *
       * This is the intersection of sympatheticFiltered values and values
       * filtered by non-hidden facets
       *
       * @instance
       * @param {Boolean} ignoreValueUsage - Skip valueUsage filters?
       * @returns {Array<AttributeValue>}
       */
      selectableValues: function (ignoreValueUsage) {
        // Note: ignoreValueUsage is a parameter that'll be passed down through
        // all other business rules, that'll eventually allow ignoring valueUsage
        // overrides when querying for active values. This is expressly for the
        // purpsose of addressing CD-3710; allowing "inactive" values to still be
        // displayed on the UI.
        var facetExcludedValues = this.facetExcludedValues();
        var selectableValues = this.facetFilteredValues(ignoreValueUsage);

        if (facetExcludedValues.length > 0) {
          return _.difference(selectableValues, facetExcludedValues);
        } else {
          return selectableValues;
        }
      },

      /**
       * Gets a collection of active Attribute Value instances sorted by priority.
       *
       * @instance
       * @param {Boolean} ignoreValueUsage - Skip valueUsage filters?
       * @returns {Array<AttributeValue>}
       */
      activeValues: function (ignoreValueUsage) {
        var cacheName = ignoreValueUsage
          ? "activeValuesWithoutVu"
          : "activeValues";

        return this.getAndCache(cacheName, function () {
          var self = this;
          var values = this.traverser(self.allValues())
            .expand()
            .where({ active: true })
            .sortBy(["priority", "id"])
            .instances();

          // "active" can also be set on the VU.
          return ignoreValueUsage
            ? values
            : _.filter(values, function (av) {
              var valueUsage = self.valueUsageForValue(av);
              return !valueUsage || valueUsage.getData().active !== false;
            });
        });
      },

      /**
       * Gets a collection of active AND inactive (only if set to inactive by
       * a value usage override) Attribute Value instances, sorted by priority.
       *
       * @instance
       * @returns {Array<AttributeValue>}
       */
      valuesIgnoringValueUsageOverrides: function () {
        return this.selectableValues(true);
      },

      /**
       * Gets a collection with all the Attribute Value instances sorted by
       * priority. This might include non-active attributes that might have
       * been discontinued but remain on the configuration graph for loading
       * and rendering older recipes.
       *
       * @instance
       * @returns {Array<AttributeValue>}
       */
      allValues: function () {
        return this.getAndCache("allValues", function () {
          return this.traverser()
            .outgoing("hasAttributeValue")
            .unique()
            .sortBy(["priority", "id"])
            .instances();
        });
      },

      /**
       * Like allValues, except does not sort.  This relies on the original
       * ordering in the legacy config (hopefully).
       *
       * @instance
       * @returns {Array<AttributeValue>}
       */
      unsortedValues: function () {
        return this.getAndCache("unsortedValues", function () {
          return this.traverser().outgoing("hasAttributeValue").instances();
        });
      },

      /**
       * Similar to unsortedValues but might include duplicated values that have
       * been defined via value sets. This is required to generate concise
       * recipes that are backwards compatible with FC2.
       *
       * @instance
       * @returns {Array<AttributeValue>}
       */
      legacyUnsortedValues: function () {
        return this.getAndCache("legacyUnsortedValues", function () {
          var result = this.traverser()
            .outgoing("hasLegacyDuplicatedAttributeValue")
            .instances();
          return result.length ? result : this.unsortedValues();
        });
      },

      /**
       * Gets a single Attribute Value instance given its numeric id.
       *
       * @instance
       * @param  {number} attributeValueId Attribute id
       * @return {AttributeValue}
       */
      valueById: function (attributeValueId) {
        return this.traverser()
          .outgoing("hasAttributeValue")
          .where({ id: attributeValueId })
          .instance();
      },

      /**
       * Gets the default Attribute Value for this configurable attribute.
       *
       * The default value is the first selectable value available.
       *
       * @instance
       * @return {AttributeValue}
       */
      defaultValue: function () {
        var value = this.getValueFromMasterAttribute();

        // If we found a value in the master attribute, we have to check
        // that the same attribute is also present in the slave attribute
        if (
          value &&
          !this.findSelectableValue({ id: value.id, active: true })
        ) {
          // in case it doesn't present, unset value variable
          value = null;
        }

        // If not value was found up to this point, proceed with the
        // facet filtered values and sympathetic values
        // omit clipartEditor, is the only case with values but dynamic
        if (!value && !this.isClipartEditor()) {
          value = _.first(this.facetFilteredValues() || []);
        }

        // Optimizing for the common case first. It's less likely that a
        // value is dynamic (text, clipart...)
        if (!value && this.hasDynamicValue()) {
          value = this.getDynamicAttributeValue();
        }

        return value;
      },

      /**
       * Gets a single attribute value using the search criteria
       * specified as the search parameter.
       *
       * Searches only on selectable values.
       *
       * @instance
       * @param {Object} query - The object of property values to filter by
       * @return {AttributeValue}
       */
      findSelectableValue: function (query) {
        return this.traverser(this.selectableValues())
          .expand()
          .where(query)
          .instance();
      },

      /**
       * Gets the sub attributes for this Configurable Attribute
       *
       * @instance
       * @return {Array<ConfigurableAttribute>}
       */
      subAttributes: function () {
        return this.traverser().outgoing("hasAttribute").instances();
      },

      /**
       * Gets all parent {@link ConfigurableAttribute}s for a sub attribute
       *
       * @instance
       * @return {Array} {@link ConfigurableAttribute}
       * collection
       */
      parentAttributes: function () {
        return this.traverser()
          .incoming("hasAttribute", true)
          .where({ ns: "ca" })
          .instances()
          .reverse();
      },

      /**
       * Gets a collection of attribute values for this attribute that
       * include the facetValue (Category) for the specified
       * facet (Category group).
       *
       * This is used to partially identify what attribute values should be
       * available on sympathetic and parametrized facets.
       *
       * @instance
       * @param {Facet} facet
       * @param {FacetValue} facetValue
       * @return {Array<AttributeValue>}
       */
      valuesWithFacetValue: function (facet, facetValue) {
        return _.filter(this.allValues(), function (av) {
          return av.hasFacetValue(facet, facetValue);
        });
      },

      /**
       * Is this Configurable Attribute a sub-attribute?
       *
       * @instance
       * @return {Boolean}
       */
      isSubAttribute: function () {
        return !!this.getData().parentId;
      },

      /**
       * Does this Configurable Attribute a contain sub-attributes?
       *
       * @instance
       * @return {Boolean}
       */
      hasSubAttributes: function () {
        return this.traverser().outgoingSize("hasAttribute") > 0;
      },

      /**
       * Does this Configurable Attribute a contain values?
       *
       * @instance
       * @return {Boolean}
       */
      hasValues: function () {
        return this.traverser().outgoingSize("hasAttributeValue") > 0;
      },

      /**
       * Gets a collection of value usage instances for this configurable
       * attribute.
       *
       * @instance
       * @return {Array<ValueUsage>}
       */
      valueUsages: function () {
        return this.getAndCache("valueUsages", function () {
          return this.traverser().outgoing("hasValueUsage").instances();
        });
      },

      /**
       * Gets a {@link ValueUsage} instance for a given attribute value
       * on this configurable attribute.
       *
       * @instance
       * @param {AttributeValue} av {@link AttributeValue} Instance
       * @return {ValueUsage}
       */
      valueUsageForValue: function (av) {
        return this.getAndCache("valueUsageFor" + av.id, function () {
          var vus = this.valueUsages();
          return (
            _.find(vus, function (vu) {
              return vu._av === av;
            }) || null
          );
        });
      },

      /**
       * Gets a collection of facet instances for on this configurable attribute.
       *
       * Regular Facet are required Facet that must be shown or applied as a
       * filter in a dropdown
       *
       * Suppose you have a facet named "color type" and the facet values
       * "normal" and "metallic".
       * If the implementation uses the "dropdown" selector the user will
       * have to select between the two values and the related attributes
       * will be shown according to the selected facet value.
       *
       * @instance
       * @return {Array<Facet>}
       */
      facets: function () {
        return this.getAndCache("facets", function () {
          return this.traverser().outgoing("hasFacet").instances();
        });
      },

      /**
       * Gets a collection of facet instances that are controlled by sympathetic
       * filters. These are also usually "hidden" facets.
       *
       * @instance
       * @return {Array<Facet>}
       */
      sympatheticFacets: function () {
        return this.getAndCache("sympatheticFacets", function () {
          return this.traverser()
            .incoming("isSlave")
            .outgoing("isSlave") // this will return f's, fv's and this ca
            .where({ ns: "f" })
            .unique()
            .instances();
        });
      },

      /**
       * Gets a collection of hidden facet instances for this configurable
       * attribute.
       *
       * Hidden Facet behave as regular Facet but there is no visible selector
       * to shown them conditionally. They are always shown.
       *
       * @instance
       * @return {Array<Facet>}
       */
      hiddenFacets: function () {
        return this.getAndCache("hiddenFacets", function () {
          return this.traverser().outgoing("hasHiddenFacet").instances();
        });
      },

      /**
       * Gets a collection of inverted hidden facet instances for this
       * configurable attribute.
       *
       * @instance
       * @return {Array<Facet>}
       */
      invertedHiddenFacets: function () {
        return this.getAndCache("invertedHiddenFacets", function () {
          return this.traverser()
            .outgoing("hasInvertedHiddenFacet")
            .instances();
        });
      },

      /**
       * Gets a collection of optional facet instances for this configurable
       * attribute.
       *
       * Optional Facet behave as required and hidden.
       *
       * @instance
       * @return {Array<Facet>}
       */
      optionalFacets: function () {
        return this.getAndCache("optionalFacets", function () {
          return this.traverser().outgoing("hasOptionalFacet").instances();
        });
      },

      /**
       * Gets a collection of parametrized facet instances for this configurable
       * attribute.
       *
       * Parameterized Facet do not have visible selectors but they can be driven
       * by implementation parameters. i.e. a sunglass in Japan might have a
       * different frame size than in the US, this is specified via initialization
       * parameters.
       *
       * @instance
       * @return {Array<Facet>}
       */
      parameterizedFacets: function () {
        return this.getAndCache("parameterizedFacets", function () {
          return this.traverser().outgoing("hasParameterizedFacet").instances();
        });
      },

      /**
       * Gets a collection of facets instances for this configurable
       * attribute.
       *
       * @instance
       * @return {Array<Facet>}
       */
      allFacets: function () {
        return [].concat(
          this.facets(),
          this.hiddenFacets(),
          this.invertedHiddenFacets(),
          this.optionalFacets(),
          this.parameterizedFacets()
        );
      },

      /**
       * Gets a collection of SympatheticMatch instances.
       *
       * {@link SympatheticMatch} has two outgoing relationships, one to a
       * {@link Facet} and other to a slave {@link ConfigurableAttribute}
       *
       * When we select a {@link ConfigurableAttribute} that has a
       * {@link SympatheticMatch} object, we have to search in the
       * {SlaveAttribute} all the {@link AttributeValue} that have the same
       * {@link FacetValue} that the {@link Facet} of the {@link SympatheticMatch}
       * has in the selected {@link ConfigurableAttribute}
       *
       * @instance
       * @return {Array<SympatheticMatch>}
       */
      sympatheticMatches: function () {
        return this.getAndCache("sympatheticMatches", function () {
          return this.traverser().outgoing("hasSympatheticMatch").instances();
        });
      },

      /**
       * Gets a collection of SympatheticMatch instances in where this
       * {@link ConfigurableAttribute} is the slave.
       *
       * @instance
       * @return {Array<SympatheticMatch>}
       */
      masterSympatheticMatches: function () {
        return this.getAndCache("masterSympatheticMatches", function () {
          return this.traverser().incoming("hasSlave").instances();
        });
      },

      /**
       * Gets a collection of master match {@link ConfigurableAttribute} instances in
       * where this {@link ConfigurableAttribute} is the slave.
       *
       * @instance
       * @return {Array<ConfigurableAttribute>}
       */
      masterSympatheticMatchAttributes: function () {
        return this.getAndCache(
          "masterSympatheticMatchAttributes",
          function () {
            return this.traverser()
              .incoming("hasSlave")
              .incoming("hasSympatheticMatch")
              .instances();
          }
        );
      },

      /**
       * Gets a single facet using the search criteria
       * specified as the search parameter
       *
       * @instance
       * @param {Object} query - The object of property values to filter by
       * @return {Facet}
       */
      findFacet: function (query) {
        if (_.isString(query) || _.isNumber(query)) {
          // assume it's an ID
          query = { id: +query };
        }

        var facet = this.traverser(this.allFacets())
          .expand()
          .where(query)
          .instance();

        if (!facet) {
          throw new Error(
            "Could not find facet " +
              JSON.stringify(query) +
              ". Available facets are: " +
              this.traverser(this.allFacets()).instances()
          );
        }

        return facet;
      },

      /**
       * If the attribute is grouped this method will return the first
       * {@link AttributeGroup} this {@link ConfigurableAttribute} belongs to.
       *
       * This method will go up the {@link ConfigurableAttribute} chain until
       * a {@link ConfigurableAttribute} is found.
       *
       * If you need to only get the direct attribute group for this ca, you'll
       * need to provide the directOnly flag
       *
       * @instance
       * @param {Boolean} directOnly - Only include {@link AttributeGroup}s this
       *                             `ca` is directly linked to.
       * @return {AttributeGroup}
       */
      attributeGroup: function (directOnly) {
        return this.getAndCache(
          "attributeGroup" + (directOnly || "-all"),
          function () {
            return this.traverser()
              .incoming("hasAttribute", !directOnly)
              .where({ ns: "ag" })
              .instance();
          }
        );
      },

      // Configurable attribute filtering

      /**
       * Returns a collection of attribute values that should be selectable for
       * this configurable attribute based on the current filter selection.
       *
       * This is useful for rendering UI's where facets define how attribute
       * values are grouped.
       *
       * @instance
       * @param {Boolean} ignoreValueUsage - Skip valueUsage filters?
       * @returns {Array<AttributeValue>}
       */
      facetFilteredValues: function (ignoreValueUsage) {
        // The ignoreValueUsage parameter is expressly for the purpose of
        // addressing CD-3710; allowing inactive AVs (only when overridden
        // by valueUsages) to still be displayed.
        var self = this;

        var filteredValues = this.sympatheticFilteredValues(ignoreValueUsage);

        const facetMatchFilters = this.selectedFacetMatchFilters();
        const facetExcludeFilters = convertFacetFiltersToPairs(
          self,
          this.selectedFacetExcludeFilters(),
          true
        )
          // match should overwrite exclude
          .filter(function (pair) {
            return !facetMatchFilters[pair.f.id];
          });

        const selectedFacetMatchFilters = convertFacetFiltersToPairs(
          self,
          facetMatchFilters,
          false
        ).concat(facetExcludeFilters);

        function filterValues(av) {
          return _.all(selectedFacetMatchFilters, function (pair) {
            if (pair.isExclude) {
              var facetValues = _.values(pair.filter);
              for (var i = 0; i < facetValues.length; i += 1) {
                if (av.hasFacetValue(pair.f, facetValues[i])) {
                  return false;
                }
              }
              return true;
            } else {
              return av.hasFacetValue(pair.f, pair.filter);
            }
          });
        }

        filteredValues = _.filter(filteredValues, filterValues);

        return filteredValues;
      },

      /**
       * Returns a collection of attribute values that should not be selectable
       * for this configurable attribute based on the inverted facets linked to
       * the configurable attribute.
       *
       * @instance
       * @returns {Array<AttributeValue>}
       */
      facetExcludedValues: function () {
        var invertedFacets = this.invertedHiddenFacets();
        if (invertedFacets.length === 0) {
          return [];
        }
        var self = this;

        var selectedFacetValues = _.compact(
          _.map(invertedFacets, function (f) {
            return self.selectedFacetValue(f);
          })
        );

        return _.reduce(
          selectedFacetValues,
          function (avs, fv) {
            return avs.concat(fv.attributeValues());
          },
          []
        );
      },

      /**
       * Sets the default {@link FacetValue}s for a {@link Recipe}.
       *
       * In order to keep a backwards compatible behavior with FC2, we need to
       * select the first {@link FacetValue} for each {@link Facet} in the
       * {@link Product} before selecting the default {@link AttributeValue}'s
       *
       * @instance
       */
      setDefaultFacetValues: function () {
        var facets = this.facets();
        var selectedFacetMatchFilters = this.selectedFacetMatchFilters();
        var self = this;
        _.each(facets, function (facet) {
          var fv = _.first(facet.selectableFacetValues(self));
          if (fv) {
            selectedFacetMatchFilters[facet.id] = fv;
          }
        });
      },

      /**
       * Sets the default {@link AttributeValue} for this
       * {@link ConfigurableAttribute}
       *
       * @instance
       */
      setDefaultValue: function () {
        // need to refresh the coercive sympathetic filters before we
        // can set a value
        this.updateSympatheticFilters();

        // We need to set the facet filters before selecting a default value in
        // order to keep things compatible with FC2 CD-1037
        this.setDefaultFacetValues();

        var av = this.defaultValue();

        if (av) {
          if (this.hasDynamicValue()) {
            // Dynamic value defaults need to be set
            // via a copy of the data instead of a reference to the av
            var data = av.getData();
            av = _.clone(data);
          }

          this.recipe().setOne(this, av);
        }
      },

      /**
       * Returns a list of attribute values that are available after sympathetic
       * filters are applied, but not regular facets.
       *
       * @instance
       * @param {Boolean} ignoreValueUsage - Skip valueUsage filters?
       * @returns {Array<AttributeValue>}
       */
      sympatheticFilteredValues: function (ignoreValueUsage) {
        // The ignoreValueUsage parameter is expressly for the purpose of
        // addressing CD-3710; allowing inactive AVs (only when overridden
        // by valueUsages) to still be displayed.
        var sympatheticFilters = this.sympatheticFilters(),
          activeValues = this.activeValues(ignoreValueUsage);

        if (Object.keys(sympatheticFilters).length > 0) {
          return _.filter(activeValues, function (av) {
            return _.all(sympatheticFilters, function (fv, facetId) {
              if (!fv) {
                return true;
              }
              return av.hasFacetValue(+facetId, fv);
            });
          });
        }

        return activeValues;
      },

      /**
       * Iterate over all the sympathetic match relations and perform
       * facet selections on the sympathetic match slaves
       *
       * @param {AttributeValue} av {@link AttributeValue} Instance
       * @instance
       */
      updateSympatheticMatchRelations: function (av) {
        var master = this;
        _.each(this.sympatheticMatches(), function (sm) {
          var f = sm.facet();
          var slaveCa = sm.slaveAttribute();
          if (!f || !av) {
            // if there's no AV, something is weird with the configuration --
            // we may have conflicting masters.  I encountered this with the
            // CD-1157 test.  The clip art Category matches on the Subcategory and
            // Subcategory Standard CAs.  These subcategories both match on the
            // Clipart Render attribute.  Luckily, the facet values for the two
            // subcategories are exclusive -- there is no overlap, so there will
            // end up being no selectable values for one of the subcategories.
            // If we don't continue on to update the Render CA, only one of the
            // subcategories will match with it, preventing a conflict.
            //
            // This is a bit inconsistent, because sympathetic filter and
            // sympathetic attributes behave differently -- if they would cause
            // a CA to have no values, or no match is found, they do nothing to
            // the target CA.  If I make it consistent -- i.e. do nothing to the
            // slave CA, M&Ms will break.
            return;
          }
          var avFvs = av.facetValuesForFacet(f);
          changeFacet(
            slaveCa,
            av,
            f,
            avFvs[0] || null,
            sm.isExclude(),
            master.id
          );
        });
      },

      /**
       * Updates the list of ca.sympatheticFilters() that are made available for
       * a configurable attribute given a sympathetic filter slave
       * relationship.
       * @instance
       * @returns {ConfigurableAttribute} Chainable ca
       */
      updateSympatheticFilters: function () {
        applySympatheticFilters(this);
        return this;
      },

      /**
       * Updates the non-sympathetic facets for this ca based on the av
       * that is currently selected.  Regular facets are non-coercive and can be
       * set based on which AV is selected.
       * This method only uses visible facets.
       * @instance
       * @returns {ConfigurableAttribute} Chainable ca
       */
      updateFacetsFromValue: function () {
        _.each(this.facets(), this.updateFacetFromValue, this);
        return this;
      },

      /**
       * Updates the facet filter for the currently selected attribute value.
       *
       * Only legal {@link Facet} + {@link FacetValue}s will be reflected back.
       *
       * @instance
       * @param {Facet} facet {@link Facet} instance
       */
      updateFacetFromValue: function (facet) {
        var av = this.selectedValue(),
          newFv,
          previousFv;

        // A miss-configured facet relationship might leave an av unselected.
        // Instead of throwing errors we should set back the default value.
        // This is what FC2 did
        if (!av) {
          console.error(
            "Could not update " +
              this +
              " " +
              facet +
              " as none of the " +
              this.selectableValues().length +
              " available attributes was selected."
          );
        }

        // Verify if the facet + facet value is legal for the av... if so,
        // we will reflect the change back on the ca. hasLegalFacet() checks if
        // the facet that was previously selected is  still valid for the current
        // av, and if it *is* legal, then there is no need to set a new fv.
        if (av && facet.id && !hasLegalFacet(av, facet, this)) {
          newFv = _.first(
            _.filter(av.facetValues(facet), function (facetValue) {
              return av.hasFacetValue(facet, facetValue);
            })
          );

          if (newFv) {
            previousFv = this.selectedFacetMatchFilters()[facet.id];
            this.selectedFacetMatchFilters()[facet.id] = newFv;

            if (!previousFv || previousFv.id !== newFv.id) {
              this.recipe().notifyFacet([
                {
                  ca: this,
                  f: facet,
                  fv: newFv,
                },
              ]);
            }
          }
        }
      },

      /**
       * Gets the facet value selected on this attribute given a facet instance.
       *
       * @instance
       * @param  {Facet} facet - Facet instance
       * @return {Object} Facet object such as: { id: 26162 }
       */
      selectedFacetValue: function (facet) {
        facet = facet instanceof BaseModel ? facet : this.findFacet(facet);
        var fvObject =
          (facet && this.sympatheticFilters()[facet.id]) ||
          this.selectedFacetMatchFilters()[facet.id];
        return fvObject && facet.findFacetValue(fvObject);
      },

      /**
       * Sets the filter for a facet on this configurable attribute, if the
       * attribute value is not included within the filter, the recipe will
       * be updated with the first av of the filter.  This is the one case
       * where regular facets coerce the selected AV.
       *
       * @instance
       * @param {Facet} facet Facet instance
       * @param {FacetValue} facetValue Facet Value instance
       * @returns {ConfigurableAttribute} Chainable ca
       */
      selectFacetValue: function (facet, facetValue) {
        var av = this.selectedValue(),
          recipe = this.recipe();

        facet = (facet.id && facet) || this.findFacet(facet);
        facetValue = facet.findFacetValue(facetValue);
        recipe.start();
        changeFacet(this, av, facet, facetValue, false, null);
        recipe.commit();
        return this;
      },

      /**
       * Gets sympathetic slaves (sfs vertices) for this attribute
       *
       * @instance
       * @returns {Array<SympatheticFilterSlave>}
       */
      sympatheticSlaves: function () {
        return this.getAndCache("sympatheticSlaves", function () {
          return this.traverser().incoming("isSlave").instances();
        });
      },

      /**
       * When a configurable attribute has a sympathetic slave relationship
       * the values that are available for the attribute are determined by the
       * facet value related to the sf vertex
       *
       * @instance
       * @returns {Boolean}
       */
      isSympatheticFilterSlave: function () {
        return this.getAndCache("isSympatheticFilterSlave", function () {
          return this.traverser().incomingSize("isSlave") > 0;
        });
      },

      /**
       * When a configurable attribute has a sympathetic match relationship
       * the values that are available for the attribute are determined by the
       * facet related to the sm vertex
       *
       * @instance
       * @returns {Boolean}
       */
      isSympatheticMatchSlave: function () {
        return this.getAndCache("isSympatheticMatchSlave", function () {
          return this.traverser().incomingSize("hasSlave") > 0;
        });
      },

      /**
       * Get attributes that could potentially change when this attribute changes
       * due to sympathetic filter relations.
       *
       * @instance
       * @returns {Array<ConfigurableAttribute>}
       */
      slaveAttributes: function () {
        return this.getAndCache("slaveAttributes", function () {
          return _.union(
            // Sympathetic filter slave controlled cas
            this.traverser()
              .outgoing("isSympatheticWith")
              .incoming("hasSympatheticRule")
              .where({ ns: "sfs" })
              .outgoing("isSlave")
              .where({ ns: "ca" })
              .instances(),

            // Sympathetic match slave controlled cas
            this.traverser()
              .outgoing("hasSympatheticMatch")
              .where({ ns: "sm" })
              .outgoing("hasSlave")
              .where({ ns: "ca" })
              .instances(),

            // Basic sympathetic cas without rules (av mirroring on other cas)
            this.traverser()
              .outgoing("isSympatheticWith")
              .where({ ns: "ca" })
              .instances()
          );
        });
      },

      /**
       * Get `ca`s that could cause the value of this `ca` to change due to
       * sympathetic filter relations as well as mirror `ca`s
       *
       * @instance
       * @see {@link Recipe#masterFilterAttributes}
       * @returns {Array<ConfigurableAttribute>}
       */
      masterAttributes: function () {
        return this.getAndCache("masterAttributes", function () {
          return _.union(
            // Is this a sympathetic filter slave controlled attribute?
            this.masterFilterAttributes(),

            // Basic sympathetic cas without rules (av mirroring on other cas)
            this.traverser()
              .incoming("isSympatheticWith")
              .where({ ns: "ca" })
              .instances()
          );
        });
      },

      /**
       * Get CAs that could cause the value of this `ca` to change due to
       * sympathetic filter relations. Only slaves with rules will be included
       *
       * @instance
       * @see {@link Recipe#masterAttributes}
       * @returns {Array<ConfigurableAttribute>}
       */
      masterFilterAttributes: function () {
        return this.getAndCache("masterFilterAttributes", function () {
          return this.traverser()
            .incoming("isSlave")
            .outgoing("hasSympatheticRule")
            .incoming("isSympatheticWith")
            .unique()
            .instances();
        });
      },

      /**
       * Gets the value defined on the master `ca` of a simple sympathetic
       * relationship. If a value is found, the `ca` will be considered `visible`
       *
       * @see {@link ConfigurableAttribute#isVisible}
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      getValueFromMasterAttribute: function () {
        var masterCa = this.getAndCache(
          "getValueFromMasterAttribute",
          function () {
            return this.traverser().incoming("isSympatheticWith").instance();
          }
        );

        if (masterCa) {
          return masterCa.selectedValue();
        }
      },

      /**
       * Gets the currently selected attribute value instance.
       *
       * @instance
       * @returns {AttributeValue}
       */
      selectedValue: function () {
        return this.recipe().getValue(this);
      },

      /**
       * Gets the currently selected attribute value instance or if one is not
       * selected (e.g. post reset), returns the default attribute value
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      getSelectedOrDefaultValue: function () {
        return this.selectedValue() || this.defaultValue();
      },

      /**
       * Gets all the  available {@link VisibilityRule} for this
       * {@link ConfigurableAttribute}.
       *
       * @instances
       * @returns {@link VisibilityRule}
       */
      visibilityRules: function () {
        return this.getAndCache("visibilityRule", function () {
          return this.traverser().outgoing("hasVisibilityRule").instances();
        });
      },

      /**
       * Is this configurable attribute visible and available to be used on a
       * {@link Recipe}?
       *
       * Keep in mind that master attribute relationships override visibility
       * rules, so if a `ca` needs to disappear from a final `Recipe`, it's
       * relationships should be removed as well.
       *
       * @see {@link VisibilityRule#isVisible}
       * @see {@link ConfigurableAttribute#getValueFromMasterAttribute}
       * @instance
       * @returns {Boolean}
       */
      isVisible: function () {
        var vr = this.visibilityRules(),
          visible = true;

        if (vr) {
          // VisibilityRule works as an "AND", so when we reach the first
          // "invalid" rule we have to stop iterating them
          _.each(vr, function (rule) {
            if (!rule.isVisible()) {
              visible = false;
              return false;
            }
          });
        }

        return visible;
      },

      /**
       * This methods returns true whenever the `selectorType` is not `none` on
       * the parent {@link ConfigurableAttribute} or any sub-attribute
       *
       * @return {Boolean}
       */
      canBeRendered: function () {
        function hasSelector(ca) {
          if (ca.hasSelector()) {
            return true;
          }

          if (ca.hasSubAttributes()) {
            return _.some(ca.subAttributes(), function (subCa) {
              return hasSelector(subCa);
            });
          }
        }

        return this.getAndCache("canBeRendered", function () {
          return hasSelector(this);
        });
      },

      /**
       * Some {@link ConfigurableAttribute}s are not selectable via the UI,
       * and they don't have a `selectorType` defined on their configuration.
       * This methods returns true whenever the `selectorType` is not `none`
       *
       * @return {Boolean}
       */
      hasSelector: function () {
        return this.getData().selectorType !== "none";
      },

      /**
       * If the {@link ConfigurableAttribute} is of type clipartEditor
       *
       * @return {Boolean}
       */
      isClipartEditor: function () {
        return this.getData().type === "clipartEditor";
      },

      /**
       * If the {@link ConfigurableAttribute} does not link to other
       * {@link AttributeValue}s an its selector type prevents it from
       * using `av`s, the attribute will be considered dynamic.
       *
       * Dynamic attributes will hold a reference to the value they've been
       * assigned.
       *
       * @return {Boolean}
       */
      hasDynamicValue: function () {
        return !!this.getData().isDynamic;
      },

      /**
       * Gets a {@link DynamicAttributeValue} instance that can be used as a proxy
       * for storing non static `av` that are represented on the config graph.
       *
       * @return {DynamicAttributeValue} {@link DynamicAttributeValue} singleton.
       * @see {@link ConfigurableAttribute#unsetDynamicCache}
       */
      getDynamicAttributeValue: function () {
        if (!(this._dynamicAttributeValue instanceof DynamicAttributeValue)) {
          this._dynamicAttributeValue = new DynamicAttributeValue(this);
        }
        return this._dynamicAttributeValue;
      },

      /**
       * Removes a previously {@link DynamicAttributeValue} instance.
       *
       * @see {@link ConfigurableProduct#unsetDynamicCaDefaults}
       */
      unsetDynamicCache: function () {
        delete this._dynamicAttributeValue;
      },

      /**
       * For dynamic CA's, return the attribute that has the font configuration.
       * @return {ConfigurableAttribute} {@linkConfigurableAttribute}
       */
      fontAttribute: function () {
        var self = this;
        return this.getAndCache("fontAttr", function () {
          var textRelations = this.getData().textRelations,
            textRelCAIds = (textRelations && textRelations.attributes) || [],
            textRelCAs = _.map(textRelCAIds, function (caId) {
              return self
                .product()
                .attributeById(caId.match(RE_LAST_NUMBERS)[1]);
            });

          return _.find(textRelCAs, function (ca) {
            var value = ca.activeValues()[0];

            return value && value.isFont && value.isFont();
          });
        });
      },

      /**
       * Get the associated {@link View} name for the configurable attribute.
       *
       * If there's no explicit {@link View} defined on the configurable
       * attribute, we will look for a view name on the attribute group.
       *
       * If no {@link View} is defined on the ca or the ag we will use the default
       * product view name which is normally the first view defined on the
       * view list.
       *
       * @instance
       * @return {String} The ID of the view, or null if it doesn't exist
       */
      viewName: function () {
        var view = this.traverser().outgoing("displaysView").instance(),
          ag;

        // When the view is not defined on the product, we will
        // use the view defined at the attribute group level (if any)
        if (!view) {
          ag = this.attributeGroup();
          if (ag) {
            view = ag.view();
          }
        }

        if (view) {
          return view.getData().name;
        }

        // After 1.13.0 we won't set a default viewName for ca's
        // on the engine DEV-5643
      },

      /**
       * When this ca is updated we need to verify that other ca's that use
       * this ca for their visibility rules, have a valid value if they've been
       * just made visible.
       * @return {ConfigurableAttribute} Chainable {@link ConfigurableAttribute}
       */
      updateVisibilityRuleWatchers: function () {
        var watchers,
          recipe = this.recipe();

        // Visibility rule watchers will not be set until the default
        // recipe has been completely defined.
        if (!recipe.hasDefaultSet()) {
          return this;
        }

        watchers = this.visibilityRuleWatchers();

        _.each(watchers, function (ca) {
          if (ca.isVisible() && !ca.selectedValue()) {
            var av = ca.defaultValue();
            if (av) {
              recipe.setOne(ca, av);
            }
          }
        });

        return this;
      },

      /**
       * Gets a list of {@link ConfigurableAttribute}s that change their
       * visibility based on which facet or value is selected on this ca.
       * @return {Array<ConfigurableAttribute>} List of cas
       */
      visibilityRuleWatchers: function () {
        return this.getAndCache("visibilityRuleWatchers", function () {
          return [].concat(
            this.traverser()
              .incoming("isVisibleWhenSameAttribute")
              .incoming("hasVisibilityRule")
              .where({ ns: "ca" })
              .instances(),
            this.traverser()
              .incoming("isHiddenWhenSameAttribute")
              .incoming("hasVisibilityRule")
              .where({ ns: "ca" })
              .instances()
          );
        });
      },

      /**
       * Returns the filters that are currently enabled on this configurable.
       * attribute.
       *
       * @private
       * @return {Object}
       */
      recipeFilters: function () {
        return this.recipe().context().recipeFilters[this.id];
      },

      /**
       * Returns selected facet match filters on the recipe.
       *
       * @private
       * @return {Object}
       */
      selectedFacetMatchFilters: function () {
        return this.recipeFilters().selectedFacetMatchFilters;
      },

      /**
       * Returns selected facet exclude filters on the recipe.
       *
       * @private
       * @return {Object}
       */
      selectedFacetExcludeFilters: function () {
        return this.recipeFilters().selectedFacetExcludeFilters;
      },

      /**
       * Returns sympathetic filters selected on the recipe.
       *
       * @private
       * @return {Object}
       */
      sympatheticFilters: function () {
        return this.recipeFilters().sympatheticFilters;
      },

      /**
       * Check if the {@link ConfigurableAttribute} has sympathetic filters
       *
       * @return {Boolean}
       */
      hasSympatheticFilters: function () {
        return !_.isEmpty(this.sympatheticFilters());
      },

      /**
       * Adds an extra `info` property to the output of {@link Traverser#explain}
       * in order to help debugging this `ca`
       * @instance
       * @return {Object} Same as {@link Traverser#explain} but with an extra
       *                  info property that includes information about:
       * - viewName
       * - isVisible
       * - masterAttributes
       * - slaveAttributes
       * - isSympatheticFilterSlave
       * - isSubAttribute
       * - hasSubAttributes
       */
      explain: function () {
        var baseExplanation = BaseModel.prototype.explain.apply(this),
          info = {},
          infoMethods = [
            "viewName",
            "isVisible",
            "masterAttributes",
            "slaveAttributes",
            "isSympatheticFilterSlave",
            "isSubAttribute",
            "hasSubAttributes",
            "getValueFromMasterAttribute",
            "masterFilterAttributes",
            "fontAttribute",
            "isSympatheticMatchSlave",
            "masterSympatheticMatchAttributes",
            "masterSympatheticMatches",
          ];

        try {
          info.selectedValue = this.selectedValue();
          info.valueUsageForValue = this.valueUsageForValue(info.selectedValue);
        } catch (e) {
          info.selectedValue = {
            error: e.message,
          };
        }

        _.each(
          infoMethods,
          function (type) {
            var result;
            try {
              result = this[type]();
              if (_.isArray(result)) {
                result = result.join(", ");
              }
            } catch (e) {
              result = e.message;
            }
            info[type] = result;
          },
          this
        );

        baseExplanation.info = info;
        return baseExplanation;
      },

      /**
       * Gets a collection of WebGl Customer Material instances.
       *
       * @instance
       * @return {Array<WebGlCustomerMaterial>}
       */
      customerMaterials: function () {
        return this.getAndCache("customerMaterials", function () {
          return this.traverser().outgoing("hasMaterial").instances();
        });
      },

      /**
       * Gets a collection of WebGl Configurable Attribute ModelComponents.
       *
       * @instance
       * @return {Array<ConfigurableAttributeModelComponent>}
       */
      modelComponents: function () {
        return this.getAndCache("modelComponents", function () {
          return this.traverser().outgoing("hasCaModelComponent").instances();
        });
      },

      /**
       * Gets a Map of all AV that has model components related to this CA.
       *
       * @instance
       * @return {Object}
       */
      avModelComponents: function () {
        return this.getAndCache("avModelComponents", function () {
          var _this = this;

          return this.allValues().reduce(function (acc, av) {
            var modelComponents = _this.modelComponentsForAv(av.id);
            if (modelComponents && modelComponents.length) {
              acc[av.id] = modelComponents;
            }
            return acc;
          }, {});
        });
      },

      /**
       * Gets a collection of WebGl Configurable Attribute ModelComponents for particular AV id.
       *
       * @instance
       * @param  {number} attributeValueId Attribute id
       * @return {Array<ConfigurableAttributeModelComponent>}
       */
      modelComponentsForAv: function (attributeValueId) {
        return this.getAndCache(
          "modelComponentsForAv" + attributeValueId,
          function () {
            return this.traverser()
              .outgoing("hasCaModelComponent")
              .instances()
              .filter(function (component) {
                return component.hasAttributeValue(attributeValueId);
              });
          }
        );
      },

      /**
       * Does this CA have customer materials?
       *
       * @instance
       * @return {Boolean}
       */
      hasCustomerMaterials: function () {
        return this.traverser().outgoingSize("hasMaterial") > 0;
      },

      /**
       * Gets a collection of Configurable Attribute instances dynamic material related.
       *
       * @instance
       * @return {Array<ConfigurableAttribute>}
       */
      dynamicMaterialRelatedCAs: function () {
        return this.getAndCache("dynamicMaterialRelatedCAs", function () {
          return this.traverser()
            .outgoing("hasDynamicMaterialRelatedCAs")
            .instances();
        });
      },

      /**
       * Does this CA have dynamic material related CAs?
       *
       * @instance
       * @return {Boolean}
       */
      hasDynamicMaterialRelatedCAs: function () {
        return (
          this.traverser().outgoingSize("hasDynamicMaterialRelatedCAs") > 0
        );
      },

      /**
       * Gets the font 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      fontTextAttribute: function () {
        return this.getAndCache("fontAttribute", function () {
          return this.traverser().outgoing("hasFontAttribute").instance();
        });
      },

      /**
       * Gets the font size 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      fontSizeTextAttribute: function () {
        return this.getAndCache("fontSizeAttribute", function () {
          return this.traverser().outgoing("hasFontSizeAttribute").instance();
        });
      },

      /**
       * Gets the color 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      colorTextAttribute: function () {
        return this.getAndCache("colorAttribute", function () {
          return this.traverser().outgoing("hasColorAttribute").instance();
        });
      },

      /**
       * Gets the styling 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      stylingTextAttribute: function () {
        return this.getAndCache("stylingAttribute", function () {
          return this.traverser().outgoing("hasStylingAttribute").instance();
        });
      },

      /**
       * Gets the textAlignment 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      textAlignmentTextAttribute: function () {
        return this.getAndCache("textAlignmentAttribute", function () {
          return this.traverser()
            .outgoing("hasTextAlignmentAttribute")
            .instance();
        });
      },

      /**
       * Gets the baseline 3D configurable attribute for CA 3D text.
       *
       * @instance
       * @returns {ConfigurableAttribute}
       */
      baselineTextAttribute: function () {
        return this.getAndCache("baselineAttribute", function () {
          return this.traverser().outgoing("hasBaselineAttribute").instance();
        });
      },

      /**
       * Gets the web font for CA 3D text.
       *
       * @instance
       * @returns {AttributeValue}
       */
      fontTextAttributeValue: function () {
        return this.getAndCache("fontAttributeValue", function () {
          return this.traverser().outgoing("hasFontAttributeValue").instance();
        });
      },
    },
    constructor
  );
};

// Private methods

/**
 * Given a facet and a facet value this function will broadcast the selection
 * on the recipe event bus
 * @param  {@link ConfigurableAttribute} ca
 * @param  {@link Facet} facet
 * @param  {@link FacetValue} facetValue
 * @private
 */
function broadcastFacetChange(ca, facet, facetValue) {
  var recipe = ca.recipe();
  recipe.notifyFacet([
    {
      ca: ca,
      f: facet,
      fv: facetValue,
    },
  ]);
}

/**
 * Should an optional facet be reset to show all the options and
 * remove a filter?
 *
 * Optional facets will disable the facet filter when
 * a facet not linked to attribute values is selected.
 * For example an "All" option that disables all
 * other facet filters.
 *
 * @param  {ConfigurableAttribute} ca
 * @param  {Facet} facet
 * @param  {FacetValue} facetValue
 * @return {Boolean}
 * @private
 */
function shouldDisplayDefaultValuesForFacet(ca, facet, facetValue) {
  if (facetValue) {
    // we do not want to clear filter if it was just set in the current iteration
    return false;
  }
  return (
    (facet.isOptional(ca) || facet.isHidden(ca)) &&
    ca.selectableValues().length === 0
  );
}

/**
 * When a facet changes and no value is selected or the value is no longer
 * valid according to the filter rules, select a new value.
 *
 * @param  {ConfigurableAttribute} ca
 * @param  {AttributeValue} av
 * @param  {Facet} facet
 * @param  {FacetValue} facetValue
 * @param  {Boolean} isExclude describe the operation of the facet value for this ca
 * @private
 */
// eslint-disable-next-line max-params
function selectValuesForFacetChange(ca, av, facet, facetValue, isExclude) {
  var shouldChangeValue;
  if (isExclude) {
    const slaveAv = ca.selectedValue();
    const hasFacetValue = slaveAv && slaveAv.hasFacetValue(facet, facetValue);
    shouldChangeValue = !slaveAv || hasFacetValue;
  } else {
    const hasFacetValue = av && av.hasFacetValue(facet, facetValue);
    shouldChangeValue = !av || !hasFacetValue;
  }
  if (shouldChangeValue) {
    selectFirstValue(ca);
    if (shouldDisplayDefaultValuesForFacet(ca, facet, facetValue)) {
      disableFacetFilter(ca, facet, isExclude);
    }
  }
}

/**
 * Disables a facet filter on an attribute.
 * This happens when making filters optional.
 *
 * @param  {ConfigurableAttribute} ca
 * @param  {Facet} facet
 * @private
 */
function disableFacetFilter(ca, facet, isExclude) {
  if (isExclude) {
    ca.selectedFacetExcludeFilters()[facet.id] = null;
  } else {
    ca.selectedFacetMatchFilters()[facet.id] = null;
  }
}

/**
 * Selects the first valid value for a configurable attribute.
 *
 * @param  {ConfigurableAttribute} ca
 * @private
 */
function selectFirstValue(ca) {
  var av = _.first(ca.selectableValues());
  if (av) {
    ca.recipe().setOne(ca, av);
  }
}

/**
 * Changes the facet and logs a facet change event
 */
// eslint-disable-next-line max-params,complexity
function changeFacet(ca, av, facet, facetValue, isExclude, parentCaId) {
  var selectedFacetMatchFilters, previousFv;

  if (isExclude) {
    selectedFacetMatchFilters = ca.selectedFacetExcludeFilters();
    previousFv = (selectedFacetMatchFilters[facet.id] || {})[parentCaId];

    if (!selectedFacetMatchFilters[facet.id]) {
      selectedFacetMatchFilters[facet.id] = {};
    }
    selectedFacetMatchFilters[facet.id][parentCaId] = facetValue;
  } else {
    selectedFacetMatchFilters = ca.selectedFacetMatchFilters();
    previousFv = selectedFacetMatchFilters[facet.id];
    selectedFacetMatchFilters[facet.id] = facetValue;
  }

  if (!previousFv || !facetValue || previousFv.id !== facetValue.id) {
    broadcastFacetChange(ca, facet, facetValue);
  }

  selectValuesForFacetChange(ca, av, facet, facetValue, isExclude);
}

/**
 * Returns true if the facet that was already selected is still legal for the
 * av.
 *
 * @param  {AttributeValue} av
 * @param  {Facet} facet
 * @param  {ConfigurableAttribute} ca
 * @private
 * @return {Boolean}
 */
function hasLegalFacet(av, facet, ca) {
  var selectedFacetMatchFilters = ca.selectedFacetMatchFilters();
  return (
    selectedFacetMatchFilters[facet.id] &&
    av.hasFacetValue({ id: facet.id }, selectedFacetMatchFilters[facet.id])
  );
}

function convertFacetFiltersToPairs(ca, facetFilters, isExclude) {
  return _.reduce(
    facetFilters,
    function (result, filter, facetId) {
      if (!filter) {
        return result;
      }
      var facet = ca.findFacet(facetId);
      if (!facet.isInverted(ca)) {
        result.push({ f: facet, filter: filter, isExclude: isExclude });
      }
      return result;
    },
    []
  );
}
