module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * {SympatheticFilterMaster}s owns {SympatheticFilter} relationships.
   *
   * On the graph their vertices contain the namespace (ns) "sfm"
   *
   * @param {Object} options Options
   * @param {number} options.id SympatheticFilterMaster numeric id.
   * @class
   * @name SympatheticFilterMaster
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends SympatheticFilterMaster */{
    ns: "sfm",


    /**
     * Gets the facet value for the {@link SympatheticFilterMaster}
     * @instance
     * @return {FacetValue}
     */
    facetValue: function () {
      return this.traverser()
        .outgoing("isMaster")
        .where({ns: "fv"})
        .instance();
    }
  });
};
