var request = require("superagent");
var debug = require("debug")("fluid:configuration-loader:http_client");
var config = require("./config");

// Superagent plugins
if (!process.browser) {
  require("superagent-retry")(request);
}

if (process.env.SUPERAGENT_MOCK) {
  var mockConfig = global.superAgentMockConfig || require(process.env.SUPERAGENT_MOCK);
  require("superagent-mock")(request, mockConfig);
}

/**
 * Superagent will throw exceptions on invalid
 * CORS requests so we need to wrap it properly.
 *
 * @param  {string}   url
 * @param  {Function} callback
 */
function getJson(url, callback) {

  function onresponse(err, res) {
    if (!err && res && res.status && res.status >= 400) {
      err = new Error("Failed loading " + url + ", with status " + (res && res.status || "unknown"));
    }
    callback(err, res && res.body || {});
  }

  debug("[info] GET %s", url);

  try {
    if (process.browser) {
      request.get(url).end(onresponse);
    } else {
      request.get(url).retry(config.retryTimes).end(onresponse);
    }
  } catch (e) {
    callback(e);
  }
}

module.exports = getJson;

