var   overrideProductGraph = require("./product_overrides");
var addBaseUrls = require("./base_urls");
var _ = require("lodash");

module.exports = function modifyGraph(fcParams, productGraph) {
  addBaseUrls(fcParams, productGraph);
  if (fcParams.productOverrides && _.size(fcParams.productOverrides)) {
    overrideProductGraph(productGraph, fcParams.productOverrides);
  }
};