var
  _ = require("lodash");

/**
 * Explain the recipe price
 *
 * @param  {Recipe} recipe
 * @param  {Product} product
 * @param  {String} currency
 * @param  {Function} getUpchargeFor
 * @return {Array} return array containing attribute name, attribute value
 * name, upcharge and from where the upcharge was taken
 */
function explainRecipePrice(recipe, product, currency, getUpchargeFor) {

  var
    explanation = [],
    ca,
    av,
    avName;

  _.each(recipe.getData(), function (entry) {

    ca = entry.ca;
    av = entry.av;

    if (ca.hasDynamicValue()) {
      avName = JSON.stringify(av.getData());
    } else {
      avName = av.getData().vertexKey + " - " + av.getData().name;
    }

    explanation.push({
      "Attribute Name": ca.getData().vertexKey + " - " + ca.getData().name,
      "Value Name": avName,
      "Upcharge": getUpchargeFor(ca, av, currency),
      "Upcharge From": ca.valueUsageForValue(av) ? "Value Usage" :
        "Attribute Value"
    });
  });

  explanation.push({
    "Attribute Name": "---",
    "Value Name": "---",
    "Upcharge": "---",
    "Upcharge From": "---"
  }, {
    "Attribute Name": "Product",
    "Value Name": "---",
    "Upcharge": product.price(currency),
    "Upcharge From": "---"
  }, {
    "Attribute Name": "---",
    "Value Name": "---",
    "Upcharge": "---",
    "Upcharge From": "---"
  }, {
    "Attribute Name": "Total",
    "Value Name": "---",
    "Upcharge": recipe.price(currency),
    "Upcharge From": "---"
  });

  return explanation;
}

module.exports = explainRecipePrice;
