var _ = require("lodash");
var configurationLoader = require("@cfg.plat/configuration-loader");
var getProductGraph = configurationLoader.productGraph;
var getApi = require("./api/");
var clone = require("./api/clone_changes");
var modifyGraph = require("./graph_modifications/");
var createEngine = require("@cfg.plat/configuration-engine");
var getLoadRecipe = require("./api/load_recipe");
var setEnvironmentParams = require("./set_environment_params");
var pageViewEvent = require("./metrics/page_view_event");

var util = require("./util");
var shouldTrackPageView =
  util.shouldTrackMetricsEvents((global || {}).location);

function getApiInstance(fcParams, productGraph, callback) {
  // Explicit graphs will be cloned to prevent side effects
  var graphClone = JSON.parse(JSON.stringify(productGraph))
  modifyGraph(fcParams, graphClone);

  var engine = createEngine(graphClone);
  fcParams = _.omit(fcParams, "productGraph");

  if (fcParams.recipeId) {
    // CD-2959: removing skipSettingAsDefault to ensure a default is set when
    // loading recipe by id
    fcParams = _.omit(fcParams, "skipSettingAsDefault");

    var loadRecipe = getLoadRecipe(engine, fcParams);
    loadRecipe(fcParams.recipeId, fcParams, function (err, recipeDoc) {
      var api = getApi(fcParams, engine);
      callback(err, api);

      if (!err) {
        // recipe:loaded happens before exposing the event bus so defer to
        // next tick so implementors can listen to this event
        var data = clone(engine.recipe().getData());
        _.defer(function () {
          api.trigger("recipe:loaded", data, recipeDoc);
        });
      }
    });
  } else {
    callback(null, getApi(fcParams, engine));
  }

  // send event to metrics service
  if (process.browser && shouldTrackPageView) {
    pageViewEvent(fcParams);
  }
}

module.exports = function loadAndExpandConfig(
  fcParams, callback
  ) {
  fcParams = setEnvironmentParams(fcParams);

  if (!_.isFunction(callback)) {
    throw new Error("configure-core expects a callback");
  }

  if (fcParams && fcParams.productGraph) {
    return getApiInstance(fcParams, fcParams.productGraph, callback);
  }

  getProductGraph(fcParams, function (err, productGraph) {
    if (err) {
      return callback(err);
    }
    getApiInstance(fcParams, productGraph, callback);
  });
};
