module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * SympatheticFilter
   *
   * `SympatheticFilter`s automatically selects the same filters on
   * related {ConfigurableAttribute}s when a {Facet} selection is
   * applied to a {ConfigurableAttribute}.
   *
   * For example, If the customer selects the "Waterproof fabrics" {Facet},
   * then only allow other relevant {ConfigurableAttribute}s to have
   * "Waterproof fabrics".
   *
   * On the graph their vertices contain the namespace (ns) "sf"
   *
   * @class
   * @constructor
   * @augments BaseModel
   * @name SympatheticFilter
   * @param {Object} options Options
   * @param {number} options.id SympatheticFilter numeric id.
    */
  return BaseModel.extend(/** @lends SympatheticFilter */{
    ns: "sf",

    /**
     * Gets the master {@link ConfigurableAttribute}s for this
     * {@link SympatheticFilter}
     * @instance
     * @return {ConfigurableAttribute}
     */
    masterAttribute: function () {
      var self = this;
      return this.getAndCache("masterCA", function () {
        return self.traverser()
          .incoming("isSympatheticWith")
          .instance();
      });
    },

    /**
     * Gets all the  {@link SympatheticFilterMaster}s for this
     * {@link SympatheticFilter}
     * @instance
     * @return {Array<SympatheticFilterMaster>}
     */
    masters: function () {
      var self = this;
      return this.getAndCache("masterFVs", function () {
        return self.traverser()
          .incoming("hasSympatheticRule")
          .where({ns: "sfm"})
          .instances();
      });
    },


    /**
     * Gets the relationship type for this {@link SympatheticFilter}
     * @instance
     * @return {String} Type of relationship. Either "or" or "and"
     */
    relationshipType: function () {
      return this.getData().relationship || "or";
    }
  });
};
