var MB_SPLIT_RE = /([\uD800-\uDBFF][\uDC00-\uDFFF]|.|[\000-\031])/;

function isOdd(n, i) {
  return i % 2 !== 0;
}

// this could be done with ArrayFrom but like IE polyfills does not support
// multibyte it's not reliable for us
exports.multibyteSplit = function (s) {
  return s.split(MB_SPLIT_RE).filter(isOdd);
}
