/*
 * The Model map defines what vertices will be instantiated as
 * lightweight Models.
 */

var _ = require("lodash");

module.exports = function (options) {
  var modelMap = {
      base: require("./base")(options),
      av: require("./attribute_value")(options),
      ca: require("./configurable_attribute")(options),
      f: require("./facet")(options),
      fv: require("./facet_value")(options),
      ag: require("./attribute_group")(options),
      view: require("./view")(options),
      vu: require("./value_usage")(options),
      vr: require("./visibility_rule")(options),
      sf: require("./sympathetic_filter")(options),
      sfs: require("./sympathetic_filter_slave")(options),
      sfm: require("./sympathetic_filter_master")(options),
      sm: require("./sympathetic_match")(options),
      md: require("./metadata")(options),
      p: require("./configurable_product")(options),
      pp: require("./purpose")(options),
      ppV: require("./purposeView")(options),
      pf: require("./product-facet")(options),
      /* WebGl related */
      wm: require("./webgl_model")(options),
      wmc: require("./webgl_model_component")(options),
      camc: require("./configurable_attribute_model_component")(options),
      wma: require("./webgl_customer_material")(options),
      wpma: require("./webgl_platform_material")(options),
      wmlod: require("./webgl_model_lod")(options),
      wload: require("./webgl_loading_strategy")(options),
      lod: require("./webgl_lod_resolution")(options),
      wplod: require("./webgl_product_lod_resolution")(options),
      wenv: require("./webgl_environment")(options),
      wcam: require("./webgl_environment_camera")(options),
      whdr: require("./webgl_hdr_background")(options),
    },
    explicitNs = _.keys(modelMap),
    availableNs =
      (options.graph && _.keys(options.graph().vertexNsIndex)) || [],
    implicitNs = _.difference(availableNs, explicitNs);

  // Models that do not include an explicit model map will
  // generate a base one on the fly
  _.each(implicitNs, function (ns) {
    modelMap[ns] = modelMap.base.extend({ ns: ns });
  });

  return modelMap;
};
