/**
 * @publicDocs methods/getItemNumber ../../docs/get_item_number.md
 *
 * This code is based in the way FC2 managed item numbers
 * https://gist.github.com/bermi/3bd8f6cb4b67afd8294c
 *
 * Test with: mocha -g "api.getItemNumber"
 */

var
  _ = require("lodash"),
  CA_RE = /([^_]+)__f([^_]+)$/;

module.exports = function (engine) {

  return function getItemNumber(type) {
    type = type || "product";

    var
      product = engine.product(),
      recipe = engine.recipe();

    // Multiple item number entries can be defined in the config
    // by default "product" will be used. We don't think others are
    // needed and this might be simplified once we re-factor the admin
    var itemNumbersConfig = _.get(product.getData(), "itemNumbers." + type, {});

    // Each element on itemNumbersConfig.order represents a step
    // in the sequence that will be used to compute the next value
    var sequence = _.get(itemNumbersConfig, "order", []),
      itemNumberValues = itemNumbersConfig.values;

    if (!itemNumberValues || sequence.length === 0) {
      return "";
    }
    return _.reduce(sequence, function (itemNumberValues, itemValueId) {
      itemNumberValues = (itemNumberValues || {})[itemValueId];
      var ca, av, fv;

      if (itemNumberValues) {
        ca = product.attributeById(itemValueId.match(CA_RE)[1]);
        av = recipe.getValue(ca);
        fv = getFvOnItemNumberValues(itemNumberValues, av);

        return fv && itemNumberValues["fv" + fv.id];
      }
    }, itemNumberValues);
  };
};

// Get the first element that matches the facet values
// for the av
function getFvOnItemNumberValues(itemNumberValues, av) {
  return _.first(_.filter(av.facetValues(), function (fv) {
    return itemNumberValues["fv" + fv.id];
  }));
}
