var _ = require("lodash");
var RE_LAST_NUMBERS = require("./common").RE_LAST_NUMBERS;
var getSortedVerboseAttributes = require("./get_sorted_verbose_attributes.js");


/**
 * This method is used for loading FC2 recipes as
 * FC3 recipes. This should be deprecated in favor of
 * a simpler and less verbose format returned by the servers.
 *
 * converts concise recipe
 * to
 * [ [1234, 2345], [1236, 2346], ... ]
 */
module.exports =
function convertConciseRecipe(conciseRecipe, productInstance) {
  if (
      !conciseRecipe
      || !conciseRecipe.length
      || !(typeof conciseRecipe === "string")
  ) {
    throw new Error("recipe.setFromConcise concise recipe is not valid");
  }
  var conciseArray = conciseRecipe.split(",");
  var attributes = getSortedVerboseAttributes(productInstance);
  var exceptions = []

  var recipeSelection = _.reduce(attributes, function (acc, legacyCaId, key) {
    var caId = +legacyCaId.match(RE_LAST_NUMBERS)[1];
    var ca = productInstance.attributeById(caId);
    var conciseValue = conciseArray[key];

    // The CA is not included on the verboseRecipe, it might be not
    // visible
    if (conciseValue === "-1") {
      return acc;
    }
    // Shadow attributes are a legacy from FC2.
    // They are represented on concise recipes and taken into
    // account by imagecomposer CD-1728
    if (conciseValue === "0") {
      var defaultValue= ca
        .selectableValues()
        .sort(function (ca1, ca2) {
          return ca1.id - ca2.id
        })[0];
      acc.push([caId, defaultValue])
    } else if (ca.hasDynamicValue()) {
      try {
        var dynamicValue = JSON.parse(conciseValue);
        acc.push([caId, dynamicValue])
      } catch (e) {
        var error = new Error("Failed converting concise recipe " +
          conciseRecipe + " ca " + ca +
          " with av " + conciseValue + ". Cannot parse dynamic value");
        exceptions.push(error);
      }
    } else {
      var avId = _.pluck(ca.legacyUnsortedValues(), "id")[conciseValue]
      if (!avId) {
        var error = new Error("Failed converting concise recipe " +
            conciseRecipe + " ca " + ca +
            " with av " + conciseValue + ". Av not found");
        exceptions.push(error);
      } else {
        acc.push([caId, +avId])
      }
    }


    return acc;
  }, []);
  return {
    changes: recipeSelection,
    exceptions: exceptions,
  }
}

