module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * `Purpose`s define what image should be shown for
   * a articular action, f.e. addToCart, share
   *
   * On the graph their vertices contain the namespace (ns) "pp"
   *
   * {@link PurposeView}
   *
   * @param {Object} options Options
   * @class
   * @name Purpose
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends Purpose */{
    ns: "pp"
  });
};
