/* eslint complexity:0 */
/**
 * @publicDocs methods/loadRecipe ../../docs/load_recipe.md
 */

var
  request = require("superagent"),
  fetchPreferences = require("../preferences"),
  _ = require("lodash"),
  async = require("async"),
  applyParameterizedFacetFilters =
    require("./apply_parameterized_facet_filters");

var V3_RECIPE_ID_RE = /[a-f0-9]+-[a-f0-9]+-[a-f0-9]+/;

module.exports = function loadRecipe(engine, fcParams) {

  var skipCdn = fcParams.skipCdn === true;
  var loadRecipeEndpoint;
  if (fcParams.loadRecipeEndpoint) {
    loadRecipeEndpoint = fcParams.loadRecipeEndpoint;
  } else {
    var endpointRoot = fcParams.configureEndpoint.replace(/^cdn-/, "");
    loadRecipeEndpoint = "https://" + (skipCdn ? "" : "cdn-") + endpointRoot
  }

  function buildRecipeUrl(id, callback) {
    var getPrefs = fetchPreferences;

    if (fcParams.preferences) {
      getPrefs = function (_, callback) {
        return callback(null, fcParams.preferences);
      };
    }

    getPrefs(fcParams, function (err, preferences) {
      if (err) { return callback(err); }
      var isV3 = V3_RECIPE_ID_RE.test(id);
      var apiKey = fcParams.apiKey || preferences.apiKey;
      var url;

      if (isV3) {
        // URL for 2018 recipes
        url = loadRecipeEndpoint + "/v3/api/" +
          fcParams.workflow + "/customers/" + fcParams.customerId +
          "/recipes/" + id + "?apiKey=" + apiKey;
      // Recipe v1
      } else {
        // URL's for non DynamoDB backwards compatible recipes
        url = loadRecipeEndpoint + "/api/recipe/" + id +
          "?apiKey=" + apiKey;
      }

      callback(null, url);
    });
  }

  function requestRecipe(id, recipeLoader, callback) {

    buildRecipeUrl(id, function (err, url) {
      if (err) { return callback(err); }
      try {
        recipeLoader(url, function (err, recipeDoc) {
          if (err) { return callback(err); }
          callback(null, recipeDoc);
        });
      } catch (e) {
        return callback(e);
      }
    });
  }


  return function loadRecipe(id, options, callback) {

    if (!callback) {
      callback = options;
      options = {};
    }

    if (!callback || (typeof callback !== "function")) {
      throw new Error("loadRecipe expects a callback");
    }

    if (!isValidRecipeId(id)) {
      return callback(new Error("load_recipe: Invalid recipeId provided"));
    }

    var recipeLoader = getPerformLoadFunction(fcParams, options);

    requestRecipe(id, recipeLoader, function (err, recipeDoc) {
      if (err) {
        handleRecipeLoadError({err: err, id: id}, callback);
        return;
      }

      try {
        var recipe = engine.recipe(false); // prevent setting the default recipe
        recipe.start();

        var legacyRecipe = getLegacyRecipe(recipeDoc);
        if (legacyRecipe) {
          recipe.legacySet("verbose", legacyRecipe);
        } else {
          // V3 recipes already come in a configuration-engine
          // digestible format
          recipe.set(recipeDoc);
        }

        // Parameterized facets contain facets that are pre-selected
        // based on the contents of fcParams.facetFilter
        // and a URL parameter. Any event triggered by these changes
        // is irrelevant and should not be broadcasted. The selection
        // needs to be part of the initial product JSON as we won't
        // be able to update them as regular facets
        // after the product has been loaded.
        applyParameterizedFacetFilters(fcParams, engine);

        var invalidRecipe = recipe.validate();
        if (invalidRecipe) {
          handleInvalidRecipe({
            recipe: recipe,
            invalidRecipe: invalidRecipe,
            recipeDoc: recipeDoc
          }, callback);
          return;
        }

        //CD-2410 skipSettingAsDefault flag added to prevent recipe from rolling
        //back to a snapshot on reset instead of the recipe default
        if (!options.skipSettingAsDefault) {
          recipe.saveAsDefault();
        }

        recipe.commit();

        recipe.trigger("load", recipe.getData(), recipeDoc);
        callback(null, recipeDoc);

      } catch (e) {
        err = new Error("Failed loading recipe " + id +
          " (" + JSON.stringify(recipeDoc.configuration, null, 2) + ")." +
          " Got error: " + e.message + ". Please check your recipe loaded " +
          "event listeners");
        if (process.env.NODE_ENV !== "production") {
          console.log(e);
        }
        recipe.rollback();
        return callback(err);
      }
    });
  };
};

function handleRecipeLoadError(options, callback) {
  var err = options.err;
  var id = options.id;
  if (err.status && err.status !== 200) {
    var notFoundError = new Error(
      "Recipe " + id + " not found. Got status code: " + err.status
    );
    notFoundError.fcErrorType = "recipe:notFound";
    notFoundError.fcErrorDetails = err.message;
    notFoundError.status = err.status;
    return callback(notFoundError);
  }
  return callback(err);
}

function handleInvalidRecipe(options, callback) {
  var recipeDoc = options.recipeDoc;
  var recipe = options.recipe;
  var invalidRecipe = options.invalidRecipe;

  var error = new Error("Invalid recipe. Please check the docs on " +
    "https://prod.fluidconfigure.com/static/code/configure-ui/" +
    "stable/docs/index.html" +
    "#methods-loadrecipe-handling-invalid-recipes");
  error.fcErrorType = "recipe:invalid";
  error.fcErrorDetails = JSON.parse(JSON.stringify(invalidRecipe));
  error.fcErrorDetails.fallbackRecipe = invalidRecipe.fallbackRecipe.map(function (change) {
    return {
      ca: JSON.parse(JSON.stringify(change.ca)),
      // FCPD-9698 preserve original av instances to be able to use as a fallback for recipe.
      // See configuration-engine getOrSearchAvOrDefault and its usage of getVertexInstance
      av: change.av
    }
  });
  error.fcErrorDetails.recipeDocument = recipeDoc;


  /**
   * CD-1869 tested on configure-ui with
   * mocha -g "CD-1869"
   * The first recipe change event after an invalid recipe
   * is loaded should be used to set the default recipe.
   */
  recipe.once("change", function () {
    // CD-2410 skipSettingAsDefault flag added
    // to prevent recipe from rolling
    // back to a snapshot on reset instead of the recipe default
    if (!options.skipSettingAsDefault) {
      recipe.saveAsDefault();
    }
    recipe.commit();
    recipe.trigger("load", recipe.getData());
  });
  callback(error, recipeDoc);
}

function getLegacyRecipe(recipeDoc) {
  var legacyRecipe = recipeDoc["configurable_attributes"];

  if (!legacyRecipe) {
    return;
  }

  // Shadow attributes are added when a recipe is being saved
  // by FC2 (i.e. from a catalog) but they are irrelevant for us
  // or the engine
  legacyRecipe = removeShadowAttributes(legacyRecipe);

  return legacyRecipe;
}

// Saved recipes are immutable so we can memoize the recipes
// in memory for implementations that re-create a configure-ui instance
// when performing Onload Availability like Louis Vuitton.
var performMemoizedRequest;

/** Uses either a custom method passed via fcParams.recipeLoader,
  options.recipeLoader to load the recipe JSON from the server.
*/
function getPerformLoadFunction(fcParams, options) {
  return options.recipeLoader || fcParams.recipeLoader ||
    performMemoizedRequest;
}

/** Performs a request form the API server and grabs the configurable
  attributes for the recipe.
*/
function performRequest(url, callback) {
  request
    .get(url)
    .end(function (err, res) {
      if (err) { return callback(err); }
      if (res.status >= 400) {
        return callback(new Error("couldn't load recipe: " + res.error));
      }

      callback(null, res.body);
    });
}

function isValidRecipeId(recipeId) {
  return (+recipeId > 0) || V3_RECIPE_ID_RE.test(recipeId);
}

performMemoizedRequest = async.memoize(performRequest);

function removeShadowAttributes(legacyRecipe) {
  return _.reduce(_.keys(legacyRecipe), function (newLegacyRecipe, key) {
    if (!_.contains(key, "shadow")) {
      newLegacyRecipe[key] = legacyRecipe[key];
    }
    return newLegacyRecipe;
  }, {});
}
