module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * Facet Value.
   *
   * On the graph their vertices contain the namespace (ns) "fv"
   *
   * @param {Object} options Options
   * @param {number} options.id Facet value numeric id.
   * @class
   * @name FacetValue
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends FacetValue */{

    ns: "fv",

    /**
     * Gets the {@link AttributeValue}s linked to this FacetValue
     * @return {Array<AttributeValue>}
     * @instance
     */
    attributeValues: function () {
      return this.getAndCache("attributeValues", function () {
        return this.traverser()
          .incoming("hasFacetValue")
          .where({ns: "av"})
          .expand()
          .sortBy("priority")
          .instances();
      });
    },

    /**
     * Get the {@link Facet} that is linked to this {FacetValue}
     *
     * @instance
     * @return {Facet}
     */
    facet: function () {
      if (!this._facet) {
        this._facet = this.traverser()
          .incoming("hasFacetValue")
          .where({ns: "f"})
          .first();
      }
      return this._facet;
    }

  });
};


