module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGlEnvironment.
   *
   * On the graph their vertices contain the namespace (ns) "wenv"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGlEnvironment numeric id.
   * @class
   * @name WebGlEnvironment
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlEnvironment */{

    ns: "wenv",

    /**
     * Gets webGl environment camera.
     *
     * @instance
     * @return {Object<WebGlEnvironmentCamera>}
     */
    camera: function () {
      return this.getAndCache("camera", function () {
        return this.traverser()
          .outgoing("hasWebGlEnvironmentCamera")
          .instance();
      })
    },

    /**
     * Gets webGl HDR background.
     *
     * @instance
     * @return {Object<WebGlHdrBackground>}
     */
    hdrBackground: function () {
      return this.getAndCache("hdrBackground", function () {
        return this.traverser()
          .outgoing("hasWebGlHdrBackground")
          .instance();
      })
    }

  }, constructor);

};
