/**
 * @publicDocs methods/filterText ../../docs/filter_text.md
 */

module.exports = function (engine) {

  return function filterText(text, caId) {
    if (!text) {
      return "";
    }
    if (!caId) {
      throw new Error("Missing caId on filterText");
    }

    var caNode = engine.vertexInstance({
      vertexKey: "ca:" + caId
    });

    var fontValue = caNode.selectedValue();
    return fontValue.sanitize({text: "" + text}).text;
  };
};
