/**
 * @publicDocs methods/getRecipe ../../docs/get_recipe.md
 */

var _ = require("lodash");
var i = require("icepick");

function getLegacyVerboseRecipe(recipe) {
  return recipe.legacy("verbose");
}

function getLegacyConciseRecipe(recipe) {
  return recipe.legacy("concise");
}

function getCompactRecipe(recipe) {
  return recipe.toHash("id", "id");
}

function getHumanRecipe(recipe) {
  return recipe.toHash("alias", "name");
}

function getCustomRecipe(recipe, product, caKey, avKey) {
  return JSON.parse(JSON.stringify(recipe.toHash(caKey, avKey)));
}

function getArrayRecipe(recipe, includeHidden) {
  return recipe.toArray(includeHidden);
}

function getJsonRecipe(recipe) {
  return JSON.parse(JSON.stringify(recipe));
}

function getExtendedRecipe(productJson, recipe) {
  if (!productJson) {
    throw new Error(
      "extended recipe format requires a initialized productJson"
    );
  }
  var recipeJson = getJsonRecipe(recipe);
  return _.reduce(recipeJson, function (memo, entry, idx) {
    var values = i.getIn(
      productJson,
      productJson.allAttributes[entry.ca.id]
    ).values;
    memo[idx].av = _.find(values, {id: entry.av.id});
    return memo;
  }, recipeJson);
}

module.exports = function (engine, getProduct) {
  getProduct = getProduct || _.noop;

  return function getRecipe(format, option1, option2) {
    format = format || "compact";

    var
      recipe = engine.recipe(false),
      product = engine.product(),
      output,
      formats = {
        legacyVerbose: getLegacyVerboseRecipe,
        legacyConcise: getLegacyConciseRecipe,
        compact: getCompactRecipe,
        save: getArrayRecipe,
        human: getHumanRecipe,
        custom: getCustomRecipe,
        json: getJsonRecipe,
        extended: _.partial(getExtendedRecipe, getProduct())
      };

    if (!formats[format]) {
      throw new Error("Invalid format \"" + format + "\" when calling " +
        "`getRecipe`. Valid formats are: " + _.keys(formats).join(", "));
    }

    output = formats[format](recipe, product, option1, option2);

    return output;
  };

};
