/**
 * @publicDocs methods/analytics ../../../docs/analytics.md
 */

var analyticsEvents = require("./analytics_events");
var analyticsEventsMock = require("./mocks/analytics_events_mock");

var executionEnv = process.env.NODE_ENV;

module.exports = function (fcParams) {

  return function analytics(eventName, eventData, isWebGl) {
    if (executionEnv !== "test") {
      analyticsEvents(fcParams, eventName, eventData, isWebGl);
    } else {
      /**
        * this mock avoids make http requests in test,
        * analytics events are better tested on directory:
        * test/metrics.test.js
      */
      analyticsEventsMock(fcParams, eventName, eventData, isWebGl);
    }
  };
};
