const _ = require("lodash");
module.exports = function (options) {
  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGl Loading Strategy.
   *
   * On the graph their vertices contain the namespace (ns) "wload"
   * Loading strategies are used to connect ConfigurableProduct to WebGlModelLod via WebGlProductLodResolution
   *
   * @param {Object} options Options
   * @param {number} options.id WebGl Loading strategy numeric id.
   * @class
   * @name WebGlLoadingStrategy
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(
    /** @lends WebGlLoadingStrategy */ {
      /**
       * Gets LOD resolutions of the strategy.
       *
       * @instance
       * @return {Array<WebGlLodResolution>}
       */
      lodResolutions: function () {
        return this.getAndCache("lodResolutions", function () {
          return this.traverser()
            .outgoing("hasModelLodResolution")
            .outgoing("hasLodResolution")
            .unique()
            .instances();
        });
      },

      /**
     * Gets single webGl LOD resolution matching query.
     *
     * @instance
     @param {Object} query - The object of property WebGlLodResolution to filter by
     * @return {WebGlLodResolution}
     */
      findLodResolution: function (query) {
        var queryToUse = query;
        if (_.isString(query) || _.isNumber(query)) {
          // assume it's an ID
          queryToUse = { id: +query };
        }
        return this.traverser()
          .outgoing("hasModelLodResolution")
          .outgoing("hasLodResolution")
          .where(queryToUse)
          .instance();
      },

      /**
       * Gets webGl Model LODs for this strategy
       * @instance
       * @return {Array<WebGlModelLod>}
       */
      modelLods: function () {
        return this.getAndCache("modelLods", function () {
          return this.traverser()
            .outgoing("hasModelLodResolution")
            .outgoing("hasModelLod")
            .unique()
            .instances();
        });
      },

      /**
       * Gets webGl Model LODs that are available for a given resolution.
       *
       * @instance
       * @param {Partial<WebGlLodResolution>} query - query to filter resolution by
       * @return {Array<WebGlModelLod>}
       */
      findModelLodsByResolution: function (query) {
        var queryToUse = query;
        if (_.isString(query) || _.isNumber(query)) {
          // assume it's an ID
          queryToUse = { id: +query };
        }

        const modelLods = [];
        
        this.traverser()
          .outgoing("hasModelLodResolution")
          .instances()
          .filter(function (productLod) {
            var lodResolution = productLod.lodResolution();
            return lodResolution
              ? _.matches(queryToUse)(lodResolution.getData())
              : false;
          })
          .forEach(function (productLod) { // flatMap
            productLod.modelLods().forEach(function (modelLod) {
              modelLods.push(modelLod);
            });
          });

        return modelLods;
      },

      ns: "wload",
    },
    constructor
  );
};
