/**
 * @publicDocs methods/getWebGlModelLods ../../docs/get_webgl_model_lods.md
 */

var _ = require("lodash"),
  validateFilter = require("../util").validateFilter;

module.exports = function (engine) {
  var product = engine.product();
  /**
   * Gets a collection of WebGl model LOD for the current Product.
   *
   * @param  {Object} options - Options object
   * @param  {Object} options.filter - Filters the attributes using
   *                                 [_.filter](https://lodash.com/docs#filter)
   * @returns {Array<Object>} An array of WebGl Model LOD objects
   */
  return function getWebGlModelLods(options) {
    options = options || {};

    validateOptions(options);

    return product.webGlModelLods(options.filter).map(function (lod) {
      return Object.freeze(
        Object.assign({}, lod.getData(), {
          webGlModelId: lod.webGlModel() && lod.webGlModel().id,
        })
      );
    });
  };

  function validateOptions(options) {
    if (!_.isPlainObject(options)) {
      throw new Error("getWebGlModelLods options should be an Object");
    }

    validateFilter(options.filter);
  }
};
