var _ = require("lodash");
// Product facets don't change with recipe changes so there's no need to
// keep them up to date
module.exports = function (engine, product) {
  return _.reduce(product.facets(), function (facets, facet) {
    var data = facet.getData();
    facets[data.name] = facets[data.name] || [];
      /* HISTORICAL NOTE:
       * At some point, fluid-configurations changed the data type of the
       * values property from an array to just a string. If you look at a
       * configuration graph (at the time of this writing), notice that
       * Product Facet vertices ({"ns": "pf"}) should contain single-entry
       * arrays in the .values property, and there can be *multiple product
       * facets with the same name*. What we're doing below is creating an
       * array from those values. IMPORTANT: .concat() is being used
       * because it can accept arguments as either strings OR arrays:
       * eg.
       *  [1,2].concat("three")   -> [1,2,"three"]
       *  [1,2].concat(["three"]) -> [1,2,"three"]
       *                             ^^^^^^^^^^^^^ same result
       * Using .concat lets us address configuration graphs published with
       * either strings OR arrays.
       *
      */
    facets[data.name] = facets[data.name].concat(data.values);
    return facets;
  }, {});
}