module.exports = getConfig;

var getJson = require("./get_json");
var jsonpClient = require("jsonp-client");
var defaults = require("lodash/defaults");
var getPreferences = require("./preferences");
var config = require("./config");
var debug = require("debug")("fluid:configuration-loader:http_client");
var memoize = require("memoizee");

var RE_HASH = /\W/g;

var shouldMemoize = config.memoize || process.browser;

var getMemoizedJson = shouldMemoize ? memoize(getJson, {
  // args to use in the hash  map
  length: 1,
  async: true,
  max: config.cacheSize
}) : getJson;

var getMemoizedJsonp = shouldMemoize ? memoize(jsonpClient, {
  // args to use in the hash  map
  length: 1,
  async: true,
  max: config.cacheSize
}) : jsonpClient;

if (config.memoize && !process.browser) {
  debug("[info] Using disk memoizer");
  var diskMemoizer = require("disk-memoizer");
  getMemoizedJson = diskMemoizer(getJson, {
    type: "json",
    cacheDir: config.cacheDir,
    memoryCacheItems: config.cacheSize
  });
  getMemoizedJsonp = diskMemoizer(getMemoizedJsonp, {
    type: "json",
    cacheDir: config.cacheDir,
    memoryCacheItems: config.cacheSize
  });
} else {
  debug("[info] Not using disk memoization");
}

/**
 * Gets a JSON configuration via a CORS JSON request or a JSONp call
 *
 * @param  {Object}   params       Parameters for generating the URL
 * @param  {Function} urlGenerator fluid-product-urls method
 *                                 for generating the URL
 * @param  {Function} callback     (err, data)
 */
