module.exports = getPreferences;

var configureUrls = require("@cfg.plat/fluid-product-urls/configure");
var jsonpClient = require("jsonp-client");
var getJson = require("./get_json");
var config = require("./config");
var memoize = require("memoizee");
var debug = require("debug")("fluid:configuration-loader:preferences");

var shouldMemoize = config.memoize || process.browser;

var getMemoizedJson = shouldMemoize ? memoize(getJson, {
  // args to use in the hash  map
  length: 1,
  async: true,
  max: config.cacheSize
}) : getJson;

var getMemoizedJsonp = shouldMemoize ? memoize(jsonpClient, {
  // args to use in the hash  map
  length: 1,
  async: true,
  max: config.cacheSize
}) : jsonpClient;

if (config.memoize && !process.browser) {
  getMemoizedJson = require("disk-memoizer")(getJson, {
    type: "json",
    cacheDir: config.cacheDir,
    maxAge: config.preferencesTtl,
    memoryCacheItems: config.cacheSize
  });
}

function getPreferences(params, callback) {
  // Allow overriding defaults
  var jsonBaseUrl = params.jsonBaseUrl || config.getBaseUrl(params);

  var jsonpBaseUrl = params.jsonpBaseUrl || config.getJsonpBaseUrl(params);
  var configVersion = params.configVersion || config.version;
  var jsonUrl = void 0;
  var preferencesPath = void 0;
  var jsonpUrl = void 0;

  try {
    preferencesPath = configureUrls.preferences(params);
  } catch (e) {
    return callback(e);
  }

  function setResultDefaults(result) {
    result.configVersion = configVersion;
    result.configBase = jsonBaseUrl;
    result.isBrowser = process.browser;
    result.useJsonp = false;
    return result;
  }

  if (params.preferences) {
    return callback(null, setResultDefaults(JSON.parse(JSON.stringify(params.preferences))));
  }

  var urlPath = jsonBaseUrl + "/" + preferencesPath;

  jsonUrl = urlPath + (urlPath.match(/\?/) ? "&" : "?");

  if (params.skipCache && getMemoizedJson.delete) {
    getMemoizedJson.delete(jsonUrl);
  }

  // eslint-disable-next-line complexity
  getMemoizedJson(jsonUrl, function (err, json) {
    var result = setResultDefaults(json || {});

    try {
      // We want to allow forcing jsonp via params and also
      // for testing purposes
      if (params.forceJsonp) {
        throw new Error("Forcing jsonp for " + jsonUrl);
      }
      if (!result.version) {
        throw new Error("Version not found on " + jsonUrl);
      }
    } catch (e) {

      // On browsers, an invalid preferences.json might represent that
      // CORS JSON is not working properly, this can be the case
      // in some old browsers and also can be caused by some VPN
      // policies.
      if (result.isBrowser || params.forceJsonp) {
        jsonpUrl = jsonpBaseUrl + "/" + preferencesPath;
        jsonpUrl += jsonpUrl.match(/\?/) ? "&" : "?";
        jsonpUrl += "callback=cb";

        debug("[info] Switching to JSONp, calling %s", jsonpUrl);

        if (params.skipCache && getMemoizedJson.delete) {
          getMemoizedJsonp.delete(jsonpUrl);
        }

        return getMemoizedJsonp(jsonpUrl, function (err, jsonpResult) {
          var result = setResultDefaults(jsonpResult || {});
          result.useJsonp = true;
          result.configBase = jsonpBaseUrl;

          if (!result.version) {
            result.version = 0;
          }

          callback(err, result);
        });
      } else if (!err) {
        err = e;
      }
    }
    callback(err, result);
  });
}

