var _ = require("lodash");

var RE_LAST_NUMBERS = require("./common").RE_LAST_NUMBERS;

/**
 * This method is used for loading FC2 recipes as
 * FC3 recipes. This should be deprecated in favor of
 * a simpler and less verbose format returned by the servers.
 *
 * convert the object
 * {
 *   "p_234_ca_1234": "av2345",
 *   "p_234_ca_1236": "av2346",
 *   ...
 * }
 * to
 * [ [1234, 2345], [1236, 2346], ... ]
 */
module.exports =
function convertVerboseRecipe(verboseRecipe, productInstance) {

  function getCaInstance(caString) {
    return productInstance.attributeById(+RE_LAST_NUMBERS.exec(caString)[1]);
  }

  function addCaChangesToResult(ca, caString, avData, result) {
    if (ca.hasDynamicValue()) {
      var dynamicCa = ca.getDynamicAttributeValue();
      setDynamicValue(avData, ca, dynamicCa);
      result.changes.push([ca.id, dynamicCa]);
    } else {
      var avIdString = RE_LAST_NUMBERS.exec(avData);
      if (!avIdString) {
        result.exceptions.push(
          new Error("Failed converting verbose recipe " +
                  JSON.stringify(verboseRecipe) + " ca " + ca +
                  " with av " + JSON.stringify(avData)) +
                  " and ca info " +
                  JSON.stringify(
                    caInfoMaibe(productInstance, caString), null, 2)
          );
        result.changes.push([ca, avData]);
      } else {
        result.changes.push([ca, +avIdString[1]]);
      }
    }
  }

  return _.reduce(verboseRecipe, function (result, avData, caString) {
    var ca;
    // Unset legacy values are represented with a dash
    // Skip over any FC2 shadow attributes
    if (avData === "-" || !caString || avData === "av_shadow_value") {
      return result;
    }

    try {
      ca = getCaInstance(caString);
    } catch (e) {
      const error = new Error("Failed converting verbose recipe " +
        JSON.stringify(verboseRecipe) + " ca " + caString + " does not exist");
      // Nothing to do if the ca was completely removed from the product
      // we can't fall-back to anything under this circumstance
      console.log(error);
      result.exceptions.push(error);
      return result;
    }

    try {
      addCaChangesToResult(ca, caString, avData, result);
    } catch (e) {
      result.changes.push([ca, avData]);
      result.exceptions.push(e);
    }

    return result;
  }, {changes: [], exceptions: []});

};

function setDynamicValue(avData, ca, dynamicCa) {
  if (_.isString(avData)) {
    if (ca.getData().allowUGC) {
      dynamicCa.setData({ clipArt: avData });
    } else if (ca.isClipartEditor()) {
      // TODO: find a way to get the images data
      dynamicCa.setData({ clipArt: avData, images: [] });
    } else {
      dynamicCa.setData({text: avData});
    }
  } else {
    dynamicCa.setData(avData);
  }
}

function caInfoMaibe(productInstance, caString) {
  try {
    return productInstance.attributeById(+RE_LAST_NUMBERS.exec(caString)[1])
      .explain();
  } catch (e) {
    return " not available for " + caString;
  }
}

