module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGl Model Component.
   *
   * On the graph their vertices contain the namespace (ns) "wmc"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGl Model Component numeric id.
   * @class
   * @name ModelComponent
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends ModelComponent */{

    ns: "wmc",

    /**
     * Gets webGl customer material.
     *
     * @instance
     * @return {Object<WebGlCustomerMaterial>}
     */
    customerMaterial: function () {
      return this.getAndCache("customerMaterial", function () {
        return this.traverser()
          .outgoing("hasMaterial")
          .instance();
      })
    }

  }, constructor);

};
