// Run tests with mocha -g "CD-192|Polo Shirt"

// Important!
// This code has been optimized for improved CPU performance
// We've avoided nested for loops/map/reduce/filters as much as possible
// by creating lookup maps and avoiding work when not necessary
// The performance of this code impacts directly the load time of configure
// therefore it's critical that the performance remains a top priority when
// editing this code.
//
// Run the benchmarks with: "make benchmark" and make sure that
// the performance doesn't degrade over time
//
// In order to analyze the areas that require optimization, run
// `maket test-coverage-report` and focus your energy on the
// most traveled paths.

///////////////////////////////////////////////////////////////////////////////
// Suggestions for future improvements, mostly for readability and
// maintainability:
// 1. Eliminate many of the functions so that the calling stack is smaller and
// the code is more compact and easier to follow.
// 2. Simplify the use of the options parameters to the functions. One approach
// would be to standardize the options objects between the calls so that there
// is more consistency and efficiency. Another option would be to use named
// parameters instead of options. In either case, the parameters should be
// pruned so that only essential data is being passed.
// 3. Reduce the usage of variables scoped to the module in favor of variables
// scoped more narrowly to the functions they are needed, and explicitly passed
// as parameters between functions. Rely less on broad closures.
// 4. More thorough documentation, especially of parameters to functions.
///////////////////////////////////////////////////////////////////////////////

module.exports = overrideProductGraph;

var _ = require("lodash");
var createId2IxsMap = require("./product_overrides_helpers/createId2IxsMap.js");
var createAvId2CaObjMap =
  require("./product_overrides_helpers/createAvId2CaObjMap.js")

/**
 * Product overwriting on FC2 supported via the productOverrides param
 * to change the values of valueUsage for some av's
 * Right now we support the availability but not the associatedView or the
 * up-charge override that we had on FC2
 */
var debug = false;

function overrideProductGraph(graph, overrides) {
  debug = overrides.debug;
  override(graph, _.get(overrides, "values", {}));
  // Keep track of the "renderInactive" value for use elsewhere.
  // vertices[0] === product JSON
  graph.vertices[0].renderInactive = _.get(overrides, "renderInactive", false); // eslint-disable-line
}

var findRelatedIndexMap;
var findRelatedVUIndexMap;
var findRelatedCaIndexMap;
var findAvsByVendorIdMap;
var av2CaMap;
var scopeInfo = {};
var upchargesRelations;
var hasAttributeValue;
var defaultCurrency;
var vertices;

function override(graph, overridenValues) {
  // Used to debug where a change came from
  var hasValueUsageRelations = _.get(graph, "edgeGroups.hasValueUsage", []);
  var usesValueRelations = _.get(graph, "edgeGroups.usesValue", []);
  upchargesRelations = _.get(graph, "edgeGroups.upcharges", []);
  hasAttributeValue = _.get(graph, "edgeGroups.hasAttributeValue", []);
  defaultCurrency = _.get(graph, "vertices[0].currencySettings.default", "USD");
  vertices = graph.vertices;

  // findRelatedIndexMap: the keys are attribute value id's and the values are
  // the index numbers of the value usages associated with the indicated av's.
  findRelatedIndexMap = createId2IxsMap(usesValueRelations, vertices, true);
  findRelatedVUIndexMap = createId2IxsMap(upchargesRelations, vertices);
  findRelatedCaIndexMap = createId2IxsMap(hasValueUsageRelations, vertices);
  av2CaMap = createAvId2CaObjMap(hasAttributeValue, vertices);
  findAvsByVendorIdMap = populateFindAvsByVendorIdMap(vertices);

  processOverrides(overridenValues);

  findRelatedIndexMap = null;
  findRelatedVUIndexMap = null;
  findRelatedCaIndexMap = null;
  findAvsByVendorIdMap = null;
}

function processOverrides(overridenValues) {
  var overridenKeys = Object.keys(overridenValues);
  var totalOverrides = overridenKeys.length;

  for (
    var indexOverride = 0;
    indexOverride < totalOverrides;
    indexOverride = indexOverride + 1
  ) {
    var valueId = overridenKeys[indexOverride];
    var newValueUsages = overridenValues[valueId].valueUsages;
    // if does not have value usages we don't need to apply changes
    if (!newValueUsages) {
      continue;
    }

    // get attribute values for this vendorId
    var avs = findAvsByVendorIdMap[valueId];

    // if this vendorId does not apply we can move on
    if (!avs) {
      if (process.env.NODE_ENV !== "production" && debug) {
        console.log("No matching av's for vendorId: '%s'", valueId);
      }
      continue;
    }

    var newVUKeys = Object.keys(newValueUsages);
    var totalVuKeys = newVUKeys.length;

    // get the value usage from the new config by alias or vendor id
    for (var index = 0; index < totalVuKeys; index = index + 1) {
      updateOverrideForVU(newVUKeys[index], newValueUsages, avs);
    }
  }
}

function populateFindAvsByVendorIdMap(graphVertices) {
  var vId2Avs = {};
  var found = false;
  var vLen = graphVertices.length;
  var vertex = null;
  for (var ix = 0; ix < vLen; ix += 1) {
    vertex = graphVertices[ix];
    if (vertex.ns === "av") {
      vId2Avs[vertex.vendorId] = vId2Avs[vertex.vendorId] || [];
      vId2Avs[vertex.vendorId].push(vertex);
      found = true;
    } else if (found) {
      // If we have already found the av's and are done with them, we can
      // break the loop because all the av's are together in the vertices
      // array.
      break;
    }
  }
  return vId2Avs;
}

/**
 * @param {String} vuId The id of the value usage being updated.
 * @param {Dictionary} newValueUsages Dictionary lookup object of value usages
 * from the product overrides keyed by their ids.
 * We only care about the single value usage with the id of vuId.
 * @param {Array} avs The attribute value objects from the product that match
 * the current vendor id.
 */
function updateOverrideForVU(vuId, newValueUsages, avs) {
  var newVu = newValueUsages[vuId];
  var vus = getVusForId(vuId, avs);
  updateOverrides(vus, newVu, vuId);
}

/**
 * @param {Array} vus The value usages from the product that will be updated
 * based on the newVu parameter.
 * @param {Object} newVu The single value usage object from the product
 * overrides that will be used to update the product definition.
 * @param {String} vuId The vendor id of the value usage we are updating.
 */
function updateOverrides(vus, newVu, vuId) {
  var hasActive = _.has(newVu, "active");

  if (hasActive) {
    overrideAvailability(vus, newVu.active);
  }

  // check if we need override upcharge
  if (_.has(newVu, "upcharge")) {
    var options = {
      vus: vus,
      valueId: vuId,
      upchargesRelations: upchargesRelations,
      upcharge: newVu.upcharge
    };

    if (hasActive) {
      options.active = newVu.active;
    }
    overrideNewUpcharge(options);
  }
}

/**
 * Override new Upcharges
 * @param options
 **/
function overrideNewUpcharge(options) {
  // we only override upcharge for active values
  if (
    !_.has(options, "active") ||
    (_.has(options, "active") && options.active)
  ) {
    var vus = options.vus;
    var upchargesRelations = options.upchargesRelations;

    // if we have an upcharge to override we do it
    if (upchargesRelations) {
      overrideUpcharge(vus, options.upcharge);
    }
  }
}

function overrideAvailability(vus, isActive) {
  isActive = isActive === true || isActive === "true";
  vus.map(function(vu) {

    if (vu && vu.active !== isActive) {
      vu.active = isActive;
      if (process.env.NODE_ENV !== "production" && debug) {
        console.log(
          "Changing availability for vu:%s %s to: %j on %s",
          vu.id,
          vu.vendorId,
          isActive,
          scopeInfo[vu.id]
        );
      }
    }
  });
}

/**
 * @param {String} id The value usage id that is being overridden.
 * @param {Array} attributeValues The attribute values from the product whose
 * vendor id is specified in an outer iteration.
 * @return The value usages with the given id that are associated with one of
 * given AV's, either directly or via an association with a related CA.
 */
function getVusForId(id, attributeValues) {
  var vusForId = attributeValues.map(function(av) {
    // Look for value usages that belong to the attribute value that have the
    // indicated value usage id.
    var vus = getVusRelatedToAv(av, id);
    if (vus.length > 0) {
      return vus;
    }

    var cas = findCasWithAv(id, av);
    if (cas.length > 0) {
      return cas.map(function(ca) {
        return findValueUsages(ca, av);
      });
    } else {
      return [];
    }
  });

  return _.flattenDeep(vusForId);
}

/**
 * @param {Object} av An attribute value from the product definition.
 * @param {String} id A value usage id from the collection of overrides.
 * @return {Array} The value usages with the given id that are associated with
 * the given attribute values.
 */
function getVusRelatedToAv(av, id) {
  // valueUsageIxs is an array of the index numbers of the value usages
  // associated with the given attribute value.
  var valueUsageIxs = (findRelatedIndexMap[av.id] || []);
  return valueUsageIxs.reduce(function(relatedVus, idx) {
    var vu = vertices[idx];
    if (vu.vendorId + "" === id + "") {
      if (process.env.NODE_ENV !== "production" && debug) {
        scopeInfo[vu.id] = "av:" + av.id + " " + av.vendorId;
      }
      vu.idx = idx;
      relatedVus.push(vu);
    }
    return relatedVus;
  }, []);
}

function overrideValues(options) {
  var currency = options.currency;
  var vu = options.vu;
  // create and push the new vertex
  var newUc = {
    id: "uccOverride" + options.vu.id + options.ucIdx + currency,
    ns: "uc"
  };
  newUc[currency] = options.value;
  vertices.push(newUc);
  // Remove the previous vu > uc relation
  _.remove(upchargesRelations, function(relation) {

    return relation[0] === vu.idx && relation[1] === options.ucIdx;
  });
  // push the new vu > uc relation
  upchargesRelations.push([vu.idx, vertices.length - 1]);
}

/**
 * @param {Array} vus The value usage objects from the product, the ones to be
 * updated.
 * @param {Object} upcharge The upcharge data from the overrides that will be
 * applied to the value usage objects in vus.
 */
function overrideUpcharge(vus, upcharge) {
  // remove empty vus (PE-409)
  var vuFiltered = vus.filter(function(vu) {
    return vu;
  });

  var isUpchageObject = _.isPlainObject(upcharge);
  var upchageKeys = [];
  var upchageKeysLength = 0;

  if (isUpchageObject) {
    upchageKeys = Object.keys(upcharge);
    upchageKeysLength = upchageKeys.length;
  }

  var vuLength = vuFiltered.length;
  var vu = null;
  for (var index = 0; index < vuLength; index = index + 1) {

    vu = vuFiltered[index];
    updateUpCharges({
      upcharge: upcharge,
      vu: vu,
      upchageKeys: upchageKeys,
      isUpchageObject: isUpchageObject,
      upchageKeysLength: upchageKeysLength
    });
  }
}

function updateUpCharges(options) {
  var upcharge = options.upcharge;
  var vu = options.vu;
  var upchageKeys = options.upchageKeys;
  var isUpchageObject = options.isUpchageObject;
  var upchageKeysLength = options.upchageKeysLength;
  var ucs = findRelatedVUIndexMap[vu.id] || [];

  for (var ucsIndex = 0; ucsIndex < ucs.length; ucsIndex = ucsIndex + 1) {

    updateUpAllChargesValues({
      vu: vu,
      uc: vertices[ucs[ucsIndex]],
      idx: ucs[ucsIndex],
      upcharge: upcharge,
      upchageKeys: upchageKeys,
      upchageKeysLength: upchageKeysLength,
      isUpchageObject: isUpchageObject
    });
  }
}

function updateUpAllChargesValues(options) {
  var uc = vertices[options.idx];
  if (options.isUpchageObject) {
    updateUpchargeForObject(options);
  } else {
    // if a map is not provided we use the default currency
    setNewUpcharge({
      previousUpcharge: uc[defaultCurrency],
      newUpcharge: options.upcharge,
      vu: options.vu,
      currency: defaultCurrency,
      idx: options.idx
    });
  }
}

function updateUpchargeForObject(options) {
  var vu = options.vu;
  var uc = options.uc;
  var idx = options.idx;
  var upcharge = options.upcharge;
  var upchageKeys = options.upchageKeys;
  var upchageKeysLength = options.upchageKeysLength;
  // if a map is provided we need to get upcharge by currency
  for (
    var upChIndex = 0;
    upChIndex < upchageKeysLength;
    upChIndex = upChIndex + 1
  ) {
    var currency = upchageKeys[upChIndex];
    var value = upcharge[currency];
    setNewUpcharge({
      previousUpcharge: uc[currency],
      newUpcharge: value,
      vu: vu,
      currency: currency,
      idx: idx
    });
  }
}

function setNewUpcharge(data) {
  var newUpcharge = +data.newUpcharge;

  // if upcharge it's the same then do nothing
  if (data.previousUpcharge === newUpcharge) {
    return;
  }
  // show some info
  if (process.env.NODE_ENV !== "production" && debug) {
    console.log(
      "Changing upcharge for vu %s from %s to %s on " + "the currency %s",
      data.vu.vendorId,
      data.previousUpcharge,
      newUpcharge,
      data.currency
    );
  }
  overrideValues({
    vu: data.vu,
    currency: data.currency,
    ucIdx: data.idx,
    value: newUpcharge
  });
}

function findValueUsages(ca, av) {
  var caVus, avVus;
  caVus = findRelatedCaIndexMap[ca.id] || [];
  avVus = findRelatedIndexMap[av.id] || [];
  return _.intersection(caVus, avVus).map(function(idx) {

    var vu = vertices[idx];
    if (process.env.NODE_ENV !== "production" && debug) {
      scopeInfo[vu.id] =
        "ca:" +
        ca.id +
        " " +
        ca.alias +
        " av:" +
        av.id +
        " " +
        av.vendorId;
    }
    vu.idx = idx;
    return vu;
  });
}

function findCasWithAv(id, av) {
  var superSet = av2CaMap[av.id];
  var result = superSet.filter(function (ca) {
    return isCaRelatedWithVuIdOrAlias(ca, id);
  });
  return result;
}

function isCaRelatedWithVuIdOrAlias(ca, vuIdOrAlias) {
  return ca.vendorId === vuIdOrAlias || ca.alias === vuIdOrAlias ||
    ca.id === vuIdOrAlias;
}
