module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * SympatheticMatch.
   *
   * On the graph their vertices contain the namespace (ns) "sm"
   *
   * @param {Object} options Options
   * @class
   * @name SympatheticMatch
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends SympatheticMatch */{
    ns: "sm",

    /**
     * Gets the {Facet} linked to this {SympatheticMatch}
     *
     * @instance
     * @return {Facet}
     */
    facet: function () {
      var self = this;
      return this.getAndCache("facet", function () {
        return self.traverser()
          .outgoing("hasFacet")
          .instance();
      });
    },

    /**
     * Gets the slave {ConfigurableAttribute} linked to this {SympatheticMatch}
     *
     * @instance
     * @return {ConfigurableAttribute}
     */
    slaveAttribute: function () {
      var self = this;
      return this.getAndCache("slaveAttribute", function () {
        return self.traverser()
          .outgoing("hasSlave")
          .instance();
      });
    },

    /**
     * Return true if SympatheticMatch is of exclude kind
     *
     * @return {Boolean}
     */
    isExclude: function () {
      var self = this;
      return this.getAndCache("isExclude", function () {
        return self.getData().operation === "exclude";
      })
    }

  });
};
