module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGlCustomerMaterial.
   *
   * On the graph their vertices contain the namespace (ns) "wma"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGlCustomerMaterial numeric id.
   * @class
   * @name WebGlCustomerMaterial
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlCustomerMaterial */{

    ns: "wma",

    /**
     * Gets an instance of WebGlPlatformMaterial.
     *
     * @instance
     * @return {Object<WebGlPlatformMaterial>}
     */
    platformMaterial: function () {
      return this.getAndCache("platformMaterial", function () {
        return this.traverser()
          .outgoing("basedOnPlatformMaterial")
          .instance();
      })
    }

  }, constructor);

};
