/**
 * @publicDocs methods/getProductFacetsDetails
 *  ../../docs/get_product_facets_details.md
 */

var _ = require("lodash");
var deepFreeze = require("../util").deepFreeze;

function mapFacetValue(fv) {
  return fv && fv.getData ? fv.getData() : null;
}

function mapFacet(f) {
  return f && f.getData ? f.getData() : null;
}

module.exports = function (engine) {
  var facetsDetails;
  return function () {
    if (!facetsDetails) {
      facetsDetails = deepFreeze(
        _.reduce(
          engine.product().facets(),
          function (facets, productFacet) {
            var data = productFacet.getData();
            var values = productFacet.facetValues()
              ? productFacet.facetValues().map(mapFacetValue)
              : [];
            if (facets[data.name]) {
              facets[data.name].values = (facets[data.name].values || [])
                .concat(values)
            } else {
              facets[data.name] = {
                values: values,
                facet: mapFacet(productFacet.facet())
              };
            }

            return facets;
          },
          {}
        )
      );
    }
    return facetsDetails;
  };
};
