module.exports = function getMigration(version) {
  return function (response) {
    if (version === "2018") {
      // {"error":null,"text":"FUK","lang":"en","valid":false}
      return JSON.parse(JSON.stringify(response));
    } else {
      // { success: "true", error: "", text: "fuck", textValid: 0 }
      var result = {
        "success": response.err ? "false" : "true",
        "text": response.text,
        "textValid": response.valid ? 1 : 0
      };
      return result;
    }
  };
};