"use strict";

module.exports = createProductJson;
module.exports.createCA = createCA;

var _ = require("lodash");
var i = require("icepick");

var getProductFacets = require("./get_product_facets");
var isWebGl = require("./is_web_gl");

var currency;
var memoizedGetData;
var memoizedCreateAv;
function getData(node) {
  return i.freeze(node.getData());
}

/**
 * Takes in the configuration engine and returns the initial flat representation
 * used for rendering
 * @param  {configuration-engine context} engine
 * @return {Object}
 */
function createProductJson(engine, userCurrency) {
  var json,
    ags,
    cas,
    product = engine.product(),
    productData = product.getData(),
    renderInactive = _.isObject(productData.renderInactive)
      ? productData.renderInactive
      : !!productData.renderInactive;

  // memoize once per product so products don't conflict
  memoizedGetData = _.memoize(getData, vertexHasher);
  memoizedCreateAv = _.memoize(createAv, vertexHasher);

  currency = userCurrency;

  json = _.clone(i.freeze(product.getData()));
  json.isWebGl = isWebGl(engine)();
  json.facets = i.freeze(getProductFacets(engine, product));

  cas = product.rootAttributes();
  json.attributes = _.map(cas, function (attr) {
    var ca = attr.getData();
    var renderCaInactive = renderInactive;
    if (!_.isBoolean(renderCaInactive)) {
      renderCaInactive = !!renderCaInactive[ca.alias] ? true : false;
    }
    return createCA(attr, renderCaInactive);
  });
  // create a convenience map of caIds to their their paths in the hierarchy
  json.allAttributes = _.reduce(
    cas,
    function (map, caNode, index) {
      return createCAMap(map, caNode, ["attributes", index]);
    },
    {}
  );

  if (product.hasAttributeGroups()) {
    ags = product.attributeGroups();
    json.attributeGroups = _.map(ags, createAG);
  }

  // Grab the default view name for the product
  json.defaultViewName = product.defaultViewName();

  // json.attributes and json.attributeGroups are hierarchical representations.
  // allAttributes is a map from CA id to the *path* inside the heirarchical
  // representation
  Object.freeze(json.allAttributes);

  expandWebGlAttributes(json, product);

  releaseMemoizedFunction();

  return Object.freeze(json);
}

// release memoized functions so they can be GCed
function releaseMemoizedFunction() {
  memoizedGetData = getData;
  memoizedCreateAv = createAv;
}

function createAG(agNode) {
  var ag = _.clone(agNode.getData());

  ag.viewName = agNode.view();
  // just create a list of IDs, these will be looked up using json.attributes
  ag.attributes = _.map(agNode.attributes(), "id");

  return Object.freeze(ag);
}

function createOverrides(caNode, ca) {
  var createAvWithOverrides = _.partial(createCaAvWithOverrides, caNode);
  var selectableOverrides = _.reduce(
    caNode.valuesIgnoringValueUsageOverrides(),
    function (set, av) {
      set[av.id] = true;
      return set;
    },
    {}
  );
  ca.valuesWithOverrides = _.map(
    caNode.valuesIgnoringValueUsageOverrides(),
    createAvWithOverrides
  );
  ca.valuesWithOverrides = mapSelectablesAndFreeze(
    ca.valuesWithOverrides,
    caNode.selectedValue(),
    selectableOverrides
  );
}

// eslint-disable-next-line complexity
function createCA(caNode, renderInactive) {
  // dirty "type checking" to ensure boolean
  renderInactive = renderInactive === true;

  var ca = _.clone(caNode.getData()),
    selectableValues = _.reduce(
      caNode.selectableValues(),
      function (set, av) {
        set[av.id] = true;
        return set;
      },
      {}
    ),
    selectedValue,
    fontAttribute = ca.type === "text" && caNode.fontAttribute(),
    fontTextAttribute = ca.type === "text3d" && caNode.fontTextAttribute(),
    fontSizeTextAttribute = ca.type === "text3d" && caNode.fontSizeTextAttribute(),
    colorTextAttribute = ca.type === "text3d" && caNode.colorTextAttribute(),
    stylingTextAttribute = ca.type === "text3d" && caNode.stylingTextAttribute(),
    textAlignmentTextAttribute = ca.type === "text3d" && caNode.textAlignmentTextAttribute(),
    baselineTextAttribute = ca.type === "text3d" && caNode.baselineTextAttribute(),
    fontTextAttributeValue = ca.type === "text3d" && caNode.fontTextAttributeValue();

  ca.userEdited = false;
  ca.userChanged = false;
  ca.isVisible = caNode.isVisible();

  ca.canBeRendered = (caNode.canBeRendered() && ca.isVisible) || false;

  if (!ca.isDynamic) {
    selectedValue = caNode.selectedValue();

    var createAvForCA = _.partial(createCaAv, caNode);
    ca.values = _.map(caNode.activeValues(), createAvForCA);
    ca.allValues = _.map(caNode.allValues(), createAvForCA);

    // set the selected value
    ca.values = mapSelectablesAndFreeze(
      ca.values,
      selectedValue,
      selectableValues
    );

    // if supposed to render inactives (determined by runtime-passed
    // value usage overrides, create a new list)
    if (renderInactive) {
      createOverrides(caNode, ca);
    }
  } else {
    var av = caNode.getDynamicAttributeValue();
    if (av) {
      ca.value = i.freeze(av.getData());
      ca.values = i.freeze(av.getCollection());
    }
  }

  // get the value usage for each value (PD-1974)
  ca.valueUsages = _.reduce(
    ca.allValues,
    function (valueUsages, value) {
      var vu = caNode.valueUsageForValue(value);
      if (vu) {
        valueUsages.push(extendValueUsage(vu, value));
      }
      return valueUsages;
    },
    []
  );

  ca = createAllFacets(ca, caNode);

  ca.subAttributes = _.map(caNode.subAttributes(), function (caNode) {
    return createCA(caNode, renderInactive);
  });
  ca.viewName = caNode.viewName();

  if (caNode.hasCustomerMaterials()) {
    ca.customerMaterials = _.map(caNode.customerMaterials(), mapWebGlMaterial);
    ca.customerMaterialProperties = reduceCustomerMaterials(
      ca.customerMaterials
    );
  }

  if (caNode.hasDynamicMaterialRelatedCAs()) {
    ca.dynamicMaterialRelatedCAs = _.map(caNode.dynamicMaterialRelatedCAs(), function (dynamicProperty) {
      return Object.freeze(dynamicProperty.getData());
    });
  }

  if (fontAttribute) {
    ca.fontAttribute = createCA(fontAttribute, renderInactive);
  }

  // 3D Text fields
  if (fontTextAttribute) {
    ca.fontTextAttribute = createCA(fontTextAttribute, renderInactive);  
  }

  if (fontTextAttribute) {
    ca.fontTextAttribute =  createCA(fontTextAttribute, renderInactive);  
  }
  
  if (fontSizeTextAttribute) {
    ca.fontSizeTextAttribute = createCA(fontSizeTextAttribute, renderInactive);  
  }

  if (colorTextAttribute) {
    ca.colorTextAttribute = createCA(colorTextAttribute, renderInactive);
  }

  if (stylingTextAttribute) {
    ca.stylingTextAttribute = createCA(stylingTextAttribute, renderInactive);  
  }

  if (textAlignmentTextAttribute) {
    ca.textAlignmentTextAttribute = createCA(textAlignmentTextAttribute, renderInactive);
  }
  
  if (baselineTextAttribute) {
    ca.baselineTextAttribute = createCA(baselineTextAttribute, renderInactive);  
  }
  
  if (fontTextAttributeValue) {
    ca.fontTextAttributeValue = createCaAvWithOverrides(caNode, fontTextAttributeValue);  
  }

  addMetadata(caNode, ca);

  return Object.freeze(ca);
}

function extendValueUsage(vuNode, av) {
  var vuData = vuNode.getData();
  var valueUsage = {
    id: vuData.id,
    valueId: av.id,
    vendorId: vuData.vendorId,
    upcharge: av.upcharge,
    active: vuData.active,
  };

  if (vuData.vendorId) {
    av.valueUsageVendorId = vuData.vendorId;
  }

  if (vuData.pricingDescription) {
    valueUsage.pricingDescription = vuData.pricingDescription;
  }

  if (vuNode.viewName()) {
    valueUsage.associatedView = vuNode.viewName();
  }

  addMetadata(vuNode, valueUsage);

  return valueUsage;
}

function mapSelectablesAndFreeze(valueList, selectedValue, selectableValues) {
  return _.map(valueList, function (av) {
    av = _.clone(av);
    if (selectedValue && av.id === selectedValue.id) {
      av.selected = true;
    }
    av.selectable = !!selectableValues[av.id];
    Object.freeze(av);
    return av;
  });
}

function createAllFacets(ca, caNode) {
  ca.facets = _.map(
    caNode.facets(),
    _.partial(createCaFacet, caNode, "regular")
  );
  ca.optionalFacets = _.map(
    caNode.optionalFacets(),
    _.partial(createCaFacet, caNode, "optional")
  );
  ca.parameterizedFacets = _.map(
    caNode.parameterizedFacets(),
    _.partial(createCaFacet, caNode, "parameterized")
  );

  ca.hiddenFacets = _.map(
    caNode.hiddenFacets().concat(caNode.invertedHiddenFacets()),
    _.partial(createCaFacet, caNode, "hidden")
  );

  ca.allFacets = Object.freeze(
    [].concat(
      ca.facets,
      ca.hiddenFacets,
      ca.optionalFacets,
      ca.parameterizedFacets
    )
  );

  return ca;
}

/*
 * recursive helper function that creates the paths to each attribute in the
 * hierarchical representation
 */
function createCAMap(map, caNode, path) {
  map[caNode.id] = Object.freeze(path);
  return _.reduce(
    caNode.subAttributes(),
    function (map, subCANode, index) {
      return createCAMap(map, subCANode, path.concat(["subAttributes", index]));
    },
    map
  );
}

function completeAvData(avNode, caNode, av) {
  av.upcharge = avNode.getUpchargeFor(caNode, currency);

  var vu = caNode.valueUsageForValue(avNode);
  if (vu) {
    av.valueUsage = extendValueUsage(vu, av);
  }

  var valueUsageVendorId = getValueUsageVendorId(caNode, avNode);

  if (valueUsageVendorId) {
    av.valueUsageVendorId = valueUsageVendorId;
  }

  return av;
}

function createCaAvWithOverrides(caNode, avNode) {
  var av = createAv(avNode);
  av = completeAvData(avNode, caNode, av);

  var vu = caNode.valueUsageForValue(avNode);
  if (vu) {
    var vuData = vu.getData();
    av.active = vuData.active;
    if (vuData.vendorId) {
      av.valueUsageVendorId = vuData.vendorId;
    }
  }
  return av;
}

function createCaAv(caNode, avNode) {
  var av = memoizedCreateAv(avNode);
  return completeAvData(avNode, caNode, av);
}

function getValueUsageVendorId(caNode, avNode) {
  // PE-341 include value usage
  var vu = caNode.valueUsageForValue(avNode);
  if (vu) {
    var valueUsageVendorId = vu.getData().vendorId;
    if (valueUsageVendorId) {
      return valueUsageVendorId;
    }
  }
}

function createAv(avNode) {
  var av = _.clone(avNode.getData());

  if (avNode.hasMetadata()) {
    av.metadata = _.map(avNode.metadata(), getData);
  }

  if (avNode.hasCustomerMaterials()) {
    av.customerMaterials = _.map(avNode.customerMaterials(), mapWebGlMaterial);
    av.customerMaterialProperties = reduceCustomerMaterials(
      av.customerMaterials
    );
  }

  av.facets = _.reduce(
    avNode.facetValues(),
    function (result, facetValue) {
      var facetId = facetValue.facet().id;
      var fvs = result[facetId];
      if (fvs === undefined) {
        fvs = result[facetId] = [];
      }
      fvs.push(facetValue.id);
      return result;
    },
    {}
  );

  return av;
}

function createCaFacet(ca, type, f) {
  var facet = _.clone(f.getData());
  var isOptional = type === "optional";
  var isParameterized = type === "parameterized";

  facet.userEdited = false;
  facet.userChanged = false;
  facet.type = type;

  facet.facetValues = _.map(f.selectableFacetValues(ca), memoizedGetData);

  if (isOptional) {
    facet.facetValues.unshift({
      description: "all",
      // This will be localized on configure-ui from
      // uiSettings.facets.i18n.allLabel
      name: "All",
      id: -1,
      showAll: true,
      selectable: true,
    });
  }

  if (!isParameterized) {
    var selectedFv = ca.selectedFacetValue(facet);
    facet.facetValues = _.map(facet.facetValues, function (fv) {
      fv = _.clone(fv);
      if (selectedFv && selectedFv.id === fv.id) {
        fv.selected = true;
      }
      // TODO: is this flag needed?  We should remove the check from the UI
      fv.selectable = true;
      Object.freeze(fv);
      return fv;
    });
    Object.freeze(facet);
  }

  return facet;
}

/**
 * Generates an object attribute with the consolidation of all the material
 * properties.
 *
 * @param {Array} customerMaterials The customer materials properties to
 * consolidate.
 * @return {Object} with all the customer material properties consolidated.
 *
 * @example
 * the following customer material properties
 *
 * [{ opacity: 0.2, color: "#FFFFFF" }, { transparent: true, color: "#CCCDDD"}]
 *
 * will reduce to
 *
 * { opacity: 0.2, transparent: true, color: "#CCCDDD" }
 *
 */
function reduceCustomerMaterials(customerMaterials) {
  return _.reduce(
    customerMaterials,
    function (acc, customerMaterial) {
      return _.merge(acc, customerMaterial.properties);
    },
    {}
  );
}

/**
 * Add WebGl models and LODs if the product is WebGL.
 *
 * @param {Object} json    The product json.
 * @param {Object} product The engine product.
 *
 */
function expandWebGlAttributes(json, product) {
  if (!json.isWebGl) {
    return;
  }
  json.webGlLoadingStrategies = _.reduce(
    product.webGlLoadingStrategies(),
    function (acc, webGlModelStrategy) {
      var lodResolutionsData = _.map(
        webGlModelStrategy.lodResolutions(),
        mapLodResolution(webGlModelStrategy.id)
      );
      var modelLodsData = _.map(webGlModelStrategy.modelLods(), mapLod);
      var webGlStrategyData = _.merge(
        {
          lodResolutions: lodResolutionsData,
          modelLods: modelLodsData,
        },
        webGlModelStrategy.getData()
      );

      acc[webGlStrategyData.id] = Object.freeze(webGlStrategyData);
      return acc;
    },
    {}
  );

  // api.getWebGlModel and api.getWebGlModels
  json.webGlModels = _.reduce(
    product.webGlModels(),
    function (acc, webGlModel) {
      var webGlModelData = mapWebGlModel(webGlModel);

      acc[webGlModelData.id] = Object.freeze(webGlModelData);
      return acc;
    },
    {}
  );

  // api.getWebGlMaterial and api.getWebGlMaterials
  json.webGlMaterials = _.reduce(
    product.webGlMaterials(),
    function (acc, webGlMaterial) {
      var webGlMaterialData = mapWebGlMaterial(webGlMaterial);

      acc[webGlMaterialData.id] = Object.freeze(webGlMaterialData);
      return acc;
    },
    {}
  );

  if (product.hasWebGlEnvironment()) {
    var hdrBackgroundNode = product.webGlEnvironment().hdrBackground(), 
        cameraNode = product.webGlEnvironment().camera(),
        hdrBackground = (hdrBackgroundNode && hdrBackgroundNode.getData()) || null;

    json.webGlEnvironment = Object.freeze(_.merge(
      product.webGlEnvironment().getData(), 
      {
        camera: (cameraNode && cameraNode.getData()) || null,
        exposure: (hdrBackground && hdrBackground.exposure) || null,
        toneMapping: (hdrBackground && hdrBackground.toneMapping) || null,
        hdr: {
          assets: (hdrBackground && hdrBackground.assets) || []
        },
        background: (hdrBackground && mapWebGlBackground(hdrBackground)) || null
      }
    ));
  }
}

function mapWebGlBackground(hdrBackground) {
  var mapBackground = {
    COLOR: {
      transparent: false,
      hdr: false,
      color: hdrBackground.color
    },
    FILE: {
      transparent: false,
      hdr: true
    },
    TRANSPARENT: {
      transparent: true,
      hdr: false
    }
  };

  return mapBackground[hdrBackground.fillType];
}

function mapWebGlModel(webGlModel) {
  return Object.freeze(
    _.merge(
      {
        customerMaterial: webGlModel.customerMaterial()
          ? mapWebGlMaterial(webGlModel.customerMaterial())
          : null,
        modelComponents: _.map(
          webGlModel.modelComponents(),
          function (modelComponent) {
            var material = {};

            if (modelComponent.customerMaterial()) {
              material.customerMaterial = mapWebGlMaterial(
                modelComponent.customerMaterial()
              );
            }

            return _.merge(material, modelComponent.getData());
          }
        ),
      },
      webGlModel.getData()
    )
  );
}

function mapLodResolution(strategyId) {
  return function (webGlResolution) {
    return Object.freeze(Object.assign(
      {},
      webGlResolution.getData(),
      {
        modelLods: webGlResolution.modelLodsForStrategy(strategyId).map(mapLod)
      }
    ));
  }
}

function mapLod(webgGlLod) {
  return Object.freeze(
    _.merge(
      {
        webGlModel: mapWebGlModel(webgGlLod.webGlModel()),
        lodResolutionIds: webgGlLod.lodResolutions()
          .map(function (lodResolution) { return lodResolution.id; })
      },
      webgGlLod.getData()
    )
  );
}

function mapWebGlMaterial(webGlMaterial) {
  return Object.freeze(
    _.merge(
      {
        platformMaterial: webGlMaterial.platformMaterial().getData(),
      },
      webGlMaterial.getData()
    )
  );
}

function addMetadata(node, entity) {
  if(!node.hasMetadata()) {
    return;
  }
  
  entity.metadata = _.map(node.metadata(), function (metadataItem) {
    var metadataItemData = metadataItem.getData();
    return {
      id: metadataItemData.id,
      key: metadataItemData.key,
      value: metadataItemData.value,
      ns: metadataItemData.ns // required for full text search
    };
  });
}

module.exports.createCAMap = createCAMap;
module.exports.createCaFacet = createCaFacet;
module.exports.createOverrides = createOverrides;

function vertexHasher(node) {
  return node.getVertexKey();
}
