module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * `PurposeView`s is view name that is to be used for particular purpose.
   * It also contain information about view data - 'format', 'scale', 'quality'
   *
   * On the graph their vertices contain the namespace (ns) "ppV"
   *
   * {@link Purpose}
   *
   * @param {Object} options Options
   * @class
   * @name PurposeView
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends PurposeView */{
    ns: "ppV"
  });
};
