// dependency for http requests
// https://www.npmjs.com/package/axios
var http = require("axios");
var _ = require("lodash");
var window = global;
var document = _.get(window, "document");

// this variable is shared among configure core instances, and is used to
// only track the first page view event.
var productTracked = false;

var pageViewEvent = function(fcParams) {
  // We only want to trigger one analytics request per configure instance
  // even if configure-ui re-initialized multiple times, for example
  // when a customer uses lazy on-load availability like LV
  // We only send session information once per configure load.

  var endpoint = "//" + fcParams.configureEndpoint + "/metrics/page_view";

  // We don't want to send the analytics event right after initialization, we
  // want to give the browser time to pull images and other UX critical assets.
  // Delaying the analytics event will also prevent our "New Relic Synthetics"
  // end user performance monitoring from creating session events.
  var analyticsDelay = 10 * 1000;

  manageEvent(analyticsDelay, endpoint, fcParams);

  return {
    isValidEvent: isValidEvent.bind(this),
    buildEvent: buildEvent.bind(this),
    productTracked: productTracked
  }
}

function manageEvent(analyticsDelay, endpoint, fcParams) {
  var event = buildEvent(fcParams);
  if (isValidEvent(event) && !productTracked) {
    productTracked = true;
    sendEvent(
      analyticsDelay,
      endpoint,
      fcParams.apiKey,
      event
    );
  }
}

function buildEvent(fcParams) {
  var event = {
    data_source: fcParams.isBrowser ? "web" : "app",
    customer_id: fcParams.customer,
    product_id: fcParams.productId,
    workflow: fcParams.workflow,
    api_key: fcParams.apiKey,
    session_id: fcParams.sessionId,
    user_language: fcParams.locale || "en_us",
    application_name: fcParams.applicationName || "configure-core",
    application_version: fcParams.applicationVersion
  };
  setWindowParams(event);
  return event;
}

function setWindowParams(event) {
  event["user_agent"] = _.get(window, [ "navigator", "userAgent"]);
  event["document_location_url"] = _.get(window, [ "location", "href"]);
  event["document_referrer"] = _.get(document, [ "referrer" ]);
  event["screen_height"] = _.get(window, [ "screen", "height"]);
  event["screen_width"] = _.get(window, [ "screen", "width"]);
  event["viewport_height"] =
    Math.max(
      _.get(document, [ "documentElement", "clientHeight"]),
      _.get(window, "innerHeight") || 0
    );
  event["viewport_width"] =
    Math.max(
      _.get(document, [ "documentElement", "clientWidth"]),
      _.get(window, [ "location", "innerWidth"]) || 0
    );
}

function sendEvent(analyticsDelay,endpoint, apiKey, event) {
  var options = {
    method: "POST",
    headers: { "x-api-key": apiKey},
    data: {data: event},
    url: endpoint,
  };
  setTimeout(function delayedAnalyticsTracking() {
    http(options);
  }, analyticsDelay);
}

function isValidEvent(event) {
  var isValid = true;
  if (
      !event ||
      !event["data_source"] ||
      !event["customer_id"] ||
      !event["application_name"] ||
      !event["application_version"]
    )
    {
    isValid = false;
  }
  return isValid;
}

module.exports = pageViewEvent;
