var
  _ = require("lodash"),
  lunr = require("lunr");

/**
 * Fluid full-text search
 * Full-text search engine
 */
module.exports = function (options, callback) {

  var
    searchIndex,

    // Public Object/Methods
    searchObject = {

      /**
       * Add the document to the index
       *
       * @param {Json} document
       */
      addDocument: function (document) {
        if (_.isArray(document)) {
          this.addDocuments(document);
        } else {
          searchIndex.add(document);
        }
      },

      /**
       * Add the documents to the index
       *
       * @param {Array} documents
       */
      addDocuments: function (documents) {
        _.each(documents, function (document) {
          searchIndex.add(document);
        });
      },

      /**
       * Perform a search of the query over the stored documents and return
       * its id within an array
       *
       * @param {String} query
       * @param {Function} callback
       * @return {Array} an array with the indexes of the documents
       * that match with the search query
       */
      search: function (query, callback) {
        var searchResult = searchIndex.search(query).map(function (result) {
          return result.ref;
        });

        return handleResponse(null, searchResult, callback);
      }
    };

  if (!options ||
    (options && !options.fields) ||
    (options.fields && _.size(options.fields) === 0)) {

    var err = new Error("You must provide fields options");
    return handleResponse(err, null, callback);
  }

  searchIndex = lunr();

  _.each(options.fields, function (field, boost) {
    searchIndex.field(field.name, {boost: boost});
  });

  if (options.ref) {
    searchIndex.ref(options.ref);
  }

  return handleResponse(null, searchObject, callback);
};

function handleResponse(err, result, callback) {
  if (callback && _.isFunction(callback)) {
    callback(err, result);
  } else {
    if (err) {
      throw err;
    }
    return result;
  }
}
