var _ = require("lodash");

module.exports = function (engineOptions) {

  var BaseModel = require("./base")(engineOptions);

  /**
   * Configurable Product.
   *
   * The configurable product is the starting point when working
   * with configure.
   *
   * On the graph it's represented as the vertex with the namespace (ns) "p",
   * which is the first element of the vertices array.
   *
   * @class
   * @name ConfigurableProduct
   * @constructor
   * @param {Object} options Options
   * @param {number} options.id Product numeric id.
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends ConfigurableProduct */{

    ns: "p",

    /**
     * Gets a collection of configurable attribute instances defined
     * at the root of the product.
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    rootAttributes: function () {
      return this.traverser()
        .outgoing("hasAttribute")
        .instances();
    },

    /**
     * Finds a single configurable attribute using the search criteria
     * specified as the search parameter
     *
     * @instance
     * @param {Object} query - The object of property values to filter by
     * @return {ConfigurableAttribute}
     */
    findAttribute: function (query) {
      return this.find({
        relationship: "hasAttribute",
        query: query,
        recursive: true
      })
      .where({ns: "ca"})
      .instance();
    },

    /**
     * Finds a single configurable attribute by its id
     *
     * @instance
     * @param  {number} id Attribute numeric primary key
     * @return {ConfigurableAttribute}
     */
    attributeById: function (id) {
      return this.traverser().getVertexByKey("ca:" + id);
    },

    /**
     * Finds a single facet given its id
     *
     * @instance
     * @param  {number} id Facet numeric primary key
     * @return {Facet}    Facet instance
     */
    facetById: function (id) {
      return this.traverser().getVertexByKey("f:" + id);
    },

    /**
     * Finds a single vertex given its id
     *
     * @instance
     * @param  {vertexKey} key Vertex key such as ca:123
     * @return {VertexInstance}
     * @private
     */
    vertexByKey: function (key) {
      return this.traverser().getVertexByKey(key);
    },


    /**
     * Gets a all the attributes available for a product including
     * subattributes
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    allAttributes: function () {
      return this.traverser()
        .outgoing("hasAttribute", true)
        .instances();
    },

    /**
     * Gets the text attributes available for a product
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    getTextAttributes: function () {
      return _.filter(this.getDynamicAttributes(), function (dynamicCa) {
        var av = dynamicCa.getDynamicAttributeValue();
        var avData = av.getData();
        return _.has(avData, "text");
      });
    },
    /**
     * Gets a all the dynamic attributes available for a product
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    getDynamicAttributes: function () {
      var self = this;
      return this.getAndCache("dynamicAttributes", function () {
        return _.filter(self.allAttributes(), function (ca) {
          return ca.hasDynamicValue();
        });
      });
    },

    /**
     * Gets view names for particular purpose
     *
     * {@link Purpose}
     * {@link PurposeView}
     *
     * @instance
     * @param  {string} purposeName name of the purpose
     * @returns {string[]} purposeViews list of view names defined fo
     * a particular purpose
     */
    getViewsForPurpose: function (purposeName) {
      var purpose = this
        .traverser()
        .outgoing("hasPurpose")
        .where({ name: purposeName })
        .instance();
      if (!purpose) {
        throw new Error(
            "Purpose '" + purposeName + "' is not defined for the product"
        );
      }
      return purpose
        .traverser()
        .outgoing("hasPurposeView")
        .all()
        .map(function (v) { return v.name; })
        // expected to be in this order for legacy reasons
        .sort();
    },

    /**
     * Gets a all the attributes available for a product except the ones that
     * can be used as sympathetic filter slaves
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    nonSympatheticSlaveAttributes: function () {
      return _.filter(this.allAttributes(), function (ca) {
        return !(ca.isSympatheticFilterSlave() || ca.isSympatheticMatchSlave());
      });
    },

    /**
     * Attributes that do not have a UI selector.
     *
     * @instance
     * @returns {Array<ConfigurableAttribute>}
     */
    nonSelectableAttributes: function () {
      return _.filter(this.allAttributes(), function (ca) {
        return !ca.hasSelector();
      });
    },

    /**
     * Some products group attributes into logical groups. This method
     * can tell us if the product contains such groups.
     *
     * @instance
     * @returns {boolean} Does this product contains attribute groups?
     */
    hasAttributeGroups: function () {
      return this.traverser()
        .outgoingSize("hasAttributeGroup") > 0;
    },

    /**
     * Gets a collection of attribute groups for the product
     *
     * @instance
     * @return {Array<AttributeGroup>}
     */
    attributeGroups: function () {
      return this.traverser()
        .outgoing("hasAttributeGroup")
        .instances();
    },

    /**
     * Sets the recipe data
     *
     * @instance
     * @param {Array} data    Recipe [[ca, av]] pair vertex instance ids.
     * @return {Recipe}
     */
    setRecipe: function (data) {
      return this.recipe().set(data);
    },

    /**
     * Get the default view for the product.
     *
     * @instance
     * @return {String} The ID of the view
     */
    defaultViewName: function () {
      var view = this.traverser()
        .outgoing("displaysView")
        .instance();

      if (view) {
        return view.getData().name;
      }
    },

    /**
     * Gets the product price in a given currency minus its discounted /
     * save price.
     *
     * @instance
     * @param  {String} currency Currency code
     * @return {Number}          Product price
     */
    price: function (currency) {

      if (!_.contains(this.availableCurrencies(), currency)) {
        throw new Error("The currency \"" + currency +
          "\" is not available for the product " + this.id +
          ". Valid currencies are: " +
          (this.availableCurrencies().join(", ")));
      }

      var config = this.getData().priceConfiguration,
        basePrice = config.basePrice,
        savePrice = config.savePrice;

      if (_.isObject(basePrice)) {
        basePrice = basePrice[currency];
      }

      if (_.isObject(savePrice)) {
        savePrice = savePrice[currency];
      }

      return (+basePrice) - (+savePrice);
    },

    /**
     * Gets a list of available currencies for this product.
     *
     * @instance
     * @return {Array<String>} List of currency codes for this product
     */
    availableCurrencies: function () {
      var self = this;
      return this.getAndCache("currencies", function () {
        return self.getData().currencies;
      }) || [];
    },

    /**
     * Get the default currency for the product.
     *
     * @instance
     * @return {String} Default currency for the product.
     */
    defaultCurrency: function () {
      var self = this;
      return this.getAndCache("defaultCurrency", function () {
        return self.getData().currencySettings["default"];
      });
    },

    /**
     * Gets the settings for a given currency defined on the product.
     *
     * @instance
     * @return {Object} Formatting and symbol details for the currency with the
     *                  following structure:
     *
     *     {
     *       "symbol": "$",
     *       "format": {
     *         "pos": "%s%v",
     *         "neg": "%s-%v",
     *         "zero": "%s%v"
     *       }
     *     }
     */
    currencySettings: function (currency) {
      var self = this;
      currency = currency || this.defaultCurrency();
      return this.getAndCache("settingsForCurrency" + currency, function () {
        var result = self.getData().currencySettings.symbols[currency];
        if (!result) {
          throw new Error("Could not find settings for the currency " +
            currency + ". The following currencies are available on this " +
            "product: " + self.availableCurrencies().join(", "));
        }
        return result;
      });
    },

    /**
     * Gets `ProductFacet`s.
     *
     * Product facets are not used to drive configures behavior but we need
     * to expose them to implementors via the public API
     *
     * @instance
     * @return {Array<ProductFacet>}
     */
    facets: function () {
      return this.traverser()
        .outgoing("hasCustomFacets")
        .instances();
    },

    /**
     * Updates all sympathetic filters defined on this product.
     *
     * @instance
     */
    updateSympatheticFilters: function () {
      _.each(this.allAttributes(), function (ca) {
        if (ca.hasSympatheticFilters()) {
          ca.updateSympatheticFilters();
        }
      });
    },


    /**
     * Get recipe url template.
     *
     * @return {String}
     */
    recipeUrlTemplate: function () {
      var self = this;
      return this.getAndCache("recipeUrlTemplate", function () {
        return self.getData().recipeUrlTemplate;
      });
    },


    /**
     * Removes existing {@link DynamicAttributeValue} instances from
     * {@link ConfigurableAttribute}s.
     *
     * @see {@link ConfigurableAttribute#unsetDynamicCache}
     */
    unsetDynamicCaDefaults: function () {
      _.each(this.allAttributes(), function (ca) {
        if (ca.hasDynamicValue()) {
          ca.unsetDynamicCache();
        }
      });
    },

    /**
     * Does this Configurable Product has WebGlModels?
     *
     * @instance
     * @return {Boolean}
     */
    hasWebGlModels: function () {
      return this.traverser()
        .outgoingSize("hasModel") > 0;
    },

    /**
     * Gets product webGl models.
     *
     * Models are used to render a product inside configure-ui
     *
     * @instance
     * @return {Array<WebGlModel>}
     */
    webGlModels: function () {
      return this.getAndCache("webGlModels", function () {
        return this.traverser()
          .outgoing("hasModel")
          .instances();
      })
    },

    /**
     * Gets product webGl loading strategies.
     *
     * @instance
     * @param {Object} query - The object of property webgl strategy to filter by
     * @return {Array<WebGlLoadingStrategy>}
     */
    webGlLoadingStrategies: function (query) {
      if (!query) {
        return this.getAndCache("webGlLoadingStrategies", function () {
          return this.traverser()
            .outgoing("hasLoadStrategy")
            .instances();
        })
      }
      var queryToUse = query;
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        queryToUse = {id: +query};
      }
      return this.find({
        relationship: "hasLoadStrategy",
        query: queryToUse,
      })
        .where({ns: "wload"})
        .instances()

    },

    /**
     * Gets product webGl LODs.
     *
     * @instance
     * @param {Object} query - The object of property WebGlModelLod to filter by
     * @return {Array<WebGlModelLod>}
     */
    webGlModelLods: function (query) {
      if (!query) {
        return this.getAndCache("productWebGlModelLods", function () {
          return this.webGlModels().reduce(function (acc, webGlModel) {
            return acc.concat(webGlModel.lods())
          }, []);
        })
      }
      return this.webGlModels().reduce(function (acc, webGlModel) {
        return acc.concat(webGlModel.findLod(query)).filter(function (lod) {return !!lod})
      }, []);

    },

    /**
     * Gets product webGl LOD Resolutions.
     *
     * @instance
     * @param {Object} query - The object of property WebGlLodResolution to filter by
     * @return {Array<WebGlLodResolution>}
     */
    webGlLodResolutions: function (query) {
      if (!query) {
        return this.getAndCache("productWebGlLodResolutions", function () {
          return this.webGlLoadingStrategies().reduce(function (acc, webGlStrategy) {
            return acc.concat(webGlStrategy.lodResolutions())
          }, []);
        })
      }
      return this.webGlLoadingStrategies().reduce(function (acc, webGlStrategy) {
        return acc.concat(webGlStrategy.findLodResolution(query)).filter(function (lod) {return !!lod})
      }, []);

    },

    /**
     * Finds a single WebGlLoadingStrategy using the search criteria
     * specified as the search parameter
     *
     * @instance
     * @param {Object} query - The object of property webgl model to filter by
     * @return {WebGlLoadingStrategy}
     */
    findWebGlLodStrategy: function (query) {
      var queryToUse = query;
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        queryToUse = {id: +query};
      }
      return this.find({
        relationship: "hasLoadStrategy",
        query: queryToUse,
      })
        .where({ns: "wload"})
        .instance()
    },

    /**
     * Finds a single WebGlModel using the search criteria
     * specified as the search parameter
     *
     * @instance
     * @param {Object} query - The object of property webgl model to filter by
     * @return {WebGlModel}
     */
    findWebGlModel: function (query) {
      var queryToUse = query;
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        queryToUse = {id: +query};
      }
      return this.find({
        relationship: "hasModel",
        query: queryToUse,
      })
        .where({ns: "wm"})
        .instance()
    },

    /**
     * Does this Configurable Product have WebGl Materials?
     *
     * @instance
     * @return {Boolean}
     */
    hasWebGlMaterials: function () {
      return this.traverser()
        .outgoingSize("hasMaterial") > 0;
    },

    /**
     * Gets product WebGl Materials.
     *
     * WebGl Materials are used to apply textures and properties to WebGl Models
     *
     * @instance
     * @return {Array<WebGlCustomerMaterial>}
     */
    webGlMaterials: function () {
      return this.getAndCache("webGlMaterials", function () {
        return this.traverser()
          .outgoing("hasMaterial")
          .instances();
      })
    },

    /**
     * Finds a single WebGl Material using the search criteria
     * specified as the search parameter
     *
     * @instance
     * @param {Object} query - The object of property webgl material to filter by
     * @return {WebGlCustomerMaterial}
     */
    findWebGlMaterial: function (query) {
      var queryToUse = query;
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        queryToUse = {id: +query};
      }
      return this.find({
        relationship: "hasMaterial",
        query: queryToUse,
      })
        .where({ns: "wma"})
        .instance()
    },

    /**
     * Checks if product has custom WebGl settings like camera or scene.
     *
     * @return {Boolean}
     */
    hasWebGlExtraSettings: function () {
      return !!this.getData().webGlExtraSettings;
    },

    /**
     * Returns custom WebGl settings like camera or scene.
     *
     * @return {Object}
     */
    getWebGlExtraSettings: function () {
      return this.getData().webGlExtraSettings || null
    },

    /**
     * Does this Configurable Product has WebGlEnvironment?
     *
     * @instance
     * @return {Boolean}
     */
    hasWebGlEnvironment: function () {
      return this.traverser()
        .outgoingSize("hasWebGlEnvironment") > 0;
    },

    /**
     * Gets product webGl environments.
     *
     * WebGlEnvironment contains camera or scene settings for configure-webgl
     *
     * @instance
     * @return {Array<WebGlEnvironment>}
     */
    webGlEnvironment: function () {
      return this.getAndCache("webGlEnvironment", function () {
        return this.traverser()
          .outgoing("hasWebGlEnvironment")
          .instance();
      })
    },

  });
};
