var
  _ = require("lodash"),
  i = require("icepick"),
  createCA = require("./create_product_json").createCA;

/**
 * iterate  through all CAs on the engine and update the isVisible flags in the
 * produc JSON, if the visibility for the CA has changed.
 */
module.exports = function updateVisbilityJson(engine, json) {
  return _.reduce(json.allAttributes, function (newJson, path, caId) {
    var product = engine.product(),
      caNode = product.attributeById(caId),
      ca = i.getIn(json, path),
      newVisibility = caNode.isVisible(),
      newRenderability = newVisibility && caNode.canBeRendered(),
      renderInactive = !!product.getData().renderInactive;

    if (newVisibility !== ca.isVisible) {
      newJson = i.updateIn(newJson, path, function () {
        // CD-3125 Is not enough updating ca visibility.
        // We also need to update selectable avs since a sympathetic matches
        // before an attribute is made visible won't trigger
        // a change event that'll trigger attribute changes on the product JSON
        return createCA(caNode, renderInactive);
      });
    }

    if (newRenderability !== ca.canBeRendered) {
      newJson = i.updateIn(newJson, path, function (ca) {
        return i.assoc(ca, "canBeRendered", newRenderability);
      });
    }

    return newJson;
  }, json);
};
