/* eslint-disable max-len */
var eventify = require("eventify");
var _ = require("lodash");
var clone = require("./clone_changes");
var createProductJson = require("./create_product_json");
var updateAttributeJson = require("./update_attribute_json");
var updateVisibilityJson = require("./update_visibility_json");
var applyParameterizedFacetFilters =
  require("./apply_parameterized_facet_filters");
var setEnvironmentParams = require("../set_environment_params");

/**
 * When a recipe:change event is received from the configuration engine,
 * the first ca on the modifications list should be the first one
 * that was changed, which should also reflect the explicit user selection.
 * @param  {Array} modifications Recipe modifications [[ca, av]] array
 * @return {String}               View name
 */
function getViewNameFromModifiedAttributes(modifications) {
  var changedCa = modifications && modifications[0] && modifications[0].ca;

  if (changedCa && changedCa.viewName) {
    return changedCa.viewName();
  }
}

module.exports = function getApi(userFcParams, engine) {

  var fcParams = setEnvironmentParams(userFcParams);
  var methods, productJson;
  var api = {};
  var recipe = engine.recipe(!fcParams.skipSettingRecipeAsDefault);
  var userEdited = {
    cas: {},
    facets: {}
  };

  // Parameterized facets contain facets that are pre-selected
  // based on the contents of fcParams.facetFilter
  // and a URL parameter. Any event triggered by these changes
  // is irrelevant and should not be broadcasted. The selection
  // needs to be part of the initial product JSON as we won't
  // be able to update them as regular facets
  // after the product has been loaded.
  applyParameterizedFacetFilters(fcParams, engine);

  var getCurrency = require("./get_currency")(fcParams, engine);

  try {
    // The main product reference used for rendering will be kept here.
    // Further updates will modify this object.
    productJson = createProductJson(engine, getCurrency());
  } catch (e) {
    e.message = "Failed creating product JSON on the configure-core. " +
      "Got error " + e.message;
    throw e;
  }

  // The currently selected quantity
  var quantity = 1;

  var collectionEmitter = {};

  eventify.enable(collectionEmitter);

  function getProduct() {
    return productJson;
  }

  function updateAttributeModifications(modifications) {
    modifications.forEach(function (mod) {
      if (mod.facets && mod.facets.length) {
        _.map(mod.facets, function (f) {
          productJson = updateAttributeJson(
            engine, productJson, mod, [f], userEdited
          );
        });
      } else {
        productJson = updateAttributeJson(
          engine, productJson, mod, [], userEdited
        );
      }
    });
    productJson = updateVisibilityJson(engine, productJson);
    return modifications;
  }

  // Whenever a change or reset event, we'll update the productJson
  // with the changed ca/av's, We'll reuse the same logic for both change events
  function updateFacetModifications(facetMods) {
    _.each(facetMods, function (mod) {
      productJson = updateAttributeJson(
        engine, productJson, mod, [mod.f], userEdited
      );
    });
    productJson = updateVisibilityJson(engine, productJson);
  }

  collectionEmitter.on("change", function handleCollectionChange(ca) {
    productJson = updateAttributeJson(engine, productJson, {ca: ca}, [],
      userEdited);
  });

  recipe.on("change", function handleRecipeChange(modifications) {
    modifications = updateAttributeModifications(modifications);

    /**
     * @publicDocs events/recipe:change ../../docs/recipe_change_event.md
     */
    api.trigger("recipe:change", clone(modifications), {
      viewName: getViewNameFromModifiedAttributes(modifications)
    });
  });

  recipe.on("reset", function handleRecipeChange(modifications) {
    var viewName;

    userEdited.cas = {};
    userEdited.facets = {};
    userEdited.reset = true;

    modifications = updateAttributeModifications(modifications);
    userEdited.reset = false;

    viewName = getViewNameFromModifiedAttributes(modifications) ||
      productJson.defaultViewName;

    modifications = clone(modifications);

    /**
     * @publicDocs events/recipe:reset ../../docs/recipe_reset_event.md
     */
    // When a recipe is reset the API will trigger both, reset and change events
    api.trigger("recipe:reset", modifications, {viewName: viewName});

    if (modifications.length > 0) {
      api.trigger("recipe:change", modifications, {viewName: viewName});
    }
  });

  /**
   * @publicDocs events/facet:change ../../docs/facet_change_event.md
   */
  recipe.on("change:facet", function (facetMods) {

    updateFacetModifications(facetMods);

    api.trigger("facet:change", clone(facetMods));
  });

  recipe.on("load", function (data, recipeDocument) {

    updateAttributeModifications(data);

    /**
     * @publicDocs events/recipe:loaded ../../docs/recipe_loaded_event.md
     */
    api.trigger("recipe:loaded", clone(data), recipeDocument);
  });

  recipe.on("randomize", function (modifications) {
    /**
     * @publicDocs events/recipe:randomize ../../docs/recipe_randomize_event.md
     */
    api.trigger("recipe:randomize", modifications);
  });

  methods = {
    /**
     * @publicDocs methods/getFcParams ../../docs/get_fc_params.md
     */
    getFcParams: function () {
      return userFcParams;
    },
    getConfig: require("./get_config")(engine),

    /**
     * @publicDocs methods/getProduct ../../docs/get_product.md
     */
    getProduct: function () {
      return productJson;
    },
    getBrowserData: require("./get_browser_data")(),
    isWebGl: function () {
      return productJson.isWebGl
    },
    analytics: require("./analytics")(fcParams),
    getAttribute: require("./get_attribute")(engine, getProduct),
    getAttributes: require("./get_attributes")(engine, getProduct),

    getRecipe: require("./get_recipe")(engine, getProduct),
    getCustomerReport: require("./get_customer_report")(engine, fcParams),
    selectValue: require("./select_value")(engine, userEdited, api),
    previewValue: require("./preview_value")(engine, api),
    selectFacetValue:
      require("./select_facet_value")(engine, userEdited),
    getProductFacetsDetails: require("./get_product_facets_details")(engine),
    getUpcharges: require("./get_upcharges")(engine, api),
    getCurrency: getCurrency,

    validateText: require("./validate_text")(fcParams),
    filterText: require("./filter_text")(engine),
    formatPrice: require("./format_price")(engine, fcParams),

    _setAttributeCollection:
      require("./set_attribute_collection")(engine, collectionEmitter),

    _indexProduct: require("./index_product")(engine, getProduct),
    _indexConfigurableAttribute:
      require("./index_configurable_attribute")(engine, getProduct),
    _indexConfigurableAttributeMetadata:
      require("./index_configurable_attribute_metadata")(engine, getProduct),

    _searchProduct: require("./search_in_index")(engine, "p"),
    _searchConfigurableAttribute: require("./search_in_index")(engine, "ca"),
    _searchAttributeValue: require("./search_in_index")(engine, "av"),
    _searchFacetValue: require("./search_in_index")(engine, "fv"),
    _searchMetadata: require("./search_in_index")(engine, "md"),

    loadRecipe: require("./load_recipe")(engine, fcParams),
    resetRecipe: require("./reset_recipe")(engine),
    saveRecipe: require("./save_recipe")(engine, fcParams, api),
    setRecipe: require("./set_recipe")(engine, api),
    updateRecipe: require("./update_recipe")(engine, api),
    setFallbackRecipe: require("./set_fallback_recipe")(engine, api),
    randomizeRecipe: require("./randomize_recipe")(engine),

    _getItemNumber: require("./get_item_number")(engine),
    _getSessionId: require("./get_session_id")(engine),

    /**
     * @publicDocs methods/getQuantity ../../docs/get_quantity.md
     */
    getQuantity: getQuantity,

    /**
     * @publicDocs methods/setQuantity ../../docs/set_quantity.md
     */
    setQuantity: function (num) {
      if (typeof num === "undefined" || typeof num !== "number") {
        throw new Error("Invalid arguments for setQuantity(number)");
      }
      quantity = +num;
      /**
       * @publicDocs events/quantity ../../docs/quantity_event.md
       */
      api.trigger("quantity", num);
    },

    getPrice: require("./get_price")(engine, fcParams, getQuantity),

    /**
     * @publicDocs methods/getWebGlModelLod ../../docs/get_webgl_model_lod.md
     */
    getWebGlModelLod: require("./get_webgl_model_lod")(engine, getProduct),

    /**
     * @publicDocs methods/getWebGlModelLods ../../docs/get_webgl_model_lods.md
     */
    getWebGlModelLods: require("./get_webgl_model_lods")(engine),

    /**
     * @publicDocs methods/getWebGlModelResolutions ../../docs/get_webgl_model_resolutions.md
     */
    getWebGlModelResolutions: require("./get_webgl_model_resolutions")(engine),


    /**
     * @publicDocs methods/getWebGlLoadingStrategies ../../docs/get_webgl_loading_strategies.md
     */
    getWebGlLoadingStrategies: require("./get_webgl_loading_strategies")(getProduct),
    /**
     * @publicDocs methods/getWebGlModel ../../docs/get_webgl_model.md
     */
    getWebGlModel: require("./get_webgl_model")(engine, getProduct),
    /**
     * @publicDocs methods/getWebGlModels ../../docs/get_webgl_models.md
     */
    getWebGlModels: require("./get_webgl_models")(getProduct),
    /**
     * @publicDocs methods/getWebGlMaterial ../../docs/get_webgl_material.md
     */
    getWebGlMaterial: require("./get_webgl_material")(engine, getProduct),
    /**
     * @publicDocs methods/getWebGlModels ../../docs/get_webgl_materials.md
     */
    getWebGlMaterials: require("./get_webgl_materials")(getProduct),

    /**
     * @publicDocs methods/getWebGlLoadingStrategies ../../docs/get_webgl_extra_settings.md
     */
    getWebGlExtraSettings: require("./get_webgl_extra_settings")(engine),

    // This method is documented on the configure-ui public API
    destroy: function (callback) {
      try {
        api.off();
        engine.destroy();
      } catch (e) {
        if (callback) { return callback(e); }
      }
      if (callback) {
        _.defer(callback);
      }
    },

    _explain: function (vertexKey) {
      return engine.explain(vertexKey);
    },
  };

  _.extend(api, methods);

  eventify.enable(api);

  return api;

  function getQuantity() {
    return quantity;
  }
};
