/**
 * @publicDocs methods/getWebGlModelLod ../../docs/get_webgl_model_lod.md
 */

module.exports = function (engine) {
  var product = engine.product();

  return function getWebGlModelLod(query) {
    var modelLods = product.webGlModelLods(query);

    if (!modelLods || !modelLods.length) {
      throw new Error(
        "getWebGlModelLod could not find a WebGl model LOD matching " +
          JSON.stringify(query) +
          ". Available model LODs are: " +
          product
            .webGlModelLods()
            .map(function (lod) {
              return lod.getData().fileName + "_" + lod.getData().qualityType;
            })
            .join(", ")
      );
    }

    const modelLodNode = modelLods[0];
    return Object.freeze(
      Object.assign({}, modelLodNode.getData(), {
        webGlModelId: modelLodNode.webGlModel() && modelLodNode.webGlModel().id,
      })
    );
  };
};
