/**
 * @publicDocs methods/getWebGlMaterials ../../docs/get_webgl_materials.md
 */

var _ = require("lodash"),
  validateFilter = require("../util").validateFilter;

module.exports = function (getProductJson) {
  /**
   * Gets a collection of WebGl material for the current Product.
   *
   * @param  {Object} options - Options object
   * @param  {Object} options.filter - Filters the attributes using
   *                                 [_.filter](https://lodash.com/docs#filter)
   * @returns {Array<Object>} An array of WebGl Material objects
   */
  return function getWebGlMaterials(options) {
    var webGlMaterials;

    options = options || {};

    validateOptions(options);

    webGlMaterials = Object.values(getProductJson().webGlMaterials);

    return (
      (options.filter && _.filter(webGlMaterials, options.filter)) ||
      webGlMaterials
    );
  };

  function validateOptions(options) {
    if (!_.isPlainObject(options)) {
      throw new Error("getWebGlMaterials options should be an Object");
    }

    validateFilter(options.filter);
  }
};
