module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * {SympatheticFilterSlave}s define {SympatheticFilter} relationships.
   *
   * On the graph their vertices contain the namespace (ns) "sfs"
   *
   * @param {Object} options Options
   * @param {number} options.id SympatheticFilterSlave numeric id.
   * @class
   * @name SympatheticFilterSlave
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends SympatheticFilterSlave */{
    ns: "sfs",

    /**
     * Gets the {FacetValue} for the {@link SympatheticFilterSlave}
     * @instance
     * @return {FacetValue}
     */
    facet: function () {
      return this.traverser()
        .outgoing("isSlave")
        .where({ns: "f"})
        .instance();
    },

    /**
     * Gets the potential {FacetValue} to enforce on this
     * {ConfigurableAttribute}
     * @instance
     * @return {FacetValue}
     */
    potentialFacetValue: function () {
      return this.traverser()
        .outgoing("isSlave")
        .where({ns: "fv"})
        // sfs's always have a single value
        // TODO: investigate why this is not true for Oakley
        .instance();
    },

    /**
     * Gets the {SympatheticFilter} that mediates the current rule
     * @instance
     * @return {SympatheticFilter}
     */
    rule: function () {
      return this.traverser()
        .outgoing("hasSympatheticRule")
        .instance();
    }
  });
};
