/**
 * @publicDocs methods/resetRecipe ../../docs/reset_recipe.md
 */

var
  _ = require("lodash");

module.exports = function (engine) {

  return function resetRecipe(callback) {
    if (!callback || (callback && !_.isFunction(callback))) {
      throw new Error("Invalid arguments for resetRecipe(callback)");
    }

    var
      recipe = engine.recipe();

    recipe.reset();
    callback();
  };
};
