var _ = require("lodash");

/**
 * this Function is to avoid track metrics on demo pages
 * localhost is allowed because send events to dev.
*/
function shouldTrackMetricsEvents(url) {
  /* eslint-disable max-len */
  var ignoredPagesRegex = /^https?:\/\/[a-z-.]*((fluidconfigure\.(com|cn))|(configureid-ref-impl)|(fluidretail.net)|\d+\.\d+\.\d+\.\d+|s3\.amazonaws\.com)/;
  return !ignoredPagesRegex.test(url);
}

function isEmptyObject(eventData) {
  var isEmpty = false;

  if (!eventData || isEmptyKeys(eventData)) {
    isEmpty = true
  }
  return isEmpty;
}

function isEmptyKeys(objectToValidate) {
  return (
      Object.keys(objectToValidate).length === 0 &&
      objectToValidate.constructor === Object
    );
}

function validateFilter(filter) {
  if (!filter) {
    return;
  }
  if (process.env.NODE_ENV !== "production") {
    var isValid = _.isFunction(filter) || _.isPlainObject(filter);

    if (!isValid) {
      throw new Error("filter parameter in method requires an object or " +
        "a function. Please check https://lodash.com/docs#filter for details");
    }
  }
}

// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Object/freeze
function deepFreeze(object) {
  // Retrieve the property names defined on object
  var propNames = Object.getOwnPropertyNames(object);

  // Freeze properties before freezing self
  for (var i = 0; i < propNames.length; i += 1) {
    var name = propNames[i];
    var value = object[name];
    if (value && typeof value === "object") {
      deepFreeze(value);
    }
  }

  return Object.freeze(object);
}

module.exports = {
  shouldTrackMetricsEvents: shouldTrackMetricsEvents,
  isEmptyObject: isEmptyObject,
  validateFilter: validateFilter,
  deepFreeze: deepFreeze
};
