/*eslint max-params: ["error", 5]*/

var _ = require("lodash");
var i = require("icepick");
var createOverrides = require("./create_product_json").createOverrides;

module.exports = updateAttribute;

function updateAttribute(engine, productJson, change, facets, userEdited) {
  var ca = change.ca;
  var av = change.av;

  var caPath = productJson.allAttributes[ca.id],
    selectedAv = ca.selectedValue(),
    avId = selectedAv && selectedAv.id;

  userEdited = userEdited || {};

  return i.updateIn(productJson, caPath, function (ca) {

    var caNode = engine.vertexInstance({
      vertexKey: "ca:" + ca.id
    });

    ca = updateCaUserInteraction(ca, userEdited);

    if (caNode.hasDynamicValue()) {
      return updateDynamicAttribute(ca, caNode, av);
    }

    ca = updateFacets(ca, {
      caNode: caNode,
      facets: facets,
      userEdited: userEdited
    });

    return updateAllValues(ca, avId, caNode);
  });
}



function updateTextAttribute(caNode, ca, av) {
  var value = av.getData();
  return i.assocIn(ca, ["value", "text"], value.text);
}

function updateUgcAttribute(caNode, ca, av) {
  return i.updateIn(ca, ["value"], function (prevVal) {
    return i.merge(prevVal, av.getData());
  });
}


function updateDynamicAttribute(ca, caNode, av) {
  var dynamicAv = caNode.getDynamicAttributeValue();
  var collection = dynamicAv.getCollection();
  ca = i.assocIn(ca, ["values"], collection);

  // When un-setting an av, if the ca is no longer visible
  // selectedValue() will not contain the information required
  // to reset dynamic attributes. In this case the changes
  // returned by the engine will contain the fallback av which will
  // be a DynamicAttribute instance. CD-2443
  var selectedAv = caNode.selectedValue() || av;
  var data = selectedAv.getData();
  if (_.has(data, "text")) {
    return updateTextAttribute(caNode, ca, selectedAv);
  } else if (_.has(data, "clipArt") && data.clipArt != null) {
    return updateUgcAttribute(caNode, ca, selectedAv);
  } else {
    return ca;
  }
}

function updateAllValues(ca, avId, caNode) {
  // re create all the values overridden (PE-185)
  function updateValuesOverride(ca, caNode) {
    ca = _.clone(ca);
    createOverrides(caNode, ca);
    i.freeze(ca);
    return ca;
  }
  // this function needs to stay within updateAllValues for accessing
  // the enclosed values
  function updateValuesOnCa(ca, listName, valueList) {
    return i.updateIn(ca, [listName], function (prevAVs) {
    // if it is overrides we need to recreate the list of overrides on the ca
    // because the renderInactives property (PE-185)
      return i.freeze(_.map(prevAVs, function (av) {
        var selectable = !!valueList[av.id];
        if (av.selected) {
          av = i.assoc(av, "selected", false);
        }
        if (av.id === avId) {
          av = i.assoc(av, "selected", true);
        }
        if (selectable !== av.selectable) {
          av = i.assoc(av, "selectable", selectable);
        }
        return av;
      }));
    });
  }

  // Builds a map of ids thats easy to check for existence against; like:
  // {
  //   1234: true,
  //   ...
  // }
  function valueReducer(set, av) {
    set[av.id] = true;
    return set;
  }

  // Only update the list of valuesWithOverrides if it
  // exists already
  if (ca.valuesWithOverrides) {
    ca = updateValuesOverride(ca, caNode);
  }

  // The "values" property will always be present on the ca, so we're always
  // going to update it
  var selectableValues = _.reduce(
    caNode.selectableValues() || [],
    valueReducer,
    {}
  );
  return updateValuesOnCa(ca, "values", selectableValues);
}

function updateFacets(ca, options) {
  ca = updateFacetsByType("allFacets", ca, options);
  ca = updateFacetsByType("facets", ca, options);
  ca = updateFacetsByType("optionalFacets", ca, options);
  return updateFacetsByType("hiddenFacets", ca, options);
}

function updateFacetsByType(type, ca, options) {
  var caNode = options.caNode;
  var facets = options.facets;
  var userEdited = options.userEdited;

  ca = _.reduce(facets, function (newCa, facetNode) {

    var selectedFv = caNode.selectedFacetValue(facetNode),
      facet = _.find(newCa[type], {id: facetNode.id});

    // !facet: probably a hiddenFacet, we don't show it in the UI
    if (!facet) {
      return newCa;
    }

    var facetIdx = _.findIndex(newCa[type], {id: facetNode.id});

    if (!selectedFv &&
      (
        type === "optionalFacets" ||
        type === "hiddenFacets" ||
        type === "allFacets" ||
        type === "facets"
      )) {
      // we are setting the "all" value;
      selectedFv = {id: -1};
    }

    newCa = updateFacetUserInteraction(
      newCa,
      type,
      facetIdx,
      userEdited,
      facetNode
    );

    _.each(facet.facetValues, function (fv, fvIndex) {

      if (fv.id === selectedFv.id && !fv.selected) {
        newCa = i.assocIn(newCa, [
          type,
          facetIdx,
          "facetValues",
          fvIndex,
          "selected"
        ], true);
      } else if (fv.selected) {
        newCa = i.assocIn(newCa, [
          type,
          facetIdx,
          "facetValues",
          fvIndex,
          "selected"
        ], false);
      }
    });

    return newCa;
  }, ca);
  return ca;
}

function updateCaUserInteraction(ca, userEdited) {
  if (userEdited.reset) {
    ca = i.set(ca, "userEdited", false);
    ca = i.set(ca, "userChanged", false);
    return ca;
  }

  if (!ca.userChanged) {
    ca = i.set(ca, "userChanged", true);
  }

  if (!ca.userEdited && userEdited.cas && userEdited.cas[ca.id]) {
    ca = i.set(ca, "userEdited", true);
  }

  return ca;
}

function updateFacetUserInteraction(ca, type, idx, userEdited, facetNode) {

  if (userEdited.reset) {
    ca = i.assocIn(ca, [type, idx, "userChanged"], false);
    ca = i.assocIn(ca, [type, idx, "userEdited"], false);
    return ca;
  }

  ca = i.assocIn(ca, [type, idx, "userChanged"], true);

  if (userEdited.facets && userEdited.facets[facetNode.id]) {
    ca = i.assocIn(ca, [type, idx, "userEdited"], true);
  }

  return ca;

}
