module.exports = function (options) {

  var BaseModel = require("./base")(options);

  /**
   * Attribute Group.
   *
   * Attribute Groups aggregate {@link ConfigurableAttribute}s into logical
   * groups. The groups are normally used to render products with large
   * configurable attribute sets using a drill down strategy.
   *
   * Attribute groups can also be used to group configurable product views.
   *
   * On the graph their vertices contain the namespace (ns) "ag"
   *
   * @param {Object} options Options
   * @param {number} options.id Attribute Group numeric id.
   * @class
   * @name AttributeGroup
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends AttributeGroup */{
    ns: "ag",

    /**
     * Gets a collection of configurable attributes included on
     * this group.
     *
     * @instance
     * @return {Array<ConfigurableAttribute>}
     */
    attributes: function () {
      var self = this;
      return this.getAndCache("attributes", function () {
        return self.traverser()
          .outgoing("hasAttribute")
          .instances();
      });
    },

    /**
     * Gets the view instance for this group.
     *
     * @instance
     * @return {View}
     */
    view: function () {
      var self = this;
      return this.getAndCache("view", function () {
        return self.traverser()
          .outgoing("displaysView")
          .instance();
      });
    }
  });
};
