/* eslint complexity:0 */

/**
 * @publicDocs methods/selectFacetValue ../../docs/select_facet_value.md
 */

var
  _ = require("lodash");

module.exports = function (engine, userEdited) {

  userEdited = userEdited || {};

  // options = [ca, f, fv] or {ca: *, f: *, fv: *}
  return function selectFacetValue(/* options, *callback */) {

    if (arguments.length === 0) {
      throw new Error("Missing arguments for selectFacetValue" +
        "(options, callback)");
    }

    var
      err,
      recipe = engine.recipe(),
      options = parseOptions.apply(null, arguments),
      ca = options.ca,
      f = options.f,
      fv = options.fv,
      callback = options.callback;

    if (callback && !_.isFunction(callback)) {
      throw new Error("Invalid arguments for selectFacetValue" +
        "(options, callback)");
    }

    if (!ca || !f || !fv) {
      err = new Error(
          "improper options passed to selectFacetValue. Expected: " +
          "{caId: 123, facetId: 123, fvId: 123} or " +
          "{\"chair\", \"color\", \"red\"}");
      return handleError(err, callback);
    }

    // Cant wait for changes when using the method within a transaction
    if (callback) {
      recipe.once("change:facet", function (data) {
        callback(null, JSON.parse(JSON.stringify(data)));
      });
    }

    try {
      if (f && f.id && userEdited.facets) {
        userEdited.facets[f.id] = true;
      }
      recipe.setFacet(ca, f, fv);
    } catch (e) {
      return handleError(e, callback);
    }
  };
};

module.exports.parseOptions = parseOptions;

function parseOptions(params, callback) {
  var ca, f, fv;

  if (arguments.length === 4) {
    ca = arguments[0];
    f = arguments[1];
    fv = arguments[2];
    callback = arguments[3];
  } else {
    if (_.isArray(params)) {
      ca = params[0];
      f = params[1];
      fv = params[2];
    } else {
      ca = params.ca;
      f = params.f;
      fv = params.fv;
    }
  }

  // Supporting "alias", "name" value setting
  if (typeof ca === "string") {
    ca = {alias: ca};
  }
  if (typeof f === "string") {
    f = {name: f};
  }
  if (typeof fv === "string") {
    fv = {name: fv};
  }

  if (params.caId) {
    ca = {id: params.caId};
  }
  if (params.facetId) {
    f = {id: params.facetId};
  }
  if (params.fvId) {
    fv = {id: params.fvId};
  }

  return {ca: ca, f: f, fv: fv, callback: callback};
}

function handleError(err, callback) {
  if (callback) {
    return callback(err);
  } else {
    throw err;
  }
}
