// fluid-configurations version should be set by users of configure-core
// On the configure-core project we're getting the version from
// fluid_configurations.json. This version has to be updated
// via the fluid-releases repo sibling to this project and the
// make ensure-fluid-configurations-is-up-to-date command
if (!process.env.FLUID_CONFIGURATIONS_VERSION) {
  throw new Error("configure-core needs to know what version of " +
    "fluid-configurations is using via the " +
    "FLUID_CONFIGURATIONS_VERSION environment variable");
}

var configurationLoader = require("@cfg.plat/configuration-loader");

var setEnvironmentParams = require("./set_environment_params");

module.exports = function getPreferences(fcParams, callback) {
  configurationLoader.preferences(setEnvironmentParams(fcParams), callback);
};
