var http = require("axios");
var async = require("async");
var getEvents = require("./get_events");

/**
 * avoid track events on demo pages
 */
var util = require("../../util");
var shouldTrackMetricsEvents =
  util.shouldTrackMetricsEvents((global || {}).location);
var isEmptyObject = util.isEmptyObject;

/*
 * this variable is shared among configure core instances,
 * and is used to kepp allways the same sessionId in metrics.
 * Some custmers load more than one configure instances.
 */
var sessionId;

// eslint-disable-next-line max-params
var analyticEvents = function(
  fcParams,
  eventName,
  eventData,
  isWebGl
) {
  sessionId = sessionId || fcParams.sessionId;
  if (!shouldTrackMetricsEvents) {
    return;
  }

  if (
    !fcParams.apiKey ||
    isEmptyObject(eventData)
    )
  {
    return;
  }

  var url = "//" + fcParams.configureEndpoint + "/metrics/events";

  var events = getEvents(
    fcParams,
    sessionId,
    eventName,
    eventData,
    isWebGl
  );

  async.forEachOf(events, function (event) {
    if (event !== undefined) {
      var options = {
        method: "POST",
        headers: { "x-api-key": fcParams.apiKey },
        data: {data: event},
        url: url,
      };

      http(options)
        .then()
        .catch(function () {
        });
    }

  }, function (err) {
    if (err) {
      console.error(err.message)
    };
  });

  return events;

};

module.exports = analyticEvents;


