module.exports = function (options) {
  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * Configurable Attribute Model Component.
   *
   * On the graph their vertices contain the namespace (ns) "camc"
   *
   * @param {Object} options Options
   * @param {number} options.id Configurable Attribute Model Component numeric id.
   * @class
   * @name ConfigurableAttributeModelComponent
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(
    /** @lends ConfigurableAttributeModelComponent */ {
      ns: "camc",

      /**
       * Finds if ConfigurableAttributeModelComponent is related to this attribute value
       *
       * @instance
       * @returns {Boolean}
       */
      hasAttributeValue: function (avId) {
        return this.getAndCache("hasAttributeValue" + avId, function () {
          return (
            this.traverser()
              .incoming("hasCaModelComponent")
              .where({ id: avId, ns: "av" })
              .instances().length > 0
          );
        });
      },
    },
    constructor
  );
};
