var _ = require("lodash");
var DynamicAttributeValue;
var utils = require("../utils.js");

/**
 * Creates a DynamicAttributeValue instance
 *
 * @param {ConfigurableAttribute} ca Who owns this `dav`? We need this info
 *                                   to infer how the dynamic attribute should
 *                                   behave and load specific adapters.
 * @param {Object} data Data for the dynamic attribute
 * @class
 * @name DynamicAttributeValue
 * @constructor
 * @classdesc

 ### Overview

 Dynamic attribute values (`dav`) are proxies for setting values on
 {@link ConfigurableAttribute}s that are not linked to configuration based
 {@link AttributeValue}s.

 The main difference between a regular {@link AttributeValue} and a
 {@link DynamicAttributeValue} (`dav`) is that the later is not represented
 on the configuration graph.

 They normally hold information from User Generated Content (UCG) such as:

 - Text values
 - User images

 */

// eslint-disable-next-line complexity
DynamicAttributeValue = function DynamicAttributeValue(ca, data) {
  var caData = ca.getData();
  if (caData.containsText) {
    data = data || {text: ""};
  } else if (ca.getData().type === "ugcClipArt") {
    data = data || {clipArt: ""};
  } else if (ca.isClipartEditor()) {
    data = data || {clipArt: "", images: [] };
  }
  this.collection = [];
  this.data = data;
  this.ca = ca;
};


DynamicAttributeValue.prototype = {

  /**
   * Sets raw data on the dynamic attribute
   *
   * @instance
   * @param {Object} data Data for the dynamic attribute
   * @return {boolean} false if the data has been changed
   */
  setData: function (data) {
    if (data.ca || data.collection) {
      throw new Error("You can't set a data.ca or data.collection on " +
        "as a dynamic attribute data");
    }
    return this.validate(data);
  },

  /**
   * Validate and sanitize the given data and assign it to the current value if
   * the value has been changed
   *
   * @param {object} avData
   * @returns {boolean} true if the data is valid and there is no change.
   * false if the data has been changed.
   */
  validate: function (avData) {
    var hasCurrentDataChanged = false;
    var sanitizedData = this.sanitize(avData || this.data);

    if (JSON.stringify(this.data) !== JSON.stringify(sanitizedData)) {
      this.data = sanitizedData;
      hasCurrentDataChanged = true;
    }
    return !hasCurrentDataChanged;
  },

  /**
   * Sanitizes given data so it can be used on the dynamic attribute
   *
   * @instance
   * @param {Object} data {text: ""}
   * @returns {Object} data - Sanitized data
   */
  sanitize: function (data) {
    var ca = this.ca,
      caData = ca.getData();

    if (caData.containsText && data && data.text) {
      var sanitizedText = sanitizeText(ca, "" + data.text);
      return {
        text: sanitizedText
      };
    }

    return data;
  },

  /**
   * Sets any kind of raw data on the dynamic attribute
   *
   * @instance
   * @param {Collection} collection Raw data for the dynamic attribute
   */
  setCollection: function (collection) {
    this.collection = collection;
  },

  /**
   * Gets the collection that was previously set on the dynamic attribute
   *
   * @instance
   * @returns {Collection} collection Raw data for this dynamic attribute
   */
  getCollection: function () {
    return this.collection;
  },

  /**
   * Gets the data that was previously set on the dynamic attribute
   *
   * @instance
   * @returns {Object} Data for this dynamic attribute
   */
  getData: function () {
    return this.data;
  },

  toJSON: function () {
    return this.getData();
  },

  /**
   * Get a textual representation for the current dynamic attribute.
   * @return {String} [dav:{"text":"Foo"} or dav:["file"]] string used to
   * debug entries. Only text content will be shown.
   */
  toString: function () {
    var data = this.data && _.pick(this.getData() || {}, [
      "text", "clipArt"
    ]);
    return "[dav:" +
      JSON.stringify(data ? data : _.keys(this.data || {})) +
      "]";
  }
};

// Private functions

function filterChars(text, textConfig, multiline) {
  var arrayText = utils.multibyteSplit(text);

  return _.reduce(arrayText, function (str, char) {
    if (
      (textConfig.fontUrl || !textConfig.characters)
      || textConfig.characters[char]
      || (multiline && char.match(/[\n\r]/))
    ) {
      str += char;
    }
    return str;
  }, "");
}

function sanitizeText(ca, text) {
  var maxLength,
    textConfig = getTextConfig(ca.fontAttribute()),
    caData = ca.getData();

  if (caData.automaticCapitalization) {
    text = text.toUpperCase();
  }

  text = filterChars(text, textConfig, caData.multiline);

  maxLength = getMaxLength(text, textConfig, caData);

  return trimText(text, maxLength);
}

function getMaxLength(text, textConfig, caData) {
  if (caData.multiline) {
    return caData.maxLength;
  }
  var croppedLength = getCroppedLength(
    utils.multibyteSplit(text || ""),
    textConfig,
    caData.maxWidth,
    caData.maxLength
  );
  return croppedLength || caData.maxLength;
}

// Gets the physical number of character that can be rendered if
// the space the individual fonts take is larger than the number
// of characters allowed. If the characters don't overflow the area
// available 0 will be returned
// eslint-disable-next-line complexity
function getCroppedLength(text, textConfig, maxWidth, maxLength) {
  var chars = textConfig.characters || {};
  var letterSpacing = textConfig.letterSpacing || 0;
  var i, sum = 0, char;
  for (i = 0; text.length > i && maxLength > i; i += 1) {
    char = text[i];

    sum += (chars[char] && chars[char].width || 0) 
    + ((i < text.length -1) ? letterSpacing : 0);
    
    if (sum > maxWidth) {
      return i;
    }
  }
  return 0;
}

function trimText(text, maxLength) {
  var charactersArray = utils.multibyteSplit(text);
  if (charactersArray.length > maxLength) {
    return charactersArray.slice(0, maxLength).join("");
  }
  return text;
}

function getTextConfig(fontCa) {
  if (!fontCa) {
    return {};
  }
  // When setting recipes from scratch the font might not be selectable yet
  var activeValues = fontCa.activeValues();
  var fontAv = activeValues && activeValues[0];
  // we need to take into account old recipes that perhaps has fonts selected
  // that are not available anymore
  if (fontCa.selectedValue() &&
    _.includes(fontCa.selectableValues(), fontCa.selectedValue())) {
    fontAv = fontCa.selectedValue();
  }

  return fontAv
    .getData()
    .textConfig;
}



module.exports = DynamicAttributeValue;
