var _ = require("lodash");

module.exports = function (options) {

  var BaseModel = require("./base")(options);

  /**
   * Attribute Value.
   *
   * Attribute values are linked to {@link ConfigurableAttribute}s and together
   * with them define the state of {@link Recipe}s
   *
   * On the graph their vertices contain the namespace (ns) "av"
   *
   * @param {Object} options Options
   * @param {number} options.id Attribute Value numeric id.
   * @class
   * @name AttributeValue
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends AttributeValue */{
    ns: "av",

    /**
     * Gets the {@link FacetValue}s for this attribute value.
     *
     * @instance
     * @return {Array<FacetValue>}
     */
    facetValues: function () {
      return this.getAndCache("facetValues", function () {
        return this.traverser()
          .outgoing("hasFacetValue")
          .sortBy("order", "desc")
          .instances();
      });
    },

    /**
     * Gets the {@link FacetValue}s for a given {@link Facet}
     * @instance
     * @param {FacetValue|Number} facet - A facet instance or facet ID
     * @return {Array<FacetValue>}
     */
    facetValuesForFacet: function (facet) {
      var facetId = facet.id || facet;
      return this.getAndCache("fvsfor" + facetId, function () {

        var allFVs = this.traverser()
          // This av >hasFacetValue> fv
          .outgoing("hasFacetValue")
          .instances();

        return _.filter(allFVs, function (fv) {
          return fv.facet().id === facetId;
        });
      });
    },

    /**
     * Attributes have facetId's and facetValues associated with them in
     * order to allow filtering of sets of values.
     * facetId's are the equivalent of Facet Categories (if you are reading
     * Fluid Education).
     * This method allows you to check whether a value has a facetId/Facet
     * Category only, or both a facetId and a facetValue
     * (the facetValue must be under the facetId/Facet Category).
     *
     * @instance
     * @param {Facet} facet
     * @param {FacetValue} facetValue
     * @returns {boolean}
     */
    hasFacetValue: function (facet, facetValue) {
      if (!facetValue) { return false; }
      return _.some(this.facetValuesForFacet(facet), function (fv) {
        return fv.id === facetValue.id;
      });
    },

    /**
     * In order to compare FC2 and FC3 recipes we need to know when the admin
     * will generate value id's starting with av_ca_ which are considered
     * virtualAttributes. These are normally text attributes whose value might
     * vary depending on the ConfigurableAttribute it belongs to.
     *
     * @todo Remove as soon as we don't need it in oder to compare FC2
     * @param {@link ConfigurableAttribut} ca
     * @return {Boolean}
     */
    isVirtual: function (ca) {
      return this.traverser()
        .incoming("hasVirtualAttributeValue")
        .where({
          id: ca.id || ca
        })
        .all().length > 0;
    },

    /**
     * Gets the attribute value upcharge in a given currency
     *
     * @instance
     * @param  {String} currency Currency code
     * @return {Number}          Attribute Value price
     */
    upcharge: function (currency) {
      return this.getAndCache("upcharge" + currency, function () {
        var upcharges = this.upcharges();

        if (upcharges) {
          return upcharges[currency];
        }
        return 0;
      });
    },

    /**
     * Gets the attribute value upcharges in all currencies
     *
     * @instance
     * @return {Object|0} Object with [currencyCode]: value
     */
    upcharges: function () {
      var upcharge = this.traverser()
          .outgoing("upcharges")
          .instance();

      if (upcharge) {
        return upcharge.getData();
      }
      return 0;
    },

    /**
     * Gets the attribute value upcharge in a given currency
     *  for a specific configurable attribute by overriding
     *  the upcharge if an upcharge has been defined for the valueUsage
     *  for the specified attribute
     *
     * @param  {ConfigurableAttribue} ca
     * @param  {String} currency Currency code
     * @return {Number} Return the upcharge
     */
    getUpchargeFor: function (ca, currency) {
      var upcharge, vu;

      // we cannot compute prices on ca that has DynamicAttributeValues
      if (ca.hasDynamicValue()) {
        return 0;
      }

      // value usage's upcharge overrides attribute value's upcharge
      // so first we will search the upcharge on the value usage and then
      // on the attribute value itself
      vu = ca.valueUsageForValue(this);
      if (vu) {
        upcharge = vu.upchargeFor(this, currency);
      }

      if (upcharge === null || upcharge === undefined) {
        upcharge = this.upcharge(currency) || 0;
      }

      return upcharge;
    },

    /**
     *      * // todo @p.brunko update docs
     * Gets the attribute value upcharges in all currencies
     *  for a specific configurable attribute by overriding
     *  the upcharges if an upcharges has been defined for the valueUsage
     *  for the specified attribute
     *
     * @param  {ConfigurableAttribute} ca
     * @return {Object|0} Object with [currencyCode]: value
     */
    getUpchargesFor: function (ca) {
      var upcharge, vu;

      // we cannot compute prices on ca that has DynamicAttributeValues
      if (ca.hasDynamicValue()) {
        return 0;
      }

      // value usage's upcharge overrides attribute value's upcharge
      // so first we will search the upcharge on the value usage and then
      // on the attribute value itself
      vu = ca.valueUsageForValue(this);
      if (vu) {
        upcharge = vu.upchargesFor(this);
      }

      if (upcharge === null || upcharge === undefined) {
        upcharge = this.upcharges() || 0;
      }

      return upcharge;
    },

    /**
     * Checks if attribute value is font attribute value
     *
     * @return {Boolean} Return true if AV can be considered font AV
     */
    isFont: function () {
      var data = this.getData()
      return data && data.textConfig && (data.textConfig.characters || data.textConfig.fontUrl);
    },

    /**
     * Gets a collection of WebGl Customer Material instances.
     *
     * @instance
     * @return {Array<WebGlCustomerMaterial>}
     */
    customerMaterials: function () {
      return this.getAndCache("customerMaterials", function () {
        return this.traverser()
          .outgoing("hasMaterial")
          .instances();
      })
    },

    /**
     * Does this AV have customer materials?
     *
     * @instance
     * @return {Boolean}
     */
    hasCustomerMaterials: function () {
      return this.traverser()
        .outgoingSize("hasMaterial") > 0;
    }

  });

};
