var _ = require("lodash");

/**
 * This method returns a list of ca strings are used on FC2, including
 * the product id and the ca id (and it's parent attributes)
 *
 * This is used to build verbose recipes
 */
module.exports = function getSortedVerboseAttributeStrings(productInstance) {
  var productPrefix = "p_" + productInstance.id,
    caStrings = [];

  _.each(productInstance.allAttributes(), function (ca) {
    var key = [],
      fullKey,
      attributes = ca.parentAttributes().concat(ca);

    _.each(attributes, function (attribute) {
      key.push(productPrefix + "_ca_" + attribute.getData().id);
    });

    fullKey = key.join(".");
    caStrings.push(fullKey);

  });

  caStrings.sort();

  return caStrings;
};

