/**
 * @publicDocs methods/getConfig ../../docs/get_config.md
 */

var
  _ = require("lodash"),
  clone = _.compose(JSON.parse, JSON.stringify);

module.exports = function (engine) {

  /**
   * get configuration data from the root product node
   * @param  {String}   path     a string of properties separated by dots
   * @param  {Function} callback the data requested, or null if it doesn't exist
   */
  return function getConfig(path, callback) {
    if (!path && !callback) {
      throw new Error("Invalid arguments for getConfig(path, callback)");
    }

    if (!callback) {
      if (!_.isFunction(path)) {
        throw new Error("The parameter you provided for getConfig " +
          "must be a function");
      }
      callback = path;
      path = null;
    }

    if (path) {
      path = path.split(".");
    } else {
      path = [];
    }

    // retreive a nested value
    var config = _.reduce(path, function (obj, prop) {
      if (_.isUndefined(obj)) { return; }
      return obj[prop];
    }, engine.product().getData());

    if (_.isUndefined(config)) { config = null; }

    return callback(null, clone(config), path);
  };
};
