/**
 * @publicDocs methods/getWebGlModelResolutions
 *   ../../docs/get_webgl_model_resolutions.md
 */

module.exports = function (engine) {
  var product = engine.product();

  return function getWebGlModelResolutions(query) {
    return  product.webGlLodResolutions(query && query.filter)
      .map(function (lodResolution) {
        return Object.freeze(Object.assign({}, lodResolution.getData()))
      });
  };
};
