/**
 * @publicDocs methods/formatPrice ../../docs/format_price.md
 */

var _ = require("lodash");
var accounting = require("accounting");
var defaultSettings = {
  currency: {
    symbol: "$", // default currency symbol is '$'
    format: {
      pos: "%s%v",
      neg: "%s -%v",
      zero: "%s%v"
    }
  },
  number: {
    decimal: ".", // decimal point separator
    thousand: ",", // thousands separator
    precision: 2 // decimal places
  }
};

module.exports = function (engine, fcParams) {
  var currencySettings = {};
  var numberFormattingSettings = {};
  var getConfig = require("./get_config")(engine);

  getConfig(function (err, config) {
    currencySettings = _.merge({},
      config.currencySettings || {},
      fcParams.currencySettings || {});
    numberFormattingSettings = _.merge({},
      config.numberFormattingSettings || {},
      fcParams.numberFormattingSettings || {});
  });

  function getNumberFormatDefaults(options) {
    var formats = numberFormattingSettings.formats || {};
    var numberFormat = getNumberFormatKey(formats, options);
    return formats[numberFormat] || defaultSettings.number;
  }

  function getNumberFormatKey(formats, options) {
    return options.numberFormat || fcParams.numberFormat ||
      (formats[fcParams.currency] && fcParams.currency) ||
      (formats[fcParams.locale] && fcParams.locale) ||
      numberFormattingSettings.default;
  }

  function getCurrencyFormatDefaults(options) {
    var symbols = currencySettings.symbols || {};
    var currency = options.currency || fcParams.currency ||
      (symbols[fcParams.locale] && fcParams.locale) ||
      currencySettings.default;
    return symbols[currency] || defaultSettings.currency;
  }

  function getAccountingOptions(options) {
    return _.merge({},
      getCurrencyFormatDefaults(options),
      getNumberFormatDefaults(options),
      options);
  }

  return function formatPrice(price, options) {
    price = +price;
    if (!_.isNumber(price)) {
      throw new Error("api.run(\"formatPrice\", price) Price is not a number");
    }
    options = options || {};
    var accountingOptions = getAccountingOptions(options);
    return accounting.formatMoney(price, accountingOptions);
  };
};
