module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGlHdrBackground.
   *
   * On the graph their vertices contain the namespace (ns) "whdr"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGlHdrBackground numeric id.
   * @class
   * @name WebGlHdrBackground
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlHdrBackground */{

    ns: "whdr"

  }, constructor);

};
