/**
 * Create a lookup map to accelerate the processing of the relationships between
 * attribute values and configurable attributes later in the override process.
 * The structure of the map is:
 * - The keys are the id's of the attribute values.
 * - The values are an array of the configurable attributes associated with the
 * attribute values.
 * @param {Array} hasAttributeValueRelations The edge group from the graph
 * that connects CA's and AV's. The structure is:
 * [[caIx, avIx], [caIx, avIx]]
 * where each *Ix is an index into the vertices array.
 * @param {Array} vertices All of the vertices in the graph. All types.
 */
module.exports = function createAvId2CaObjMap(
  hasAttributeValueRelations, vertices) {
  return (hasAttributeValueRelations || []).reduce(function (map, ixPair) {
    var av = vertices[ixPair[1]];
    var ca = vertices[ixPair[0]];
    //console.log("av: %s->%s, ca: %s->%s", ixPair[1], av.id, ixPair[0], ca.id);
    map[av.id] = map[av.id] || [];
    map[av.id].push(ca);
    return map;
  }, {});
};
