/**
 * @publicDocs methods/setAttributeCollection
 *   ../../docs/set_attribute_collection.md
 */

var
  _ = require("lodash");

module.exports = function (engine, collectionEmitter) {

  return function setAttributeCollection() {

    if (arguments.length === 0) {
      throw new Error("Missing arguments for setAttributeCollection(options)");
    }

    var
      err,
      options = parseOptions.apply(null, arguments),
      ca = options.ca,
      collection = options.collection;

    if (!ca || !collection) {
      err = new Error(
          "improper options passed to setAttributeCollection. Expected: " +
          "{caId: 123, collection: [123]} or " +
          "{caId: 123}, [123]");
      throw err;
    }

    var product = engine.product();
    var attribute = product.findAttribute(ca);
    if (!attribute) {
      err = new Error("You must provide a valid attribute param");
      throw err;
    }
    if (!attribute.hasDynamicValue()) {
      err = new Error("Only attributes with selectorType text or ugc work");
      throw err;
    }

    attribute.getDynamicAttributeValue().setCollection(collection);

    collectionEmitter.trigger("change", attribute);
  };
};

module.exports.parseOptions = parseOptions;

function parseOptions(params) {
  var ca, collection;

  if (arguments.length === 2) {
    ca = arguments[0];
    collection = arguments[1];
  } else {
    if (_.isArray(params)) {
      ca = params[0];
      collection = params[1];
    } else {
      ca = params.ca;
      collection = params.collection;
    }
  }

  // Supporting "alias", "name" value setting
  if (typeof ca === "string") {
    ca = {alias: ca};
  }

  if (params.caId) {
    ca = {id: params.caId};
  }

  return {ca: ca, collection: collection};
}
