/**
 * @publicDocs methods/updateRecipe ../../docs/update_recipe.md
 */

var _ = require("lodash");

module.exports = function (engine, api) {
  var setValue = require("./select_value")(engine, null, api, null, true);
  
  return function updateRecipe(changes, callback) {
    var recipe;
    if (!changes || !callback || (callback && !_.isFunction(callback))) {
      throw new Error("Invalid arguments for updateRecipe(changes, callback)");
    }

    recipe = engine.recipe();

    // Change listener with the result of the operation
    recipe.once("change", onChange);

    try {
      // Only apply changes on a single transaction
      // @todo: POTENTIAL SOURCE OF ISSUES
      // The order in which you define changes, via an array of objects, CAN
      // AND WILL influence how changes are applied (eg. sympathetic
      // relationships and filters can change selectable values for subsequent
      // attributes).
      recipe.start();
      _.each(changes, function (change) {
        var cb = function (error) {
          if (error) {
            return callback({
              message: "" + error,
              caId: change.caId,
              avId: change.avId,
            });
          }
          return;
        };
        setValue.call(null, change, cb);
      });
      recipe.commit();
    } catch (e) {
      try {
        recipe.rollback();
      } catch (e) {
        // continue regardless of error
      }

      recipe.off("change", onChange);
      return callback(e);
    }

    function onChange(data) {
      callback(null, JSON.parse(JSON.stringify(data)));
    }
  };
};
