/**
 * Create a lookup map to accelerate the processing of large collections later
 * in the override process.
 * The structure of the map is:
 * - The keys are the id's of the objects on the left side of the given set of
 * relations.
 * - The values are an array of the indices on the right side of the relations.
 * @param {Array} relations An edge group from the graph, with the structure:
 * [[ix, ix], [ix, ix]]
 * where each ix is an index into the vertices array.
 * The first index in the pair always points to the same data type, as does the
 * second. The types vary depending on the relations parameter.
 * @param {Array} vertices All of the vertices in the graph. All types.
 * @param {Boolean} reverse True to map the relationships from the second array
 * element to the first. Otherwise, the first element in the array maps to the
 * second. Optional, defaults to false.
 */
module.exports = function createId2IxsMap(relations, vertices, reverse) {
  var sourceIx = reverse ? 1 : 0;
  return (relations || []).reduce(function (map, ixPair) {
    var curObj = vertices[ixPair[sourceIx]];
    var element = map[curObj.id];
    if (!element) {
      element = [];
      map[curObj.id] = element;
    }
    element.push(ixPair[1 - sourceIx]);
    return map;
  }, {});
};
