/**
 * @publicDocs methods/setRecipe ../../docs/set_recipe.md
 */

var
  _ = require("lodash");

module.exports = function (engine, api) {
  var setValue = require("./select_value")(engine, null, api);

  return function setRecipe(changes, callback) {
    if (!changes || !callback || (callback && !_.isFunction(callback))) {
      throw new Error("Invalid arguments for setRecipe(changes, callback)");
    }

    var
      recipe = engine.recipe();

    recipe.once("change", function (data) {
      callback(null, JSON.parse(JSON.stringify(data)));
    });

    try {
      // Only apply changes on a single transaction
      // @todo: POTENTIAL SOURCE OF ISSUES
      // The order in which you define changes, via an array of objects, CAN
      // AND WILL influence how changes are applied (eg. sympathetic
      // relationships and filters can change selectable values for subsequent
      // attributes).
      recipe.start();
      _.each(changes, function (change) {
        setValue.call(null, change);
      });
      recipe.commit();
    } catch (e) {
      try {
        recipe.rollback();
      } catch (e) {
        // continue regardless of error
      }
      return callback(e);
    }
  };
};
