var _ = require("lodash");

function getCaLegacyFullKey(productInstance, ca) {
  var productPrefix = "p_" + productInstance.id;
  var key = [];
  var attributes = ca.parentAttributes().concat(ca);
  _.each(attributes, function (attribute) {
    key.push(productPrefix + "_ca_" + attribute.getData().id);
  });

  return key.join(".");
}

/**
 * This method is mostly used for comparing FC2 recipes with
 * FC3 recipes. This might be exposed on the API to ease up
 * implementation on FC3, but it should be deprecated to favor
 * a simpler and less verbose format.
 *
 * Given this is meant to be used mostly for validating recipe
 * we need to pre-sort the hash (although there are no consistency
 * guarantees across engines)
 */
function getVerboseRecipe(
    recipeInstance,
    productInstance,
    byProduct
) {
  var result = {},
    unsorted = {},
    unsortedKeys = [];

  _.each(recipeInstance.getData(), function (entry) {
    var ca = entry.ca,
      av = entry.av;

    var fullKey = getCaLegacyFullKey(productInstance, ca);
    unsortedKeys.push(fullKey);


    if (ca.hasDynamicValue()) {
      unsorted[fullKey] = getDynamicData(ca, av);
    } else if (av) {
      // For some weird reason the admin publishes a different av id string
      // for textConfig av's, we need to account for that inconsistency
      // in order to be able to compare FC2 recipes
      if (av.isVirtual(ca)) {
        unsorted[fullKey] = "av_ca_" + ca.getData().id + "_av" + av.id;
      } else {
        unsorted[fullKey] = "av" + (av.id || av);
      }
    }

  });

  unsortedKeys.sort();

  _.each(unsortedKeys, function (key) {
    result[key] = unsorted[key];
  });
  if (byProduct) {
    productInstance.allAttributes()
      .map(function (ca) {
        return getCaLegacyFullKey(productInstance, ca)
      })
      .forEach(function (fullKey) {
        if (typeof result[fullKey] === "undefined") {
          result[fullKey] = "-";
        }
      });
  }

  return result;
}

module.exports = function (
    recipeInstance,
    productInstance
) {
  return getVerboseRecipe(recipeInstance, productInstance, false)
};

module.exports.getVerboseByProduct = function (
    recipeInstance,
    productInstance
) {
  return getVerboseRecipe(recipeInstance, productInstance, true)
};

function getDynamicData (ca, av) {
  if (av) {
    return av.data || av;
  } else {
    return ca.getDynamicAttributeValue().getData();
  }
}