var getProductFacets = require("./get_product_facets");
/**
 * @publicDocs methods/isWebGl ../../docs/is_web_gl.md
 */

var
  _ = require("lodash");

module.exports = function (engine) {
  return function isWebGl() {
    var product = engine.product(), 
        productFacets = getProductFacets(engine, product),
        displayStrategy = productFacets["Display Strategy"];
    
    return (displayStrategy && _.includes(displayStrategy, "WebGL")) || product.getData().renderingEngine === 'WEB_GL';
  };
};
