module.exports = function (options) {
  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGl Model Level of Detail.
   *
   * On the graph their vertices contain the namespace (ns) "wmlod"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGl Level of Detail numeric id.
   * @class
   * @name WebGlModelLod
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(
    /** @lends WebGlModelLod */ {
      ns: "wmlod",

      /**
       * Gets product webGl models.
       *
       * Models are used to render a product inside configure-ui
       *
       * @instance
       * @return {Array<WebGlModel>}
       */
      webGlModel: function () {
        return this.getAndCache("webGlModel", function () {
          return this.traverser()
            .incoming("hasModelLod")
            .where({ ns: "wm" })
            .instance();
        });
      },

      /**
       * Gets webGl loading strategy.
       *
       * @instance
       * @return {WebGlLoadingStrategy}
       */
      loadingStrategy: function () {
        return this.getAndCache("loadingStrategy", function () {
          return this.traverser()
            .incoming("hasModelLod")
            .where({ ns: "wplod" })
            .incoming("hasModelLodResolution")
            .where({ ns: "wload" })
            .instance();
        });
      },

      /**
       * Gets webGl WebGl Lod Resolution.
       *
       * @instance
       * @return {WebGlLodResolution}
       */
      lodResolutions: function () {
        return this.getAndCache("lodResolutions", function () {
          return this.traverser()
            .incoming("hasModelLod")
            .where({ ns: "wplod" })
            .outgoing("hasLodResolution")
            .where({ ns: "lod" })
            .instances();
        });
      },
    },
    constructor
  );
};
