/**
 * @publicDocs methods/getWebGlModel ../../docs/get_webgl_model.md
 */

module.exports = function (engine, getProductJson) {
  var product = engine.product();

  return function getWebGlModel(query) {
    var modelNode = product.findWebGlModel(query), modelPath;

    if (!modelNode) {
      throw new Error("getWebGlModel could not find a WebGl model matching " +
        JSON.stringify(query) + ". Available WebGl model are: " +
        product.webGlModels().join(", "));
    }

    return getProductJson().webGlModels[modelNode.id];
  };
};
