const _ = require("lodash");
module.exports = function (options) {
  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGl LOD resolution.
   *
   * On the graph their vertices contain the namespace (ns) "lod"
   * WebGlLodResolution are used to pick particular model file for rendering at this resolution withing the strategy.
   *
   * @param {Object} options Options
   * @param {number} options.id WebGl LOD resolution numeric id.
   * @class
   * @name WebGlLodResolution
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(
    /** @lends WebGlLodResolution */ {
      ns: "lod",

      /**
       * Gets webGl Model LODs for this LOD resolution
       * @param {Object} query - The object of property WebGlLoadingStrategy to filter by
       * @instance
       * @return {Array<WebGlModelLod>}
       */
      modelLodsForStrategy: function (query) {
        var queryToUse = query;
        if (_.isString(query) || _.isNumber(query)) {
          // assume it's an ID
          queryToUse = { id: +query };
        }
        const modelLods = this.traverser()
          .incoming("hasLodResolution")
          .where({ ns: "wplod" })
          .outgoing("hasModelLod")
          .instances();
        return modelLods.filter(function (modelLod) {
          return _.matches(queryToUse)(modelLod.loadingStrategy());
        });
      },
    },
    constructor
  );
};
