/**
 * Utility library for finding different models on the graph
 */

var _ = require("lodash");

/**
 * When using the API programatically for writting tests
 * or on custom implementations we want to offer useful
 * error messages that can help the developers to know
 * more about the underlying data.
 *
 * @private
 */
function getSearchTipData(collection, search) {
  try {
    var pickKeys = _.keys(search);
    return _.reduce(collection, function (result, value) {
      var data = value.getData(),
        hint = _.pick(data, pickKeys);
      result.push(_.size(hint) > 0 ? hint : data);
      return result;
    }, []);
  } catch (e) {
    return "Sorry no tips available";
  }
}

/**
 * Returns the input vertex or undefined if it's not a vertex
 * instance
 * @private
 */
function getVertexInstance(v) {
  if (v && v.getVertexKey) {
    return v;
  }
}


/**
 * Given a ca or a search object, gets the requested ca
 * or throws an exception with a hint on the available options
 * @private
 */
function getOrSearchCa(caOrOptions, product) {
  var ca = getVertexInstance(caOrOptions) ||
    product.findAttribute(caOrOptions || {});

  if (!ca) {
    throw new Error("Could not find attribute to set with the following " +
      "options " + JSON.stringify(caOrOptions) + ".\n\n" +
      "[tip] You might want to try with:\n\n" +
      JSON.stringify(
        getSearchTipData(product.allAttributes(), caOrOptions)));
  }
  return ca;
}

/**
 * Given a ca and an av or a search object, gets the requested av
 * or throws an exception with a hint on the available options
 * @private
 */
function getOrSearchAv(ca, avOrOptions) {
  var av = getVertexInstance(avOrOptions) ||
    ca.findSelectableValue(avOrOptions || {});

  if (!av) {
    // `dav` are less common than other attributes, therefore we should
    // only check them if the vertex instance was not available
    if (ca.hasDynamicValue()) {
      return ca.getDynamicAttributeValue();
    }
  }
  return av;
}


/**
 * Given a ca and an av or a search object, gets the requested av
 * or throws an exception with a hint on the available options
 * If the flag useDefault is true, a default av is returned if exists,
 * otherwise the first av in the array is returned
 * @private
 */
function getOrSearchAvOrDefault(ca, avOrOptions, useDefault) {
  var av = getOrSearchAv(ca, avOrOptions);

  // if we need to find a default value...
  if (!av && useDefault === true) {
    // check if the av still exists. if the default av is no longer valid, return the first av
    av = getOrSearchAv(ca, ca.defaultValue(), false) || ca.selectableValues()[0];
  }

  if (!av) {
    throw new Error("Could not find attribute value to set with the " +
      "following options " + JSON.stringify(avOrOptions) + ".\n\n" +
      "[tip] You might want to try with:\n\n" +
      JSON.stringify(getSearchTipData(ca.selectableValues(), avOrOptions)));
  }
  
  return av;
}

/**
 * Given a `f` or a search object, gets the a `f` that belongs to a `ca`
 * or throws an exception with a hint on the available options
 * @private
 */
function getOrSearchF(fOrOptions, ca) {
  var f = fOrOptions.findFacetValue ? fOrOptions :
    ca.findFacet(fOrOptions);

  return f;
}


/**
 * Given a `fv` or a search object, gets the a `fv` that belongs to a `f`
 * or throws an exception with a hint on the available options
 * @private
 */
function getOrSearchFv(fvOrOptions, f, ca) {
  var fv = fvOrOptions.attributeValues ? fvOrOptions :
    f.findFacetValue(fvOrOptions);

  if (!fv) {
    throw new Error("Could not find facet value with the following " +
      "options " + JSON.stringify(fvOrOptions) + ".\n\n" +
      "[tip] You might want to try with:\n\n" +
      JSON.stringify(
        getSearchTipData(f.selectableFacetValues(ca), fvOrOptions)));
  }
  return fv;
}


module.exports = {
  ca: getOrSearchCa,
  av: getOrSearchAvOrDefault,
  f: getOrSearchF,
  fv: getOrSearchFv
};
