/**
 * @publicDocs methods/getBrowserData ../../docs/get_browser_data.md
 */

// https://www.npmjs.com/package/bowser
var bowser = require("bowser");

// https://www.npmjs.com/package/browser-detect
var browser = require("browser-detect");

 module.exports = function () {
   return function getBrowserData(window) {
     var result = browser();

     if (window!== undefined) {
       var userAgentBrowserData = bowser.parse(window.navigator.userAgent);
       Object.keys(userAgentBrowserData).forEach(function(keyName) {
         if (!result[keyName] && keyName !== "browser" && keyName !== "engine") {
           result[keyName] = userAgentBrowserData[keyName];
         } else if (keyName === "os") {
          result[keyName] = userAgentBrowserData[keyName];
         }
       });
     }
     return result;
   };
 };
