/* eslint complexity:0 */
var _ = require("lodash");
var request = require("superagent");
var lastDigitsRE = /(\d+)$/;
var getProductQuantity = require("./utils").getProductQuantity;

module.exports = {
  getPerformPostRecipeFunction:
  	getPerformPostRecipeFunction,
  	getRecipeResourceUrl: getRecipeResourceUrl,
  	selectionRecipe: selectionRecipe,
  	recipeData: recipeData,
  	postRecipeData: postRecipeData
};

function getSaveRecipePostEndpoint(workflow,  saveRecipeEndpoint, apiKey) {
  var endpoint = "/" + workflow + "/recipes";
  return (saveRecipeEndpoint || endpoint) +
    (apiKey ? ("?apiKey=" + apiKey) : "");
}

function getRecipeResourceUrl(fcParams, id, steps) {
  var apiKey = fcParams.apiKey || steps.preferences.apiKey;
  // TODO: The format should be oms once it's implemented on the backend
  return "/" + fcParams.workflow + "/customers/" + fcParams.customerId +
    "/recipes/" + id + "?apiKey=" + apiKey + "&format=custom&ca=alias";
}

/** Uses either a custom method passed via fcParams.recipePoster,
options.recipePoster or a JSONp call to save the recipe on the server
and get back an object with the {id: 123}
*/
function getPerformPostRecipeFunction(cb, steps, fcParams) {
  cb(null, steps.customerData.recipeV3Poster || fcParams.recipeV3Poster ||
		postRecipe);
}

function selectionRecipe(steps, engine, getRecipe, cb) {
  var result;
  if (!steps.customerData.stripPersonalization) {
    result = getRecipe("save", true);
  } else {
    var recipe = engine.recipe();
    recipe.start();
    recipe.removePersonalValues();
    result = getRecipe("save", true);
    recipe.rollback();
  }
  cb(null, result);
}

function recipeData(cb, steps, api) {
  var data = steps.data;
  var customerData = steps.customerData;

  var recipeData = {
    workflow: data.workflow,
    productId: +lastDigitsRE.exec(_.get(data, "productId", data.productId))[1],
    customerId: data.customerId,
    environment: data.environment,
    selection: steps.selectionRecipe,
    quantity: getProductQuantity(customerData, api),
    price: {
      "total": api.getPrice(),
      "currency": api.getCurrency()
    },
    savePurpose: customerData.purpose || "other",
    configVersion: steps.configVersion
  }

  cb(null, recipeData);
}

function postRecipeData(cb, steps) {
  if (steps.hasTimedOut()) {
    return;
  }
  var saveRecipeEndpoint = "TODO";

  var metadata = {};
  var payload = steps.recipeV3Data

// Post to new API here, include

  if (steps.recipeV1Data) {
    metadata.recipeV1Id = steps.recipeV1SaveResponse.id;
    payload.metadata = metadata;
  }

  var errCopy = "Failed posting recipe, got error: ";

  try {
    // this method might trigger sync callbacks or throw exceptions
    // from superagent
    var postProcess = steps.performV3PostFunction;

    postProcess(getSaveRecipePostEndpoint(
      payload.workflow, saveRecipeEndpoint,
      steps.preferences.apiKey
    ), payload, cb);
  } catch (e) {
    cb(new Error(errCopy + e.message));
  }
}

function postRecipe(url, payload, cb) {
  request.post(url)
  .send(payload)
  .end(function(err, response) {
    if (err) {
      return cb(err);
    }
    var body = response.body;
    if (!body) {
      err = new Error("Empty response body");
    } else if (!body.recipeId) {
      err = new Error("Server response does not include recipe id");
    }

    cb(err, body);
  },
function onfailure (err) {
  cb(err);
}
);
}
