/* eslint complexity:0*/
var _ = require("lodash");
var fetchPreferences = require("../../preferences");

module.exports = {
  getProductQuantity: getProductQuantity,
  getLocale: getLocale,
  getConfigVersion: getConfigVersion,
  beforeSaveHook: beforeSaveHook,
  afterSaveHook: afterSaveHook,
  getHasTimedOutFunction: getHasTimedOutFunction,
  getData: getData,
  getPreferences: getPreferences
}

/**
 * The preferences are needed in order to validate and set the locale
 */
function getPreferences(cb, fcParams) {
  if (fcParams.preferences) {
    return cb(null, fcParams.preferences);
  }
  fetchPreferences(fcParams, function (err, preferences) {
    if (err) {
      err.message = "Failed getting preferences with error: " + err.message;
    }
    cb(err, preferences);
  });
}

/** Gets a basic data structure expected by the runtime endpoint in order
  to create a recipe based on a subset of fcParams */
function getData(cb, steps, getItemNumber) {
  var fcParams = steps.fcParams;
  var preferences = steps.preferences;

  var err;
  var data = _.defaults({}, _.pick(fcParams, [
    "licenseeKey", "baseUrl", "workflow",
    "productId", "customerId", "environment", "apiKey"
  ]));

  data.licenseeKey = data.licenseeKey || fcParams.licenseeKey ||
    preferences.licenseeKey;
  if (!data.licenseeKey) {
    err = new Error("saveRecipe failed. Missing licenseeKey");
    console.warn(err);
  }
  addItemNumber(data, getItemNumber);

  cb(null, data);
}


/**
* Item numbers should only be added for customers
* that implement them. Only the product item number is
* supported so far.
*/
function addItemNumber(data, getItemNumber) {
  var itemNumber = getItemNumber("product");
  if (itemNumber) {
    data.itemNumbers = {
      product: itemNumber
    };
  }
}


/** Called before setting the recipe defaults and saving it. The user can
  change the recipe at this point. */
function beforeSaveHook(cb, steps) {
  if (steps.hasTimedOut()) {
    return;
  }
  hookUserEventCallback("beforeSave", steps.customerData, cb);
}

/** Called right after receiving the response from the server. */
function afterSaveHook(cb, steps) {
  if (steps.hasTimedOut()) {
    return;
  }
  hookUserEventCallback("afterSave", steps.customerData, cb);
}

/** Executes a user provided hook function passed via options and complains
  if the hook does not include a callback */
function hookUserEventCallback(name, customerOptions, callback) {
  var hook = customerOptions[name],
    errorMessage;
  if (!hook) {
    return callback();
  }
  if (!_.isFunction(hook)) {
    return callback(new Error("options." + name + " is not a function"));
  }
  if (hook.length === 0) {
    return callback(
      new Error("Missing callback on options." + name + " option"));
  }
  try {
    hook(callback);
  } catch (e) {
    errorMessage = "Uncaught exception when calling saveRecipe " +
      "options." + name + "(). Got error: " + e.message;

    callback(new Error(errorMessage));
  }
}

function getConfigVersion(cb, api) {
  api.getConfig("configVersion", function _getConfigVersion(err, version) {
    cb(err, version);
  });
}

function getProductQuantity(customerData, api) {
  return customerData.quantity ||
    api.getQuantity() || 1;
}

/**
 * Gets the locale from fcParams.locale or from the preferences. It also
 * validates that the provided locale has been published on the admin
 */
function getLocale(cb, steps, fcParams) {
  var locales = steps.preferences.locales,
    locale = fcParams.locale || locales["default"],
    err;
  if (!isValidLocale(locales.available, locale)) {
    err = new Error("Can't save recipe without a valid locale.");
  }
  cb(err, locale);
}

/** Locale validation method used by getLocale */
function isValidLocale(available, locale) {
  if (available.indexOf(locale) !== -1) {
    return true;
  }
  if (process.env.NODE_ENV !== "production") {
    console.warn("The locale fcParams.locale " + locale + " has not been " +
      "published yet and it's not listed as available.");
  }
  return false;
}

/**
 Keeps track of timed out transactions
 */
function getHasTimedOutFunction(cb, steps) {
  var start = new Date().getTime(),
    timedOutErr,
    hasTimedOut = function hasTimedOut() {
      return timedOutErr;
    };

  if (!steps.customerData.timeout) {
    return cb(null, hasTimedOut);
  }

  setTimeout(function () {
    // No need to warn about timeouts if the recipe save has been
    // successfully broadcasted
    if (steps.broadcastSuccess) {
      return;
    }

    var end = new Date().getTime(),
      time = end - start;

    if (time >= +steps.customerData.timeout) {
      timedOutErr = new Error("saveRecipe: timeout after " + time +
        "ms. Exceeded options.timeout of " +
        steps.customerData.timeout + "ms");

      if (_.isFunction(steps.customerData.onTimeout)) {
        try {
          steps.customerData.onTimeout(timedOutErr);
        } catch (e) {
          if (process.env.NODE_ENV !== "production") {
            timedOutErr.message += ". Additionally got an uncaught " +
              "exception when calling saveRecipe options.onTimeout(). " +
              "Got error: " + e.message;
          }
        }
      }
      if (process.env.NODE_ENV !== "production") {
        console.warn("saveRecipe failed with error: " + timedOutErr.message);
      }
      // steps.done contains the main saveRecipe callback
      steps.done(timedOutErr);
    }
  }, +steps.customerData.timeout + 50);

  cb(null, hasTimedOut);
}
