/* eslint complexity:0 */
/**
 * @publicDocs methods/getPrice ../../docs/get_price.md
 */

var accounting = require("accounting"),
  // If the currency is not defined
  defaultFormatOptions = {
    symbol: "$", // default currency symbol is '$'
    format: {
      pos: "%s%v",
      neg: "%s -%v",
      zero: "%s%v"
    },
    decimal: ".", // decimal point separator
    thousand: ",", // thousands separator
    precision: 2 // decimal places
  },
  _ = require("lodash");

module.exports = function (engine, fcParams, getQuantity) {
  return function getPrice(options) {
    options = options || {};
    options.quantity = options.quantity || getQuantity() || 1;

    var recipe = engine.recipe(),
      currency = options.currency || fcParams.currency,
      price = recipe.price(currency) * +options.quantity;

    if (options.format) {
      price = accounting.formatMoney(price,
        getFormatOptions(fcParams, options, engine));
    }
    return price;
  };
};

function getFormatOptions(fcParams, options, engine) {
  var currency = options.currency || fcParams.currency,
    forcedSettings = fcParams.currencySettings || options.currencySettings,
    formatOptions = forcedSettings || {};

  if (!forcedSettings) {
    try {
      formatOptions = engine.product().currencySettings(currency);
    } catch (e) {
      if (process.env.NODE_ENV !== "production") {
        console.warn("setPrice failed to get format options. " + e.message);
      }
    }
  }
  return _.defaults({}, formatOptions, defaultFormatOptions);
}
