/**
 * Explains a recipe
 */
var _ = require("lodash");

module.exports = function explainRecipe(recipe) {
  var
    selection = recipe.getData(),
    all = recipe.context().recipeData,
    explanation = {
      selected: {},
      unselected: {},
      concise: recipe.legacy("concise")
    },
    getAvInfo = function (av) {
      var
        avData = av && av.getData(),
        value = avData && ((
          av && av.getVertexKey && av.getVertexKey()
        ) || (av && av.getData && av.getData()));

      return {
        name: avData.name,
        vertexKey: avData.vertexKey,
        vendorId: avData.vendorId,
        value: value
      };
    },
    getCaInfo = function (ca) {
      var caData = ca.getData();
      return {
        name: caData.name,
        vertexKey: caData.vertexKey,
        alias: caData.alias
      };
    },
    addExplanation = function (explanations, ca, av) {
      explanations[ca.getVertexKey()] = {
        ca: getCaInfo(ca),
        av: getAvInfo(av)
      };
    };

  _.each(all, function (entry) {
    var selected = _.find(selection, function (selected) {
      return entry.ca === selected.ca;
    });
    if (selected) {
      addExplanation(explanation.selected, selected.ca, selected.av);
    } else {
      addExplanation(explanation.unselected, entry.ca, entry.av);
    }
  });

  return explanation;
};