/* eslint complexity:[2,8] */

var _ = require("lodash");

const vrTypes = {
  EQUAL_AV: "EQUAL_AV",
  EXCLUDE_AV: "EXCLUDE_AV",
  EQUAL_F: "EQUAL_F",
  EXCLUDE_F: "EXCLUDE_F"
}

function assignFacetRuleMembers(rule, result) {
  result.ca = rule.traverser()
      .outgoing("hasFacetVisibilityRule")
      .instance();

  if (result.type === vrTypes.EQUAL_F) {
    result.el = rule.traverser()
        .outgoing("isVisibleWhenSameFacet")
        .instance();

    result.value = rule.traverser()
        .outgoing("isVisibleWhenSameFacetValue")
        .instance();
  } else if (result.type === vrTypes.EXCLUDE_F) {
    result.el = rule.traverser()
        .outgoing("isHiddenWhenSameFacet")
        .instance();

    result.value = rule.traverser()
        .outgoing("isHiddenWhenSameFacetValue")
        .instance();
  }
}

function assignAvRuleMembers(rule, result) {
  if (result.type === vrTypes.EQUAL_AV) {
    // Attribute value visibility rules don't have a
    // hasAttributeVisibilityRule edge on the graph,
    // instead we can use the isVisibleWhenSameAttribute for both
    // the target element and the ca that owns the value.
    result.ca =
        result.el =
            rule.traverser()
                .outgoing("isVisibleWhenSameAttribute")
                .instance();

    result.value = rule.traverser()
        .outgoing("isVisibleWhenSameAttributeValue")
        .instance();
  } else if (result.type === vrTypes.EXCLUDE_AV) {
    result.ca =
        result.el =
            rule.traverser()
                .outgoing("isHiddenWhenSameAttribute")
                .instance();

    result.value = rule.traverser()
        .outgoing("isHiddenWhenSameAttributeValue")
        .instance();
  }
}

module.exports = function (options) {

  var BaseModel = require("./base")(options);



  /**
   * Visibility rules define whether an attribute should be selectable or not
   * depending on the selected facet value or attribute value on another
   * configurable attribute.
   *
   * On the graph their vertices contain the namespace (ns) "vr"
   *
   * @param {Object} options Options
   * @param {String} options.id {VisibilityRule} unique id.
   * @class
   * @name VisibilityRule
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends VisibilityRule */{
    ns: "vr",

    /**
     * Gets a list of {@link ConfigurableAttribute}s affected by this rule.
     * @instance
     * @return {Array<ConfigurableAttribute>}
     */
    affectedAttributes: function () {
      var self = this;
      return this.getAndCache("affectedAttributes", function () {
        return self.traverser()
          .incoming("hasVisibilityRule")
          .instances();
      });
    },


    /**
     * Adds extra description to explain better how this visibility rule
     * affects the owner configurable attribute depending on the values and
     * facet values of the related ca's.
     * @instance
     * @see {@link Traverser#explain}
     * @return {Object} Same as {@link Traverser#explain} but with an extra
     *                  description property that includes verbose narrative
     *                  such as:

          "Heel Logo"
              `{"alias":"heellogo","vertexKey":"ca:32056","vendorId":""}`
            is only available when the facet
          "Nano 4.0 - Base"
              `{"vertexKey":"f:21866"}`
            on the Configurable Attribute
          "Base"
              `{"alias":"basetype","vertexKey":"ca:32034","vendorId":""}`
            has the value
          "Solid Color"
              `{"vertexKey":"fv:30899"}`

     */
    explain: function () {
      var baseExplanation = BaseModel.prototype.explain.apply(this),
        affectedCas = this.affectedAttributes(),
        ruleMembers = this.ruleMembers(),
        description = "",
        isExcludeRule = this.isExcludeRule();

      // Collect affected ca names
      description += _.map(affectedCas, String).join(", ");

      baseExplanation.description =  description +
        ((affectedCas.length > 1) ? " are" : " is") +
       " only available when" +
       " " + ruleMembers.el +
       " on" +
       " " + ruleMembers.ca +
       " " + (isExcludeRule ? "does not have" : "has")  + " the value" +
       " " + ruleMembers.value;

      return baseExplanation;
    },

    /**
     * Should the {@link ConfigurableAttribute} who owns this Visibility rule
     * be displayed or available when a target ca has a specific
     * {@link AttributeValue} or {@link FacetValue}?
     *
     * If a visibility rule exists and there is no match, the
     * {@link ConfigurableAttribute} will not be selectable or available on the
     * {@link Recipe}.
     *
     * @instance
     * @return {Boolean}
     */
    isVisible: function () {
      var rule = this.ruleMembers();

      // Missconfigured products might not include a `ca`
      if (!rule.ca) {
        return false;
      }

      const selectedValue = rule.ca.selectedValue();

      // If no value is selected, then the rule is not valid
      if (!selectedValue) {
        return false;
      }
      switch (rule.type) {
        case vrTypes.EQUAL_AV:
          return selectedValue === rule.value;
        case vrTypes.EXCLUDE_AV:
          return selectedValue !== rule.value;
        case vrTypes.EQUAL_F:
          return selectedValue.hasFacetValue(rule.el, rule.value);
        case vrTypes.EXCLUDE_F:
          return !selectedValue.hasFacetValue(rule.el, rule.value);
        default:
          return false
      }
    },


    /**
     * Gets the ruleMembers needed to explain and know if this rule should
     * make its owner visible/available or not.
     * @instance
     * @returns {Object} - Returns the following info required to know
     * if this rule should be applied or not.

            {
              // Target `ca`
              ca: {@link ConfigurableAttribute}

              // `ca` or `f` that contains the value we want to search for.
              el: {@link ConfigurableAttribute} | {@link Facet},

              // `av` or `fv` that should be selected on the owner of the rule
              // in order to make it visible or not.
              value: {@link AttributeValue} | {@link FacetValue},

              // Type of lookup
              type: vrTypes
            }
     */
    ruleMembers: function () {
      var self = this;
      // Rule members don't change during the life cycle of a context
      // so we can safely cache them
      return this.getAndCache("ruleMembers", function () {
        var result = {};

        const ruleType = self.getVisibilityRuleType();
        result.type = ruleType;

        if (ruleType === vrTypes.EQUAL_F || ruleType === vrTypes.EXCLUDE_F) {
          assignFacetRuleMembers(self, result)
        } else if (ruleType === vrTypes.EQUAL_AV || ruleType === vrTypes.EXCLUDE_AV) {
          assignAvRuleMembers(self, result);
        }

        return result;
      });
    },

    getVisibilityRuleType: function () {
      var self = this;
      // Rule members don't change during the life cycle of a context
      // so we can safely cache them
      return this.getAndCache("getVisibilityRuleType", function () {
        const isFacetVisibilityRule = !!self.traverser()
            .outgoing("hasFacetVisibilityRule")
            .instance();
        if (isFacetVisibilityRule) {
          const isEqualRule = !!self.traverser()
              .outgoing("isVisibleWhenSameFacet")
              .instance();
          return  isEqualRule ? vrTypes.EQUAL_F : vrTypes.EXCLUDE_F;
        }
        const isEqualAvRule = !!self.traverser()
            .outgoing("isVisibleWhenSameAttribute")
            .instance();
        const isExcludeAvRule = !!self.traverser()
            .outgoing("isHiddenWhenSameAttribute")
            .instance();
        // if product does not have attribute value that was linked to this visibility rule
        // the AV ends up not in the graph, so there is no isVisibleWhenSameAttribute link.
        // historically it was treated as empty rule
        if (!isEqualAvRule && !isExcludeAvRule) {
          return vrTypes.EQUAL_AV;
        }
        return isEqualAvRule ? vrTypes.EQUAL_AV : vrTypes.EXCLUDE_AV;
      });
    },

    isExcludeRule: function () {
      var self = this;
      return this.getAndCache("isExcludeRule", function () {
        const ruleType = self.getVisibilityRuleType();
        return ruleType === vrTypes.EXCLUDE_F || ruleType === vrTypes.EXCLUDE_AV
      });
    },
  });

};
