/* eslint complexity: 0 */

/*
 * The traverser allows us to navigate through a product configuration
 * graph in order to get the relationships between different entities
 * using a unified interface. It is modeled after Graph databases such
 * as OriendDB or Neo4J.
 *
 * The Graph need to be previously expanded via the {@link expander}.
 */

var
  _ = require("lodash"),

  // Private methods
  explainEdgeRelationships,

  // Constants
  FROM = 0,
  TO = 1,
  TYPE = 2,
  INC = "incomingVertices",
  OUT = "outgoingVertices";

/**
 * Generate a reducing function for getting related vertices based on a
 * specific direction and edge type.  Optionally recursive.
 *
 * @private
 */
function buildRelater(graph, direction, recursive, relType) {
  var edgeIndex = graph[direction][relType];
  return function getRelated(result, vertex) {
    if (!vertex) {
      return result;
    }

    var vertexKey = vertex.vertexKey || vertex.getVertexKey();

    var edges = edgeIndex[vertexKey];
    if (!edges) { return result; }
    var e, totalEdges, relVertex;
    for (e = 0, totalEdges = edges.length; totalEdges > e; e += 1) {
      relVertex = graph.vertices[edges[e]];
      result.push(relVertex);
      // Recursively fetch extra vertices, depth-first
      if (recursive) {
        result = getRelated(result, relVertex);
      }
    }

    return result;
  };
}

function countRelationships(graph, relType, options) {
  var totalVertices = options.vertices.length,
    i, vertexKey,
    direction = options.direction || OUT,
    result = 0;
  if (!graph[direction][relType]) {
    return 0;
  }
  for (i = 0; totalVertices > i; i += 1) {
    vertexKey = options.vertices[i].vertexKey ||
      (options.vertices[i].getVertexKey && options.vertices[i].getVertexKey());
    result += ((graph[direction][relType][vertexKey] || {}).length || 0);
  }
  return result;
}



function traverser(options) {

  var Traverser;
  var graph = options.graph;
  var context = options.context;

  var incomingFns = {};
  var incomingRecurFns = {};
  var outgoingFns = {};
  var outgoingRecurFns = {};

  // build the related vertex reducing functions
  _.each(graph().edgeGroups, function (edges, relType) {
    incomingFns[relType] = buildRelater(graph(), INC, false, relType);
    incomingRecurFns[relType] = buildRelater(graph(), INC, true, relType);
    outgoingFns[relType] = buildRelater(graph(), OUT, false, relType);
    outgoingRecurFns[relType] = buildRelater(graph(), OUT, true, relType);
  });

  /**
   * Gets a Graph Traverser instance.
   *
   * @class
   * @name Traverser
   * @param {Object} [options={}] Traverser options
   * @param  {Array} [options.vertices=[]] - Array with graph vertices.
   * @param  {String} [options.vertexKey] - Starting {@link vertexKey}
   */
  Traverser = function (options) {
    options = options || {};
    this.vertices = options.vertices;
    if (options.vertexKey) {
      this.setVertexKey(options.vertexKey);
    }
  };

  /** @memberOf Traverser */
  Traverser.prototype = {


    /**
     * Searches for outgoing vertices with a given relationship type.
     *
     * @param {String} relationshipType Name of the relationship
     * type/{@link edgeGroup}
     * @param {Boolean} [recursive] search recursively?
     * @return {Traverser} chainable Traverser instance
     */
    outgoing: function (relType, recursive) {
      var outgoing = (recursive ? outgoingRecurFns : outgoingFns)[relType];
      if (!outgoing) { this.vertices = []; return this; }
      this.vertices = this.vertices.reduce(outgoing, []);
      return this;

    },

    /**
     * Searches for incoming vertices with a given relationship type.
     *
     * @param {String} relationshipType Name of the relationship
     * type/{@link edgeGroup}
     * @param {Boolean} [recursive] search recursively?
     * @return {Traverser} chainable Traverser instance
     * @example
     traverser
      .incoming("hasAttribute")
      .instances();
     */
    incoming: function (relType, recursive) {
      var incoming = (recursive ? incomingRecurFns : incomingFns)[relType];
      if (!incoming) { this.vertices = []; return this; }
      this.vertices = this.vertices.reduce(incoming, []);
      return this;
    },

    /**
     * Performs a comparison of each vertex on the Traverser collection to the
     * given query object, updating the vertices with all the elements that have
     * equivalent property values.
     *
     *
     * @see Traverser#expand
     * @param {Object} query - The object of property values to filter by
     * @return {Traverser} chainable Traverser instance
     * @example
     traverser
      .expand()
      .where("hasAttribute"{
          ns: "ca"
       })
      .instances();
     */
    where: function (query) {
      if (!query || Object.keys(query).length === 0) {
        return this.set([]);
      }
      // When an id is provided we just need the ns and the id to speed up
      // the query
      if (query && query.id && query.ns) {
        query = {
          id: query.id,
          ns: query.ns
        };
      }
      this.vertices = _.where(this.vertices.map(expandVertex), query);
      return this;
    },


    /**
     * Reduces the current vertices on the Traverser using _.reduce and
     * updates the vertices with the resulting elements.
     *
     * Reduces the vertices to a value which is the accumulated result of
     * running each element in the collection through the callback, where each
     * successive callback execution consumes the return value of the previous
     * execution. If accumulator is not provided the first element of the
     * vertices will be used as the initial accumulator value.
     * The callback is bound to thisArg and invoked with four arguments;
     * (accumulator, value, index, collection).
     *
     * @param {Function} callback - The function called per iteration.
     * @param {Array} accumulator - Initial value of the accumulator.
     * @return {Traverser} chainable Traverser instance
     */
    reduce: function (callback, accumulator) {
      this.vertices = this.vertices.reduce(callback, accumulator || []);
      return this;
    },


    /**
     * Sets the vertices for the current Traverser instance collection.
     *
     * This method  updates traverser.length.
     *
     * @param  {Array} vertices - Vertices to use on the traverser.
     * @return {Traverser} chainable Traverser instance
     */
    set: function (vertices) {
      this.vertices = vertices;
      return this;
    },

    /**
     * Sets as single vertex for the current Traverser instance collection
     * given its vertexKey.
     *
     * @param {String} vertexKey Vertex Key, such as ns:123
     * @return {Traverser} chainable Traverser instance
     */
    setVertexKey: function (vertexKey) {
      var vertex = graph().vertexIndex[vertexKey];
      this.vertices = [vertex];
      return this;
    },

    /**
     * Expands the vertices to include all the vertex data from the graph.
     * This step is required if you want to perform a where search on the
     * current collection
     *
     * @see Traverser#where
     * @return {Traverser} chainable Traverser instance
     * @example
     traverser
      .expand() // before expanding only ns and id can be searched for.
      .where("hasAttribute", {
          alias: "fleece"
       })
      .instances();
     */
    expand: function () {
      this.vertices = this.vertices.map(expandVertex);
      return this;
    },

    /**
     * Sorts current vertices on the Traverser using _.sortByAll and
     * updates the vertices list with the resulting elements.
     *
     * Sorts vertices in ascending order by the results of running vertex
     * through the callback. This method performs a stable sort, that is, it
     * will preserve the original sort order of equal elements.
     *
     * If a property name is provided for callback the created "_.pluck" style
     * callback will return the property value of the given element.
     *
     * If an object is provided for callback the created "_.where" style
     * callback will return true for vertices that have the properties of the
     * given object, else false.
     *
     * @param {(Function|string)} callback|property - The function called per
     * iteration called with the following arguments:
     * (vertex, index, vertices). If a property name or object is provided
     * it will be used to create a ".pluck" or ".where" style callback,
     * respectively.
     * @return {Traverser} chainable Traverser instance
     */
    sortBy: function (prop, order) {
      this.vertices = _.sortByAll(this.vertices, prop, order);
      return this;
    },


    /**
     * Iterates over the current vertex collection collection, updating the
     * vertices list of all elements the callback returns truthy for.
     *
     * If a property name is provided for callback the created "_.pluck"
     * style callback will return the property value of the given vertex.
     *
     * If an object is provided for callback the created "_.where"
     * style callback will return true for elements that have the properties of
     * the given object, else false.
     *
     * @param {(Function|string)} callback|property - The function called per
     * iteration called with the following arguments:
     * (vertex, index, vertices). If a property name or object is provided
     * it will be used to create a ".pluck" or ".where" style callback,
     * respectively.
     * @return {Traverser} chainable Traverser instance
     */
    filter: function (callback) {
      this.vertices = this.vertices.filter(callback);
      return this;
    },



    /**
     * Removes duplicates from the current vertex collection.
     *
     * @return {Traverser} chainable Traverser instance
     */
    unique: function () {
      this.vertices = _.uniq(this.vertices);
      return this;
    },

    /**
     * Reverses the order of the current vertex collection.
     *
     * @return {Traverser} chainable Traverser instance
     */
    reverse: function () {
      this.vertices = this.vertices.reverse();
      return this;
    },


    // Unchainable methods


    /**
     * Gets a single vertex given its vertexKey.
     *
     * @param {String} vertexKey Vertex Key, such as ns:123
     * @return {(vertexInstance|undefined)} Vertex instance or undefined when no
     * vertex is found
     */
    getVertexByKey: function (key) {
      var g = graph(),
        vertex = g.vertexIndex[key];

      if (!vertex) {
        throw new Error("Could not find vertex " + key + ". Make sure it's " +
          "available in the product configuration.");
      }

      return context.vertexInstance(vertex);
    },


    /**
     * Creates an array of values by running each vertex in the collection
     * through the callback.
     *
     * If a property name is provided for callback the created "_.pluck" style
     * callback will return the property value of the given element.
     *
     * If an object is provided for callback the created "_.where" style
     * callback will return true for elements that have the properties of the
     * given object, else false.
     *
     * @param {(Function|Object|string)} callback|query - The callback is
     * invoked with per iteration with three arguments:
     * (value, index, vertices).
     * If a property name or object is provided it will be used to create a
     * ".pluck" or ".where" style callback, respectively.
     * @return Array - A new array of the results of each callback execution.
     */
    map: function (fn) {
      this.vertices = this.vertices.map(fn);
      return this.vertices;
    },

    /**
     * First element on the vertext collection
     *
     * @returns Vertex
     */
    first: function () {
      return this.vertices[0];
    },

    /**
     * Provides information about the relationships between vertices.
     *
     * @return Array - A collection of explanations including: vertex, incoming
     * and outgoing properties.
     * @example
      traverser
        .explain();
      // [{vertex: {id: 2, ns: "f"}, incoming: ["ns:1 has f:2"], outgoing: []}]
     */
    explain: function () {
      return this.vertices.map(function (vertex) {
        var vertexExplanation = {},
          vertexKey = vertex.vertexKey || vertex;
        vertexExplanation[vertexKey] = {
          vertex: vertex,
          outgoing: _.map((_.where(graph().edges, {
            "0": vertexKey
          })), explainEdgeRelationships),
          incoming: _.map((_.where(graph().edges, {
            "1": vertexKey
          })), explainEdgeRelationships)
        };
        return vertexExplanation;
      }, this);
    },

    /**
     * Gets a collection of raw vertices or vertex instances.
     *
     * @param  {Object} [options={}] - Options
     * @param  {boolean} [options.instances=false] - Return instances instead
     * of raw vertices
     * @return {Array} Collection of vertices or vertex instances
     * @see Traverser#instances
     */
    all: function (options) {

      this.expand();

      options = options || {};

      if (!options.instances) {
        return this.vertices;
      }

      return this.map(context.vertexInstance);
    },


    /**
     * Gets the size for an outgoing relationship type on the current
     * vertex collection.
     *
     * @param {String} relationshipType - Relationship type
     * @return {number} Total number of outgoing related vertices
     */
    outgoingSize: function (relationshipType) {
      return countRelationships(graph(), relationshipType, {
        vertices: this.vertices,
        direction: "outgoingVertices"
      });
    },

    /**
     * Gets the size for an incoming relationship type on the current
     * vertex collection.
     *
     * @param {String} relationshipType - Relationship type
     * @return {number} Total number of incoming related vertices
     */
    incomingSize: function (relationshipType) {
      return countRelationships(graph(), relationshipType, {
        vertices: this.vertices,
        direction: "incomingVertices"
      });
    },

    /**
     * Gets a collection of vertex instances.
     *
     * @return {Array} Collection of vertex instances
     * @see Traverser#all
     */
    instances: function () {
      return this.vertices.map(context.vertexInstance);
    },

    /**
     * Gets the first vertex instance on the vertex collection.
     *
     * @return {vertexInstance} Vertex instance
     * @see Traverser#instances
     */
    instance: function () {
      var vertex = this.first();
      return vertex ? context.vertexInstance(vertex) : null;
    },

    /**
     * Gets a collection of raw vertices.
     * @return {Array} Array of raw vertex data
     */
    toArray: function () {
      return this.all();
    },

    /**
     * Gets a collection of raw vertices.
     * @return {Array} Array of raw vertex data
     */
    toJSON: function () {
      return this.all();
    }
  };

  return Traverser;
}


// Private methods
explainEdgeRelationships = function (edge) {
  var properties = {}; //_.omit(edge, ["from", "to", "type"]);
  return edge[FROM] + " " +
    edge[TYPE] + " " +
    edge[TO] +
    (_.size(properties) > 0 ?
      " with properties " + JSON.stringify(properties) :
      "");
};

function expandVertex(vertex) {
  if (vertex.vertexKey) {
    return vertex;
  } else {
    return vertex.getData();
  }
}

module.exports = traverser;
