var _ = require("lodash");

module.exports = function getNormalizedErrorFunction(methodName) {

  /**
    Makes sure errors are valid JS errors and prepends a methodName context
    to the error messages. It also shows the error via console.warn when not
    in production
   */
  return function getNormalizedError(err) {
    if (err) {
      // Users might decide that's fun to pass an error string. Don't pass
      // error strings on purpose, it's an anti-pattern
      if (_.isString(err)) {
        if (process.env.NODE_ENV !== "production") {
          console.warn(methodName + " error passed as string. " +
            "You should only pass errors created via " +
            "`new Error(\"" + err + "\")`");
        }
        err = new Error(err);
      }
      if (err.message) {
        err.message = methodName + " failed with error: " + err.message;
      }

      if (process.env.NODE_ENV !== "production") {
        console.warn(err.message);
      }
    }
    return err;
  };

};