/**
 * @publicDocs methods/getCustomerReport ../../docs/get_customer_report.md
 */

var configurationLoader = require("@cfg.plat/configuration-loader");
var _ = require("lodash");

module.exports = function (engine, fcParams) {
  var product = engine.product().getData();
  return function getCustomerReport(callback) {

    if (typeof callback !== "function") {
      throw new Error(".run(\"getCustomerReport\", cb) expects a callback");
    }

    configurationLoader.customerReport(_.defaults(fcParams, {
      workflow: product.workflow,
      customer: product.customerId
    }),
      function (err, report) {
        callback(err, JSON.parse(JSON.stringify(report || {})));
      });
  }
};
