/*
 * The configuration engine uses a graph for handling relationships
 * between entities. This base model acts as a scaffold that implements
 * shared functionality among entities whose data is defined as a vertex
 * on the graph and whose relationships are defined as namespaced edges.
 *
 * You can use BaseModels for creating graph backed classes such as:
 *
 *     module.exports = function (options) {
 *       // options should include the context
 *       var BaseModel = require("./lib/models/base")(options);
 *        return BaseModel.extend({
 *          myCustomMethod: function () {}
 *        });
 *     };
 *
 */

var _ = require("lodash");

var getBaseModel = function (contextOptions) {

  contextOptions = contextOptions || {};

  if (contextOptions.context && contextOptions.context.BaseModel) {
    return contextOptions.context.BaseModel;
  }

  // Setting the context implicitly is needed to unit
  // test the base model
  contextOptions.context = contextOptions.context ||
    require("../context")(contextOptions);

  var context = contextOptions.context;

  var traverser = context.createTraverser();

  var BaseModel;

  /**
   * Gets a new instance provided an {id: 123} to a vertex containing
   * the data for the model
   *
   * The configuration engine uses a graph for handling relationships
   * between entities. This base model acts as a scaffold that implements
   * shared functionality among entities whose data is defined as a vertex
   * on the graph and whose relationships are defined as namespaced edges.
   *
   * @class
   * @name BaseModel
   * @constructor
   * @classdesc Shared functionality for Configurable Product Graph
   * Instances.
   * @param {Object} [options] Options
   * @param {number} [options.id] Model id.
   * @example
   * new BaseModel({id: 123})
   */
  BaseModel = function (options) {
    if (process.env.NODE_ENV !== "production") {
      validateInstanceOptions(this, options.id);
    }
    this.id = options.id;
  };

  /** @memberOf BaseModel */
  BaseModel.prototype = {

    /**
     * Returns a vertex key as indexed on the graph
     * @return {String} ns:123 style vertex key
     */
    getVertexKey: function () {
      return this.ns + ":" + this.id;
    },

    /**
     * Gets a reference to the data for this instance.
     * @return {Object} Reference to the graph vertex containing
     * information about the current instance.
     */
    getData: function () {
      return this.traverser().first();
    },

    /**
     * Get a textual representation for the current instance.
     * @return {String} [ns:123 alias or name] string used to debug entries.
     */
    toString: function () {
      var data = this.getData() || {};
      return "[" + (
          this.ns + ":" + this.id + " " +
          (data.alias || data.name || "")
        ).trim() + "]";
    },

    /**
     * Gets a copy of the data for this instance.
     *
     * Same as {@link BaseMode.getData} but returns a copy instead of a
     * reference.
     *
     * This method should be used for exposing data publicly as we don't want
     * third parties updating the internal graph configuration.
     *
     * @return {Object} Copy to the graph vertex data containing
     * information about the current instance.
     */
    clone: function () {
      var data = this.getData();
      if (data) {
        return JSON.parse(JSON.stringify(data));
      }
    },

    /**
     * Gets a copy of the data for this instance.
     *
     * @return {Object} Copy to the graph vertex data containing
     * information about the current instance.
     */
    toJSON: function () {
      return this.clone();
    },


    /**
     * Provides information about the relationships between vertices.
     *
     * @example
      model
        .explain();
      // {vertex: {id: 2, ns: "f"}, incoming: ["ns:1 has f:2"], outgoing: []}
     * @return {Object} - An object including: vertex, incoming
     * and outgoing properties.
     */
    explain: function () {
      var explanations = this.traverser().explain()[0];
      if (explanations) {
        return explanations[this.getVertexKey()];
      } else {
        throw new Error("Could not explain relationships for `" +
          this.getVertexKey() + "` as it was not found on the graph.");
      }
    },

    /**
     * Gets a reference to the traverser defined on the
     * {@link module:context Context}.
     *
     * @param {Object} [vertices=this.getVertexKey()] - List of vertices
     * used as a starting point for traversing the graph.
     * @return {Traverser} Graph traverser starting on the specified vertices.
     */
    traverser: function (vertices) {
      if (vertices) {
        traverser.set(vertices);
      } else {
        traverser.setVertexKey(this.getVertexKey());
      }
      return traverser;
    },


    /**
     * Finds models on the product configuration graph.
     *
     * Uses the graph traverser to find for vertices on the graph that
     * match the given query.
     *
     * @param {Object} options Search options
     * @param  {String} options.relationship - Relationship name. (av, ca...)
     * @param  {String} [options.direction=outgoing] - Relationship direction
     * @param  {Object} [options.query={}] - Search query.
     * Uses _.where(vertex, options.query) on each
     * vertex
     * @param  {Object} [options.options={}] - {@link Traverser} options.
     * @return {Array}  Collection of model instances
     */
    find: function (options) {
      options = options || {};

      if (!options.relationship) {
        throw new Error("You must supply a valid relationship");
      }

      options.direction = options.direction || "outgoing";
      options.query = options.query || {};
      options.options = options.options || {};

      return this.traverser()[options.direction](
            options.relationship, !!options.recursive
          )
          .where(options.query);
    },

    /**
     * Gets a {@link module:context Context} cached value or retrieves the
     * value using a callback and caches it for future callers.
     *
     * @param {String} cacheId Unique and consistent id for value to be cached.
     * @param {Function} callback When no cache is found we will use the value
     * returned by the callback.
     * @return {Mixed} Whatever the callback returns.
     */
    getAndCache: function (cacheId, fn) {
      return context.getAndCache(this, cacheId, fn);
    },

    // Override on subclasses
    ns: null,

    /**
     * Gets the current namespace for the model.
     * @return {String} Namespace name.
     */
    getNs: function () {
      return this.ns;
    },

    /**
     * Gets the current {@link ConfigurableProduct Product} on the
     * {@link module:context Context}
     * @return {ConfigurableProduct} Product Product instance for the current
     * context.
     */
    product: function () {
      return context.product();
    },

    /**
     * Gets the current {@link Recipe} on the {@link module:context Context}
     * @return {Recipe} Recipe instance for the current context.
     */
    recipe: function () {
      return context.recipe();
    },

    /**
     * Has this model metadata?
     *
     * @instance
     * @return {Boolean}
     */
    hasMetadata: function () {
      var data = this.metadata();
      return data && data.length > 0;
    },

    /**
     * Gets all the metadata associated with this model
     *
     * @instance
     * @return  {Array} the metadata
     */
    metadata: function () {
      var data = this.traverser()
        .outgoing("hasMetadata")
        .instances();

      return data;
    }

  };

  // Exposes the BaseModel on the context
  context.BaseModel = BaseModel;


  /**
   * Convenience method for extending the base model
   *
   * @example
   * // Exposes a custom model module
   * var myCustomModel = BaseModel.extend({
   *   myCustomMethod: function () {}
   * });
   *
   * @returns BaseModel
   */
  BaseModel.extend = function (proto, constructor) {
    var Model = constructor || function (options) {
      BaseModel.call(this, options);
    };
    Model.prototype = Object.create(BaseModel.prototype);
    _.merge(Model.prototype, proto);
    return Model;
  };

  return BaseModel;
};




// Private methods

// Validate an instance
function validateInstanceOptions(instance, id) {
  // ns is set on the proto of the subclass
  if (!instance.ns) { throw new Error("No namespace for model class"); }
  if (!id) {
    throw new Error("Missing ID");
  }
  // We should NOT favor vertex/namespaced ids, but we want
  // to provide hints while developing about it.
  if (typeof id === "string" && id.indexOf(instance.ns + ":") === 0) {
    throw new Error("Please don't use vertexKey's like " + id +
      " for instantiating " + instance.ns + " models. " +
      "Use " + id.split(instance.ns + ":").slice(1).join("") + " instead.");
  }
}


module.exports = getBaseModel;
