var
  _ = require("lodash");

module.exports = function (options) {

  var BaseModel = require("./base")(options);


  /**
   * Metadata.
   *
   * On the graph their vertices contain the namespace (ns) "md"
   *
   * @param {Object} options Options
   * @param {String} options.id Metadata name, used as id/primary key.
   * @class
   * @name Metadata
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends Metadata */{

    ns: "md",

    /**
     * This method will return an Array of Objects which every one of them
     * will have a "ns", "id" and "vertexKey"
     *
     * @return {Array}
     */
    vertex: function () {
      var vertexInstances = this.traverser()
        .incoming("hasMetadata")
        .instances();

      return _.map(vertexInstances, function (vertex) {
        return {
          ns: vertex.ns,
          id: vertex.id,
          vertexKey: vertex.getData().vertexKey
        };
      });
    }

  });
};
