module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(options) {
    BaseModel.call(this, options);
    this._av = this.attributeValue();
  }

  /**
   * When an {@link AttributeValue} is selected {@link ConfigurableAttribute}s
   * special properties can be defined on the {ConfigurableAttribute} if a
   * `ValueUsage` exists for the {@link AttributeValue}.
   *
   * `ValueUsage` Defines the following properties on
   * {@link AttributeValues}s
   *
   * - Vendor ID - This specific {@link AttributeValue} /
   * {@link ConfigurableAttribute}
   *   combination is tied to a SKU
   * - Upcharge - Apply an upcharge on top of the {@link ConfigurableAttribute}
   *   price when this {@link AttributeValue} is selected
   * - Pricing Description - Optional text to display when
   *   {@link AttributeValue} is selected
   * - Model Component - Should the {@link AttributeValue} apply to a different
   *   component as well?
   * - Associated View - Should the {@link View} of the model change when this
   *   {@link AttributeValue} is selected?
   * - Active - Toggle whether this {@link AttributeValue} should be offered for
   *   a given {@link ConfigurableAttribute}
   *
   * Visibility rules can be based on Facet Value usage (e.g {@link FacetValue}
   * 1 of {@link ConfigurableAttribute} 1 can trigger [ConfigurableAttribute] 2
   * to become visible).
   *
   * On the graph their vertices contain the namespace (ns) "vu"
   *
   * @param {Object} options Options
   * @param {number} options.id ValueUsage numeric id.
   * @class
   * @name ValueUsage
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends ValueUsage */{
    ns: "vu",

    /**
     * Get the {@link AttributeValue} this value usage is for.
     * @instance
     * @return {AttributeValue}
     */
    attributeValue: function () {
      return this.traverser()
        .outgoing("usesValue")
        .instance();
    },

    /**
     * Gets the upcharge for the {@link AttributeValue}
     * @instance
     * @param {AttributeValue} av
     * @param {String} currencyCode - Currency as defined on the product
     * configuration
     * @return {Number} - Extra price on top of the
     *                    {@link ConfigurableAttribute}
     * base price or null if no upcharge defined
     */
    upchargeFor: function (av, currency) {
      var ownAv = this.traverser()
        .outgoing("usesValue")
        .instance(),
        upcharge;

      if (ownAv.id === av.id) {
        upcharge = this.traverser()
          .outgoing("upcharges")
          .instance();
        if (upcharge) {
          return upcharge.getData()[currency];
        }
      }
      return null;
    },

    /**
     * Gets the upcharges for the {@link AttributeValue}
     * @instance
     * @param {AttributeValue} av
     * @return {Object} Object with [currencyCode]: value
     */
    upchargesFor: function (av) {
      var ownAv = this.traverser()
              .outgoing("usesValue")
              .instance();
      var upcharges;

      if (ownAv.id === av.id) {
        upcharges = this.traverser()
            .outgoing("upcharges")
            .instance();
        if (upcharges) {
          return upcharges.getData();
        }
      }
      return null;
    },



    /**
     * Gets the {@link View} name for the value usage.
     * @instance
     * @return {String} View name
     */
    viewName: function () {
      var view = this.traverser()
        .outgoing("displaysView")
        .instance(),
        name;

      if (view) {
        name = view.getData().name;
        return name;
      }
    }
  }, constructor);
};
