/*eslint complexity:0 */
/**
 * @publicDocs methods/previewValue ../../docs/preview_value.md
 */

var
  _ = require("lodash");

module.exports = function (engine, api) {
  var getRecipe = require("./get_recipe")(engine),
    setValue = require("./select_value")(engine, null, api);

  return function previewValue(params, format, callback) {
    if (!params || !format || !callback) {
      throw new Error(
        "Missing arguments for previewValue(params, format, callback)"
      );
    }

    if (callback && !_.isFunction(callback)) {
      throw new Error(
        "Invalid arguments for previewValue(params, format, callback)"
      );
    }

    var
      recipe = engine.recipe(),
      isMultiChange = _.isArray(params) && _.isObject(params[0]),
      result;

    try {
      recipe.start();
      if (isMultiChange) {
        _.forEach(params, function (param) {
          setValue(param);
        });
      } else {
        setValue(params);
      }

      result = getRecipe(format);
      recipe.rollback();
      callback(null, result);
    } catch (e) {
      try {
        recipe.rollback();
      } catch (e) {
        /* continue regardless of error */
      }
      callback(e);
    }
  };
};
