const _ = require("lodash");
const context = require("./context");
const engineCacheFlushEnabled = require("../config").engineCacheFlushEnabled;
const engineCacheSize = require("../config").engineCacheSize;

const flushAll = context.flushAll;
const engineIds = context.engineIds;
const contexts = context.contexts;

function flushEngine(options) {
  const isEngineFlushEnabled = typeof options.engineCacheFlushEnabled !== "undefined"
      ? options.engineCacheFlushEnabled
      : engineCacheFlushEnabled
  if (!isEngineFlushEnabled) {
    return;
  }
  const cacheSize = typeof options.engineCacheSize !== "undefined"
      ? options.engineCacheSize
      : engineCacheSize;
  if (engineIds().length > cacheSize) {
    flushAll();
  }
}

function validateArguments(graphJson) {
  if (!graphJson || !graphJson.vertices || !graphJson.edgeGroups) {
    throw new Error("The configuration-engine expects a graph configuration.");
  }
}

/**
 * The configuration-engine is the Fluid Configure core. It contains all the
 * business rules for Fluid Configure and exposes an API that allows creating
 * configurable product contexts that can be used to render UI's, validate
 * configurations or provide real time feedback when defining new
 * configurations.
 *
 * The entry point for the configuration engine is
 * a product instance with an optional starting point
 * recipe
 *
 * @module configuration-engine
 * @function
 * @param {graphJson} graphJson Product configuration graph
 * @param {Object} [options={}]
 * @param {Object} [options.engineId=] The id for this configuration engine.
 * @param {boolean} [options.engineCacheFlushEnabled=] should enable cache flush for this instance.
 * @param {number} [options.engineCacheSize=] cache size for automatic flush
 * This is useful when you have multiple configuration-engines on the same
 * page.
 * @returns {Context} Configuration engine context
 */
module.exports = function(graphJson, options) {
  flushEngine(options || {});

  validateArguments(graphJson);

  const contextInstance = context(_.defaults({}, options || {}, {
    graph: graphJson,
    engineId: "fc-configuration-engine-" + _.uniqueId(),
  }));

  return contextInstance;
};

// Static Context methods
module.exports.flushAll = flushAll;
module.exports.engineIds = engineIds;
module.exports.contexts = contexts;
