/**
 * @publicDocs methods/indexProduct ../../docs/index_product.md
 */

var
  _ = require("lodash"),
  indexConfigurableAttribute = require("./index_configurable_attribute"),
  GetFullTextSearch = require("./get_full_text_search");

module.exports = function (engine, getProductJson) {

  /**
   * This method will index the whole product
   *
   * @param  {options} options the fields you want to index
   * @param  {Function} callback the callback will be called when the index
   * is done, if error it will be passed into the callback
   * @return {Function}
   */
  return function indexProduct (options, callback) {

    var
      product = getProductJson();

    GetFullTextSearch.createSearchIndex(options);

    GetFullTextSearch.addDocument(engine, product, [
      "priceConfiguration", "locales",
      "currencySettings", "numberFormattingSettings", "currencies",
      "attributes", "allAttributes"
    ]);

    _.each(product.attributes || [], function (attribute) {
      options.attribute = attribute.id;

      indexConfigurableAttribute(engine, getProductJson)(options,
      function (err) {
        if (err) {
          callback(err);
          return;
        }
      });
    });

    callback();
  };
};
