/**
 * @publicDocs methods/randomizeRecipe ../../docs/randomize_recipe.md
 */

var
  _ = require("lodash"),
  clone = _.compose(JSON.parse, JSON.stringify);

module.exports = function (engine) {

  return function randomizeRecipe(callback) {
    if (!callback || (callback && !_.isFunction(callback))) {
      throw new Error("Invalid arguments for randomizeRecipe(callback)");
    }

    var
      recipe = engine.recipe(),
      product = engine.product(),
      attributes = product.allAttributes();

    recipe.once("change", function (modifications) {
      modifications = clone(modifications);

      // Trigger randomize event when all changes were applied
      recipe.trigger("randomize", modifications);

      // Modifications should be expanded, not ca, av references
      callback(null, modifications);
    });

    recipe.start();
    _.each(attributes, function (attribute) {
      // CD-3654: due to all the stuff that can happen behind the scenes with
      // visible CAs influencing the values of any other CA (including
      // potentially hidden ones), we only want to set a randomize selection
      // on attributes that are have selector options
      if (attribute.hasSelector()) {
        recipe.setOne(attribute, _.sample(attribute.allValues()));
      }
    });
    recipe.commit();
  };
};
