var _ = require("lodash");

var configureEndpoint =
  process.env.FLUID_SERVER_ENDPOINT || "prod.fluidconfigure.com";

module.exports = function setEnvironmentParams(fcParams) {
  // In the kubernetes world this becomes irrelevant, the only
  // thing that matters is the configureEndpoint hostname.
  // If fixtures still pointed to the old qaconfigure or staging, they'll
  // have to be udpated to ones hosted on the prod publish buckets
  fcParams = _.clone(fcParams);
  fcParams.environment = fcParams.environment || "prod";

  if (!fcParams.configureEndpoint) {
    fcParams.configureEndpoint = configureEndpoint;
  }

  return fcParams;
}
