const _ = require("lodash");
module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGlModel.
   *
   * On the graph their vertices contain the namespace (ns) "wm"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGlModel numeric id.
   * @class
   * @name WebGlModel
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlModel */{

    ns: "wm",

    /**
     * Does this WebGlModel have model components?
     *
     * @instance
     * @return {Boolean}
     */
    hasModelComponents: function () {
      return this.traverser()
        .outgoingSize("hasModelComponent") > 0;
    },

    /**
     * Gets a collection of model components instances for this WebGlModel.
     *
     * @instance
     * @return {Array<ModelComponent>}
     */
    modelComponents: function () {
      return this.getAndCache("modelComponents", function () {
        return this.traverser()
          .outgoing("hasModelComponent")
          .instances();
      })
    },

    /**
     * Gets a collection of model LOD instances for this WebGlModel.
     *
     * @instance
     * @return {Array<WebGlModelLod>}
     */
    lods: function () {
      return this.getAndCache("lods", function () {
        return this.traverser()
          .outgoing("hasModelLod")
          .instances();
      })
    },

    /**
     * Gets an LOD by the name for this WebGlModel.
     *
     * @instance
     * @param {Object} query - The object of property webgl model to filter by
     * @return {WebGlModelLod}
     */
    findLod: function (query) {
      var queryToUse = query;
      if (_.isString(query) || _.isNumber(query)) {
        // assume it's an ID
        queryToUse = {id: +query};
      }
      return this.find({
        relationship: "hasModelLod",
        query: queryToUse,
      })
        .instance()
    },


    /**
     * Gets a collection of model LOD resolutions for this WebGlModel.
     *
     * @instance
     * @return {Array<WebGlLodResolution>}
     */
    resolutions: function () {
      return this.getAndCache("resolutions", function () {
        return this.traverser()
          .outgoing("hasModelLod")
          .incoming("hasModelLod")
          .where({ns: "wplod"})
          .outgoing("hasLodResolution")
          .unique()
          .instances();
      })
    },

    /**
     * Gets webGl customer material.
     *
     * @instance
     * @return {Object<WebGlCustomerMaterial>}
     */
    customerMaterial: function () {
      return this.getAndCache("customerMaterial", function () {
        return this.traverser()
          .outgoing("hasMaterial")
          .instance();
      })
    }

  }, constructor);

};
