/**
 * @publicDocs methods/getAttribute ../../docs/get_attribute.md
 */
var
  i = require("icepick");

module.exports = function (engine, getProductJson) {
  var product = engine.product();

  return function getAttribute(query) {
    var
      queryMethod = typeof query === "number" ?
        // findAttribute is slower and should only be used for testing
        // and for ease of implementation where knowing the id beforehand
        // is inconvenient
        "attributeById" : "findAttribute",
      caNode = product[queryMethod](query),
      caPath;

    if (!caNode) {
      throw new Error("getAttribute could not find a ca matching " +
        JSON.stringify(query) + ". Available attributes are: " +
        product.allAttributes().join(", "));
    }

    caPath = getProductJson().allAttributes[caNode.id];

    return i.getIn(getProductJson(), caPath);
  };
};
