module.exports = function (options) {

  var BaseModel = require("./base")(options);

  function constructor(opts) {
    BaseModel.call(this, opts);
  }

  /**
   * WebGl Product LOD resolution.
   *
   * On the graph their vertices contain the namespace (ns) "wplod"
   *
   * @param {Object} options Options
   * @param {number} options.id WebGl Product LOD resolution numeric id.
   * @class
   * @name WebGlProductLodResolution
   * @constructor
   * @augments BaseModel
   */
  return BaseModel.extend(/** @lends WebGlProductLodResolution */{

    /**
     * Gets LOD resolution for this product lod resolution.
     *
     * @instance
     * @return {WebGlLodResolution}
     */
    lodResolution: function() {
      return this.getAndCache("lodResolution", function () {
        return this.traverser()
          .outgoing("hasLodResolution")
          .instance()
      })
    },


    /**
     * Gets single webGl Model LOD for this product lod resolution.
     * @instance
     * @return {WebGlModelLod}
     */
    modelLods: function() {
      return this.getAndCache("modelLod", function () {
        return this.traverser()
          .outgoing("hasModelLod")
          .instances()
      })
    },

    ns: "wplod",

  }, constructor);

};
