var urlParamRegexp = /\:([a-zA-Z_]+)/g,
  removeIdStringRegexp = /(Id|_id)$/,
  integerStringRegexp = /[^\d]+/g,
  composeUrl,
  getQueryParams,
  getUrlComposer,
  replaceIdParams,
  setDefaults,
  addQueryParamsToUrl;


/**
 * Returns a function that can compose a template base
 * on a template.
 *
 * @param  {string} urlTemplate :token/:template/
 * @return {function} A function that called with
 *                      fn({token: "Foo", template: "Bar"});
 *                    will generate Foo/Bar
 *                    If a url parameter is missing, an
 *                    exception will be raised.
 */
getUrlComposer = function (urlTemplate, options) {
  var requiredKeys = urlTemplate.match(urlParamRegexp);

  return function (urlOptions) {
    var urlOptionsCopy = JSON.parse(JSON.stringify(urlOptions || {})),
      generatedUrl, queryParams;
    if (options.defaults) {
      urlOptionsCopy = setDefaults(urlOptionsCopy, options.defaults);
    }
    if (options.idParams) {
      urlOptionsCopy = replaceIdParams(urlOptionsCopy, options.idParams);
    }
    generatedUrl = composeUrl(requiredKeys, urlTemplate, urlOptionsCopy);
    queryParams = getQueryParams(urlOptionsCopy, options.legalQueryParams);
    return addQueryParamsToUrl(generatedUrl, queryParams);
  };
};


/**
 * For a set of URL options replace entries like
 * customerId=c123 or customer_id=c123 to customer=123
 *
 * @param  {object} options  URL options
 * @param  {array} idParams List of parameters that will be treated
 *                          as numeric ids
 * @return {object} options Modified URL options
 */
replaceIdParams = function (options, idParams) {
  var i, idFieldKey, nonIdKey;
  for (i = 0; idParams.length > i; i += 1) {
    idFieldKey = idParams[i];
    if (options[idFieldKey]) {
      nonIdKey = idFieldKey.replace(removeIdStringRegexp, "");
      if (!options[nonIdKey]) {
        options[nonIdKey] = parseInt(
            (options[idFieldKey] + "")
            .replace(integerStringRegexp, ""),
          10);
      }
    }
  }
  return options;
};

/**
 * Sets default options defined for the product when
 * explicit options are not provided.
 *
 * @param  {Object} options User URL options
 * @param  {Object} defaults Application specific defaults
 *                           defined on the application .json
 *                           rules.
 * @return {Object} options User URL options with missing values
 */
setDefaults = function (options, defaults) {
  var key;

  if (defaults) {
    for (key in defaults) {
      if (defaults.hasOwnProperty(key) && !options[key]) {
        options[key] = defaults[key];
      }
    }
  }

  return options;
};

/**
 * Gets a list of legal query parameters and escapes them
 * in order for them to be used when building URL's
 *
 * @param  {object} inputParams      Object with {key: values}
 *                                   to be used as query parameters.
 * @param  {array} legalQueryParams  List of legal parameters
 * @return {array}                   Array of ["key=escaped%20values"]
 */
getQueryParams = function (inputParams, legalQueryParams) {
  var query = [],
    i = 0;
  for (i = 0; legalQueryParams.length > i; i += 1) {
    if (typeof inputParams[legalQueryParams[i]] !== "undefined") {
      query.push(legalQueryParams[i] + "=" + encodeURIComponent(inputParams[legalQueryParams[i]]));
    }
  }
  return query;
};

/**
 * Adds query parameters to a URL
 * @param  {string} url
 * @param  {array} params  Array of ["key=escaped%20values"]
 * @return {string}        URL with a query string if applicable
 */
addQueryParamsToUrl = function (url, params) {
  if (params.length > 0) {
    if (url.indexOf("?") !== -1) {
      // The URL on the template might include
      // a query parameter we need to avoid double &
      // if the & is already included
      if (url[url.length - 1] !== "&") {
        url = url + "&";
      }
    } else {
      url = url + "?";
    }
    url = url + params.join("&");
  }
  return url;
};

/**
 * Composes a url
 *
 * @param  {array} requiredKeys List of keys that are required to
 *                              build the URL, and exception will be
 *                              raised if there are missing keys
 * @param  {string} urlTemplate  :attr1/:attr2 URL template
 * @param  {object} urlOptions   {attr1: "Val1", attr2: "Val 2"}
 * @return {string}              URL with the replacements in place
 */
composeUrl = function (requiredKeys, urlTemplate, urlOptions) {
  var i,
    generatedUrl = urlTemplate,
    key,
    userKeys = requiredKeys,
    missingKeys = [];

  for (i = 0; userKeys.length > i; i += 1) {
    key = userKeys[i].replace(":", "");
    if (typeof urlOptions[key] !== "undefined") {
      generatedUrl = generatedUrl.replace(userKeys[i], encodeURIComponent(urlOptions[key]));
    } else {
      missingKeys.push(key);
    }
  }

  if (missingKeys.length > 0) {
    throw new Error("Missing " +
      "\"" + missingKeys.join("\", \"") + "\"" +
      " option" + (missingKeys.length === 1 ? "" : "s") +
      " for generating " + urlTemplate + " URL");
  }

  return generatedUrl;
};



module.exports = getUrlComposer;
