/**
 * @publicDocs methods/indexConfigurableAttribute
 *   ../../docs/index_configurable_attribute.md
 */

var
  _ = require("lodash"),
  getAttribute = require("./get_attribute"),
  indexConfigurableAttributeMetadata =
    require("./index_configurable_attribute_metadata"),
  GetFullTextSearch = require("./get_full_text_search");

module.exports = function (engine, getProductJson) {

  /**
   * This method will index the whole attribute
   *
   * @param  {options} options the fields and attribute you want to index
   * @param  {Function} callback the callback will be called when the index
   * is done, if error it will be passed into the callback
   * @return {Function}
   */
  return function indexConfigurableAttribute (options, callback) {
    options = options || {};

    var
      attribute = null;

    if (!options.attribute) {
      return callback(new Error("You must provide an options.attribute param"));
    }

    attribute = getAttribute(engine, getProductJson)(options.attribute);

    if (!attribute) {
      callback(new Error("You must provide a valid options.attribute param"));
      return;
    }

    GetFullTextSearch.createSearchIndex(options);

    GetFullTextSearch.addDocument(engine, attribute, [
      "values", "allValues",
      "facets", "hiddenFacets", "optionalFacets", "parameterizedFacets",
      "allFacets", "subAttributes"
    ]);

    _.each(attribute.allValues, function (attributeValue) {

      GetFullTextSearch.addDocument(engine, attributeValue, ["metadata"]);
    });

    _.each(attribute.allFacets, function (facet) {

      GetFullTextSearch.addDocument(engine, facet, ["facetValues"]);

      _.each(facet.facetValues, function (facetValue) {
        GetFullTextSearch.addDocument(engine, facetValue);
      });
    });

    indexConfigurableAttributeMetadata(engine, getProductJson)(
      options, callback);
  };
};
